import React from 'react';
import { AlertCircle, RefreshCw } from 'lucide-react';
import BaseButton from './BaseButton';

interface BaseErrorProps {
    title?: string;
    message?: string;
    onRetry?: () => void;
    className?: string;
}

const BaseError: React.FC<BaseErrorProps> = ({
    title = "Something went wrong",
    message = "An error occurred while loading the data. Please try again.",
    onRetry,
    className = ""
}) => {
    return (
        <div className={`flex flex-col items-center justify-center p-8 rounded-lg bg-red-50 dark:bg-red-900/20 ${className}`}>
            <AlertCircle className="w-12 h-12 text-red-500 mb-4" />

            <h3 className="text-lg font-semibold text-red-700 dark:text-red-400 mb-2">
                {title}
            </h3>

            <p className="text-sm text-red-600 dark:text-red-300 text-center mb-4">
                {message}
            </p>

            {onRetry && (
                <BaseButton
                    onClick={onRetry}
                    variant="outline"
                    className="flex items-center gap-2 text-red-600 hover:text-red-700 dark:text-red-400 dark:hover:text-red-300"
                    icon={<RefreshCw className="w-4 h-4" />}
                >
                    Try Again
                </BaseButton>
            )}
        </div>
    );
};

export default BaseError;