import React, { useState } from "react";
import { NavLink, useNavigate } from "react-router-dom";
import { toast } from "sonner";
import {
  useResendOtpMutation,
  useSendOtpMutation,
  useResetPasswordMutation,
} from "@/features/auth/authApi";
import BaseInput from "@/components/ui/BaseInput";
import BaseButton from "@/components/ui/BaseButton";
import {
  InputOTP,
  InputOTPGroup,
  InputOTPSeparator,
  InputOTPSlot,
} from "@/components/ui/input-otp";
import { ArrowLeft, Mail, Lock, RefreshCw, ArrowRight } from "lucide-react";
import logo from "@/assets/images/logo.png";

export default function ForgotPassword() {
  const [email, setEmail] = useState("");
  const [otp, setOtp] = useState("");
  const [isOtpSent, setIsOtpSent] = useState(false);
  const [isOtpVerified, setIsOtpVerified] = useState(false);
  const [newPassword, setNewPassword] = useState("");
  const [confirmPassword, setConfirmPassword] = useState("");
  const [showPassword, setShowPassword] = useState(false);
  const [showConfirmPassword, setShowConfirmPassword] = useState(false);

  const [sendOtp, { isLoading: isSendingOtp }] = useSendOtpMutation();
  const [resendOtp, { isLoading: isResendingOtp }] = useResendOtpMutation();
  const [resetPassword, { isLoading: isResetting }] = useResetPasswordMutation();

  const navigate = useNavigate();

  const handleSendOtp = async (e: React.FormEvent) => {
    e.preventDefault();
    if (!email) {
      toast.error("Email is required");
      return;
    }
    try {
      await sendOtp({ email }).unwrap();
      toast.success("OTP sent successfully");
      setIsOtpSent(true);
    } catch (error) {
      toast.error("Failed to send OTP");
    }
  };

  const handleResendOtp = async (e: React.MouseEvent) => {
    e.preventDefault();
    if (!email) return;
    try {
      await resendOtp({ email }).unwrap();
      toast.success("OTP resent successfully");
    } catch (error) {
      toast.error("Failed to resend OTP");
    }
  };

  const handleSaveOtp = (e: React.FormEvent) => {
    e.preventDefault();
    if (!otp) {
      toast.error("OTP is required");
      return;
    }
    setIsOtpVerified(true);
  };

  const handleResetPassword = async (e: React.FormEvent) => {
    e.preventDefault();
    if (!newPassword || !confirmPassword) {
      toast.error("Please fill in all password fields");
      return;
    }
    if (newPassword !== confirmPassword) {
      toast.error("Passwords do not match");
      return;
    }
    try {
      await resetPassword({
        email,
        otp,
        password: newPassword,
        password_confirmation: confirmPassword,
      }).unwrap();
      toast.success("Password reset successfully!");
      navigate("/login");
    } catch (error) {
      toast.error("Failed to reset password");
    }
  };

  return (
    <div className="min-h-screen flex">
      {/* Left Side - Form */}
      <div className="w-full md:w-1/2 flex items-center justify-center px-4 sm:px-6 lg:px-8 bg-white dark:bg-gray-900">
        <div className="w-full max-w-md">
          <div className="md:hidden mb-8">
            <div className="w-48 h-auto mx-auto">
              <img
                src={logo}
                alt="Logo"
                className="w-full h-full object-contain"
              />
            </div>
          </div>

          <NavLink
            to="/login"
            className="inline-flex items-center text-sm text-gray-600 dark:text-gray-400 hover:text-blue-500 dark:hover:text-blue-400 mb-4"
          >
            <ArrowLeft className="w-4 h-4 mr-2" />
            Back to Login
          </NavLink>

          <h2 className="text-3xl font-extrabold text-gray-900 dark:text-white mb-6">
            Forgot Password
          </h2>
          <p className="text-gray-600 dark:text-gray-400 mb-8">
            {isOtpVerified
              ? "Set your new password below"
              : isOtpSent
                ? "Enter the OTP sent to your email"
                : "Enter your email address to reset your password"}
          </p>

          <form
            className="space-y-6"
            onSubmit={
              !isOtpSent
                ? handleSendOtp
                : !isOtpVerified
                  ? handleSaveOtp
                  : handleResetPassword
            }
          >
            {/* Email Input */}
            {!isOtpSent && (
              <div>
                <BaseInput
                  name="email"
                  type="email"
                  value={email}
                  onChange={(e) => setEmail(e.target.value)}
                  placeholder="Enter your email"
                  required
                  label="Email Address"
                />
              </div>
            )}

            {/* OTP Input */}
            {isOtpSent && !isOtpVerified && (
              <div className="space-y-4">
                <div className="bg-blue-50 dark:bg-blue-900/20 p-4 rounded-lg">
                  <p className="text-sm text-blue-600 dark:text-blue-400">
                    OTP sent to <span className="font-medium">{email}</span>
                  </p>
                </div>
                <div>
                  <label className="block text-sm font-medium text-gray-700 dark:text-gray-300 mb-2">
                    Enter OTP
                  </label>
                  <InputOTP
                    maxLength={6}
                    value={otp}
                    onChange={setOtp}
                    className="w-full"
                  >
                    <InputOTPGroup className="w-full flex justify-between gap-2">
                      <InputOTPSlot index={0} className="flex-1 h-12 text-lg" />
                      <InputOTPSlot index={1} className="flex-1 h-12 text-lg" />
                      <InputOTPSlot index={2} className="flex-1 h-12 text-lg" />
                    </InputOTPGroup>
                    <InputOTPSeparator className="mx-2" />
                    <InputOTPGroup className="w-full flex justify-between gap-2">
                      <InputOTPSlot index={3} className="flex-1 h-12 text-lg" />
                      <InputOTPSlot index={4} className="flex-1 h-12 text-lg" />
                      <InputOTPSlot index={5} className="flex-1 h-12 text-lg" />
                    </InputOTPGroup>
                  </InputOTP>
                </div>
                <div className="flex justify-between items-center">
                  <span className="text-sm text-gray-600 dark:text-gray-400">
                    Didn't receive the OTP?
                  </span>
                  <BaseButton
                    type="button"
                    className="text-sm cursor-pointer text-blue-500 dark:text-blue-400 dark:hover:text-blue-300 flex items-center"
                    onClick={handleResendOtp}
                    disabled={isResendingOtp}
                    icon={<RefreshCw className={`w-4 h-4 mr-1 ${isResendingOtp ? 'animate-spin' : ''}`} />}
                  >
                    Resend OTP
                  </BaseButton>
                </div>
              </div>
            )}

            {/* New Password Fields */}
            {isOtpVerified && (
              <div className="space-y-4">
                <BaseInput
                  name="newPassword"
                  type={showPassword ? "text" : "password"}
                  value={newPassword}
                  onChange={(e) => setNewPassword(e.target.value)}
                  placeholder="Enter new password"
                  label="New Password"
                  required
                  showPassword={showPassword}
                  onTogglePassword={() => setShowPassword((prev) => !prev)}
                />
                <BaseInput
                  name="confirmPassword"
                  type={showConfirmPassword ? "text" : "password"}
                  value={confirmPassword}
                  onChange={(e) => setConfirmPassword(e.target.value)}
                  placeholder="Confirm new password"
                  label="Confirm Password"
                  required
                  showPassword={showConfirmPassword}
                  onTogglePassword={() => setShowConfirmPassword((prev) => !prev)}
                />
              </div>
            )}

            {/* Submit Button */}
            <BaseButton
              type="submit"
              className="w-full flex justify-center items-center"
              disabled={isSendingOtp || isResetting}
              isLoading={isSendingOtp || isResetting}
              icon={<ArrowLeft className="ml-2 h-4 w-4" />}
            >
              {!isOtpSent
                ? "Send OTP"
                : !isOtpVerified
                  ? "Verify OTP"
                  : "Reset Password"}

            </BaseButton>
          </form>
        </div>
      </div>

      {/* Right Side - Logo and Text */}
      <div className="hidden md:flex md:w-1/2 bg-blue-600 flex-col items-center justify-center p-10 text-white">
        <div className="max-w-md">
          <div className="w-64 h-32 mb-8">
            <img
              src={logo}
              alt="Logo"
              className="w-full h-full object-contain"
            />
          </div>
          <h1 className="text-4xl font-bold mb-6">
            Reset Your Password
          </h1>
          <p className="text-lg text-blue-100 mb-8">
            Don't worry! It happens. Please enter your email address and we'll send you an OTP to reset your password.
          </p>
          <div className="space-y-4">
            <div className="flex items-center">
              <div className="w-8 h-8 rounded-full bg-blue-500 flex items-center justify-center mr-3">
                <span className="text-lg">✓</span>
              </div>
              <span>Secure Password Reset</span>
            </div>
            <div className="flex items-center">
              <div className="w-8 h-8 rounded-full bg-blue-500 flex items-center justify-center mr-3">
                <span className="text-lg">✓</span>
              </div>
              <span>Quick OTP Verification</span>
            </div>
            <div className="flex items-center">
              <div className="w-8 h-8 rounded-full bg-blue-500 flex items-center justify-center mr-3">
                <span className="text-lg">✓</span>
              </div>
              <span>Instant Email Notifications</span>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}
