import React, { useEffect, useState } from "react";
import { BaseModal } from "@/components/ui/BaseModal";
import { toast } from "sonner";
import {
  useCreateHappyCustomerMutation,
  useUpdateHappyCustomerMutation,
} from "@/features/happyCustomer/happyCustomerApi";

interface FormModalProps {
  isOpen: boolean;
  onClose: () => void;
  editData?: any;
}

export default function FormModal({
  isOpen,
  onClose,
  editData,
}: FormModalProps) {
  const [formData, setFormData] = useState({
    id: undefined,
    title: "",
    image: null as string | File | null,
  });

  const [
    createHappyCustomer,
    {
      isLoading: isCreating,
      isSuccess: isCreated,
      isError: isCreateError,
      error: createError,
    },
  ] = useCreateHappyCustomerMutation();
  const [
    updateHappyCustomer,
    {
      isLoading: isUpdating,
      isSuccess: isUpdated,
      isError: isUpdateError,
      error: updateError,
    },
  ] = useUpdateHappyCustomerMutation();

  // First useEffect for form data initialization
  useEffect(() => {
    if (editData) {
      setFormData({
        ...editData,
        image: editData.image_url || null,
      });
    } else {
      setFormData({
        id: undefined,
        title: "",
        image: null,
      });
    }
  }, [editData, isOpen]);

  const handleChange = (
    e: React.ChangeEvent<HTMLInputElement | HTMLTextAreaElement>
  ) => {
    const { name, value } = e.target;
    setFormData((prev) => ({
      ...prev,
      [name]: value,
    }));
  };

  const onImageChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const files = e.target.files;
    if (files && files.length > 0) {
      setFormData((prev) => ({
        ...prev,
        image: files[0],
      }));
    }
  };

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();

    // Validate required fields
    if (!formData.title.trim()) {
      toast.error("Title is required");
      return;
    }
    if (!formData.image) {
      toast.error("Image is required");
      return;
    }

    try {
      // Create FormData for multipart/form-data
      const formDataToSubmit = new FormData();

      // Add title
      formDataToSubmit.append("title", formData.title.trim());

      // Handle image file
      if (formData.image instanceof File) {
        formDataToSubmit.append("image", formData.image);
      } else if (formData.image) {
        formDataToSubmit.append("image_url", formData.image);
      }

      if (formData?.id) {
        const response = await updateHappyCustomer({
          id: formData.id,
          data: formDataToSubmit,
        }).unwrap();
        console.log("Update response:", response);
        toast.success("Happy customer updated successfully");
      } else {
        const response = await createHappyCustomer(formDataToSubmit).unwrap();
        // const response = await testService.createHappyCustomer(formData);
        console.log("Create response:", response);
        toast.success("Happy customer created successfully");
      }
      onClose();
    } catch (error: any) {
      console.error("Failed to save happy customer:", error);
      if (error.data) {
        console.error("Error details:", error.data);
        toast.error(error.data.message || "Failed to save happy customer");
      }
    }
  };

  if (!isOpen) return null;

  return (
    <BaseModal
      onClose={onClose}
      title={`${formData.id ? "Edit" : "Add"} Happy Customer`}
      onSubmit={() => handleSubmit(new Event("submit") as any)}
      btnCaption={formData.id ? "Update" : "Add"}
      btnLoading={isCreating || isUpdating}
    >
      <div className="max-h-[90vh] flex flex-col">
        <div className="flex-1 pr-2">
          <form className="space-y-4">
            <div>
              <label className="block text-sm font-medium text-gray-700 dark:text-gray-300 mb-1">
                Title
              </label>
              <input
                type="text"
                name="title"
                value={formData.title}
                onChange={handleChange}
                placeholder="Enter promotion title"
                className="w-full px-3 py-2 border rounded-md text-sm text-gray-700 dark:text-gray-300 dark:bg-gray-800 border-gray-300 dark:border-gray-700 focus:outline-none focus:ring-2 focus:ring-blue-500"
                required
              />
            </div>

            <div>
              <label className="block text-sm font-medium text-gray-700 dark:text-gray-300 mb-1">
                Image
              </label>
              <input
                type="file"
                name="image_url"
                onChange={onImageChange}
                className="w-full px-3 py-2 border rounded-md text-sm text-gray-700 dark:text-gray-300 dark:bg-gray-800 border-gray-300 dark:border-gray-700 focus:outline-none focus:ring-2 focus:ring-blue-500"
                required={!formData.id}
              />
              {formData.image && (
                <img
                  src={
                    formData.image instanceof File
                      ? URL.createObjectURL(formData.image)
                      : formData.image
                  }
                  alt="Preview"
                  className="w-32 h-32 mt-2 rounded-md object-cover"
                />
              )}
            </div>
          </form>
        </div>
      </div>
    </BaseModal>
  );
}
