import { baseApi } from "../baseApi";
import apiRoutes from "@/api/apiRoutes";

export const happyCustomerApi = baseApi.injectEndpoints({
  endpoints: (builder) => ({
    getHappyCustomers: builder.query({
      query: ({
        page = 1,
        search = "",
        start_date = null,
        end_date = null,
      }) => ({
        url: `${apiRoutes.happyCustomer.getAll}${
          page ? `?page=${page}` : ""
        }${search ? `&search=${search}` : ""}${
          start_date ? `&start_date=${start_date}` : ""
        }${end_date ? `&end_date=${end_date}` : ""}`,
        method: "GET",
      }),
      transformResponse: (response: any) => {
        return response.data;
      },
      providesTags: ["HappyCustomer"],
    }),
    getHappyCustomerById: builder.query({
      query: (id) => ({
        url: apiRoutes.happyCustomer.getById(id),
        method: "GET",
      }),
      transformResponse: (response: any) => response.data,
      providesTags: ["HappyCustomer"],
    }),
    createHappyCustomer: builder.mutation({
      query: (data) => ({
        url: apiRoutes.happyCustomer.create,
        method: "POST",
        body: data,
      }),
      invalidatesTags: ["HappyCustomer"],
    }),
    updateHappyCustomer: builder.mutation({
      query: ({ id, data }) => ({
        url: apiRoutes.happyCustomer.update(id),
        method: "POST",
        body: data,
      }),
      invalidatesTags: ["HappyCustomer"],
    }),
    deleteHappyCustomer: builder.mutation({
      query: (id) => ({
        url: apiRoutes.happyCustomer.delete(id),
        method: "DELETE",
      }),
      invalidatesTags: ["HappyCustomer"],
    }),
  }),
});

export const {
  useGetHappyCustomersQuery,
  useGetHappyCustomerByIdQuery,
  useCreateHappyCustomerMutation,
  useUpdateHappyCustomerMutation,
  useDeleteHappyCustomerMutation,
} = happyCustomerApi;
