import { baseApi } from "../baseApi";
import apiRoutes from "@/api/apiRoutes";

interface Inclusion {
    id: string;
    name: string;
    description: string;
    created_at: string;
}

interface InclusionResponse {
    data: Inclusion[];
    total: number;
    current_page: number;
    per_page: number;
}

export const inclusionApi = baseApi.injectEndpoints({
    endpoints: (builder) => ({
        getInclusions: builder.query<InclusionResponse, { page?: number; search?: string, start_date?: string, end_date?: string, limit?: number, last_page?: number, total?: number }>({
            query: ({ page = 1, search = "", start_date = null, end_date = null, limit = 100, last_page = 1, total = 0 }) => ({
                url: `${apiRoutes.inclusion.getAll}?page=${page}&search=${search}&start_date=${start_date}&end_date=${end_date}&last_page=${last_page}&total=${total}&per_page=${limit}`,
                method: "GET",
            }),
            transformResponse: (response: any) => response,
            providesTags: ["Inclusion"],
        }),
        getInclusionById: builder.query<Inclusion, string>({
            query: (id) => ({
                url: apiRoutes.inclusion.getById(id),
                method: "GET",
            }),
            transformResponse: (response: any) => response,
            providesTags: ["Inclusion"],
        }),
        createInclusion: builder.mutation<Inclusion, Inclusion>({
            query: (inclusion) => ({
                url: apiRoutes.inclusion.create,
                method: "POST",
                body: inclusion,
            }),
            invalidatesTags: ["Inclusion"],
        }),
        updateInclusion: builder.mutation<Inclusion, Inclusion>({
            query: (inclusion) => ({
                url: apiRoutes.inclusion.update(inclusion.id),
                method: "PUT",
                body: inclusion,
            }),
            invalidatesTags: ["Inclusion"],
        }),
        deleteInclusion: builder.mutation<Inclusion, string>({
            query: (id) => ({
                url: apiRoutes.inclusion.delete(id),
                method: "DELETE",
            }),
            invalidatesTags: ["Inclusion"],
        }),
    }),
});

export const {
    useGetInclusionsQuery,
    useGetInclusionByIdQuery,
    useCreateInclusionMutation,
    useUpdateInclusionMutation,
    useDeleteInclusionMutation,
} = inclusionApi;
