import React from "react";
import { BaseModal } from "@/components/ui/BaseModal";
import { Info } from "lucide-react";

interface ViewModalProps {
    isOpen: boolean;
    onClose: () => void;
    happyCustomer?: {
        id: number;
        title: string;
        image_url: string;
    };
}

export default function ViewModal({ isOpen, onClose, happyCustomer }: ViewModalProps) {
    if (!isOpen) return null;

    console.log("happyCustomer", happyCustomer)

    if (!happyCustomer) {
        return (
            <BaseModal onClose={onClose} width="max-w-xl" isSubmit={false} title="Happy Customer Details">
                <div className="flex items-center justify-center py-8">
                    <p className="text-gray-500 dark:text-gray-400">No happy customer data available</p>
                </div>
            </BaseModal>
        );
    }

    return (
        <BaseModal onClose={onClose} width="max-w-xl" isSubmit={false} title="Happy Customer Details">
            <div className="flex flex-col">
                <div className="space-y-6">
                    {/* Header Section with Image */}
                    <div className="relative">
                        <div className="aspect-video w-full overflow-hidden rounded-lg">
                            <img
                                src={happyCustomer.image_url}
                                alt={happyCustomer.title}
                                className="w-full h-full object-contain"
                            />
                        </div>
                        <div className="absolute bottom-0 left-0 right-0 bg-gradient-to-t from-black/70 to-transparent p-4">
                            <h3 className="text-xl font-bold text-white mb-1">
                                {happyCustomer.title}
                            </h3>
                        </div>
                    </div>
                </div>
            </div>
        </BaseModal>
    );
}
