import BoxItem from "@/components/BoxItem";
import RecentBookings from "@/components/dashbaord/RecentBookings";
import { useGetDashboardDataQuery } from "@/features/dashboard/dashboardApi";
import {
  Calendar,
  Users,
  DollarSign,
  CheckCircle,
  XCircle,
  Briefcase,
  MapPin,
  Star,
  List,
  TicketPercent,
  Clock,
} from "lucide-react";
import React from "react";
import { constData } from "@/api/constData";
import { useGetTodayUpcomingBookingsQuery } from "@/features/dashboard/dashboardApi";

export default function Dashboard() {
  const { data: stats, isLoading, error } = useGetDashboardDataQuery();

  const {
    data: upcomingBookingsData,
    isLoading: isUpcomingBookingsLoading,
    error: upcomingBookingsError,
  } = useGetTodayUpcomingBookingsQuery({
    page: 1,
    search: "",
  });

  const boxData = [
    {
      title: "Total Booking",
      value: stats?.bookings?.total ?? 0, // Placeholder, update if you have real data
      isPositive: true,
      icon: <Calendar className="w-6 h-6" />,
    },
    {
      title: "Confirmed Booking",
      value: stats?.bookings?.confirmed ?? 0,
      isPositive: true,
      icon: <Briefcase className="w-6 h-6" />,
    },
    {
      title: "Pending Booking",
      value: stats?.bookings?.pending ?? 0,
      isPositive: true,
      icon: <Clock className="w-6 h-6" />,
    },
    {
      title: "Cancelled Booking",
      value: stats?.bookings?.cancelled ?? 0,
      isPositive: false,
      icon: <XCircle className="w-6 h-6" />,
    },
    {
      title: "Total Customers",
      value: stats?.users?.total_customers ?? 0,
      percentage: "1.45",
      isPositive: true,
      icon: <Users className="w-6 h-6" />,
    },
    {
      title: "Total Staff",
      value: stats?.users?.total_staff ?? 0,
      percentage: "1.45",
      isPositive: true,
      icon: <Users className="w-6 h-6" />,
    },
    {
      title: "Total inclusions",
      value: stats?.inclusions?.total_inclusions ?? 0,
      isPositive: true,
      icon: <List className="w-6 h-6" />,
    },
    {
      title: "Total Reviews",
      value: stats?.reviews?.total_reviews ?? 0,
      isPositive: true,
      icon: <Star className="w-6 h-6" />,
    },
    {
      title: "Approved Reviews",
      value: stats?.reviews?.approved_reviews ?? 0,
      isPositive: true,
      icon: <Star className="w-6 h-6" />,
    },
    {
      title: "Pending Reviews",
      value: stats?.reviews?.pending_reviews ?? 0,
      isPositive: false,
      icon: <Star className="w-6 h-6" />,
    },
    {
      title: "Total Promotions",
      value: stats?.banners?.total_banners ?? 0,
      isPositive: true,
      icon: <TicketPercent className="w-6 h-6" />,
    },
    {
      title: "Active Promotions",
      value: stats?.banners?.active_banners ?? 0,
      isPositive: true,
      icon: <TicketPercent className="w-6 h-6" />,
    },
  ];

  const columns = [
    {
      title: "Sl No",
      key: "index",
      sort: false,
      width: "70px",
      className: "text-center",
    },
    {
      title: "Job Ref",
      key: "job_reference",
      sort: false,
      width: "180px",
    },
    {
      title: "Date",
      key: "booking_datetime",
      sort: true,
      width: "120px",
      render: (value: string) => value.split(" ").slice(0, 3).join(" "),
    },
    {
      title: "Day",
      key: "booking_day_name",
      sort: true,
      width: "120px",
    },
    {
      title: "Time",
      key: "pickup_time",
      sort: true,
      width: "100px",
      render: (value: string) =>
        new Date(value).toLocaleTimeString([], {
          hour: "2-digit",
          minute: "2-digit",
          hour12: false,
        }),
    },
    {
      title: "Traveler",
      key: "lead_traveler",
      sort: true,
      width: "150px",
    },
    {
      title: "No of Travelers",
      key: "traveler_count",
      sort: true,
      width: "150px",
      className: "text-center",
    },
    {
      title: "Pickup",
      key: "pickup_location",
      sort: true,
      width: "150px",
    },
    {
      title: "Tour Destination",
      key: "description",
      sort: true,
      width: "250px",
    },
    {
      title: "Tour Duration",
      key: "tour_duration",
      sort: true,
      width: "150px",
      className: "text-center",
    },
    {
      title: "Status",
      key: "status",
      render: (value: string) => (
        <span
          className={`px-2 py-1 rounded text-sm font-medium ${value === constData.status.confirmed
            ? "bg-blue-100 text-blue-600"
            : value === constData.status.cpending
              ? "bg-yellow-100 text-yellow-600"
              : value === constData.status.completed
                ? "bg-green-100 text-green-600"
                : "bg-red-100 text-red-600"
            }`}
        >
          {value.charAt(0).toUpperCase() + value.slice(1)}
        </span>
      ),
      sort: true,
    },
    {
      title: "Reference",
      key: "booking_reference",
      sort: true,
      width: "150px",
    },
    {
      title: "Drop Off Date",
      key: "drop_off_time",
      sort: true,
      width: "140px",
      render: (value: string) => value.split(" ").slice(0, 3).join(" "),
    },
    {
      title: "Drop Off Time",
      key: "drop_off_time",
      sort: true,
      width: "150px",
      render: (value: string) =>
        new Date(value).toLocaleTimeString([], {
          hour: "2-digit",
          minute: "2-digit",
          hour12: false,
        }),
    },
    // {
    // title: "Seller Info",
    // key: "seller_info",
    // sort: true,
    // width: "180px",
    // },
    {
      title: "Booking Source",
      key: "booking_source",
      sort: true,
      width: "150px",
      render: (value: string) => value?.name
    },
    {
      title: "Created By",
      key: "created_by",
      sort: true,
      width: "150px",
    },
    {
      title: "Created At",
      key: "created_at",
      sort: true,
      width: "180px",
    },
  ];

  return (
    <>
      <div className="grid grid-cols-2 sm:grid-cols-3 lg:grid-cols-4 gap-4">
        {boxData.map((box, index) => (
          <BoxItem
            key={index}
            title={box.title}
            value={box.value}
            percentage={box.percentage}
            isPositive={box.isPositive}
            icon={box.icon}
            isLoading={isLoading}
          />
        ))}
      </div>

      <div className="mt-10">
        <RecentBookings
          columns={columns}
          data={upcomingBookingsData?.today}
          isLoading={isUpcomingBookingsLoading}
          error={upcomingBookingsError}
          title="Today's Booking"
        />
      </div>

      <div className="mt-10">
        <RecentBookings
          columns={columns}
          data={upcomingBookingsData?.upcoming}
          isLoading={isUpcomingBookingsLoading}
          error={upcomingBookingsError}
          title="Upcoming Booking"
        />
      </div>
    </>
  );
}
