import { createSlice, PayloadAction } from "@reduxjs/toolkit";

interface TableFilterState {
    page: number;
    limit: number;
    start_date: string | null;
    end_date: string | null;
    search: string;
    sort_by: string;
    sort_order: 'asc' | 'desc';
    filters: Record<string, any>;
}

const initialState: TableFilterState = {
    page: 1,
    limit: 20,
    start_date: null,
    end_date: null,
    search: '',
    sort_by: 'created_at',
    sort_order: 'desc',
    filters: {},
};

const tableFilterSlice = createSlice({
    name: "tableFilter",
    initialState,
    reducers: {
        setPage: (state, action: PayloadAction<number>) => {
            state.page = action.payload;
        },
        setLimit: (state, action: PayloadAction<number>) => {
            state.limit = action.payload;
        },
        setDateRange: (state, action: PayloadAction<{ start_date: string | null; end_date: string | null }>) => {
            state.start_date = action.payload.start_date;
            state.end_date = action.payload.end_date;
        },
        setSearch: (state, action: PayloadAction<string>) => {
            state.search = action.payload;
            state.page = 1; // Reset to first page on new search
        },
        setSort: (state, action: PayloadAction<{ sort_by: string; sort_order: 'asc' | 'desc' }>) => {
            state.sort_by = action.payload.sort_by;
            state.sort_order = action.payload.sort_order;
        },
        setFilter: (state, action: PayloadAction<{ key: string; value: any }>) => {
            state.filters[action.payload.key] = action.payload.value;
            state.page = 1; // Reset to first page on new filter
        },
        clearFilters: (state) => {
            state.page = 1;
            state.limit = 10;
            state.start_date = null;
            state.end_date = null;
            state.search = '';
            state.sort_by = 'created_at';
            state.sort_order = 'desc';
            state.filters = {};
        },
    },
});

export const {
    setPage,
    setLimit,
    setDateRange,
    setSearch,
    setSort,
    setFilter,
    clearFilters,
} = tableFilterSlice.actions;

export default tableFilterSlice.reducer;