import React from "react"
import { Skeleton } from "./ui/skeleton"

export default function BoxItem({
  title,
  value,
  percentage,
  isPositive,
  icon,
  isLoading,
}) {
  return (
    <div className="flex items-center gap-3 p-4 bg-blue-50 dark:bg-gray-800 rounded-lg w-full">
      <div className="text-blue-500 dark:text-blue-300 bg-white dark:bg-gray-900 rounded-md p-3 flex items-center justify-center w-12 h-12">
        {icon}
      </div>
      <div className="flex items-end justify-between w-full">
        <div>
          <span className="text-sm font-medium">{title}</span>
          {isLoading ? (
            <Skeleton className="w-12 h-4 bg-gray-300 dark:bg-gray-700" />
          ) : (
            <h3 className="text-2xl font-bold text-gray-800 dark:text-white">{value}</h3>
          )}
        </div>
      </div>
    </div>
  )
}