import React from 'react';
import { ChevronRight } from 'lucide-react';

const PrivacyPolicy = () => {
    return (
        <div className="min-h-screen bg-gray-50 dark:bg-gray-900 py-12 px-4 sm:px-6 lg:px-8">
            <div className="max-w-4xl mx-auto">
                {/* Header */}
                <div className="text-center mb-12">
                    <h1 className="text-4xl font-bold text-gray-900 dark:text-white mb-4">
                        Privacy Policy
                    </h1>
                    <p className="text-gray-600 dark:text-gray-400">
                        Effective Date: May 25, 2025
                    </p>
                </div>

                {/* Main Content */}
                <div className="bg-white dark:bg-gray-800 rounded-lg shadow-lg p-8">
                    {/* Introduction */}
                    <section className="mb-8">
                        <p className="text-gray-700 dark:text-gray-300 leading-relaxed">
                            The Visit London ("we", "our", "us") is committed to protecting your privacy. This Privacy Policy outlines how we collect, use, and safeguard your personal information when you use our services, including booking tours, visiting our website, and communicating with us.
                        </p>
                    </section>

                    {/* Information We Collect */}
                    <section className="mb-8">
                        <h2 className="text-2xl font-semibold text-gray-900 dark:text-white mb-4">
                            1. Information We Collect
                        </h2>
                        <div className="space-y-4">
                            <div>
                                <h3 className="text-xl font-medium text-gray-900 dark:text-white mb-2">
                                    a. Personal Information
                                </h3>
                                <ul className="list-disc list-inside space-y-2 text-gray-700 dark:text-gray-300">
                                    <li>Name</li>
                                    <li>Email address</li>
                                    <li>Phone number</li>
                                    <li>Address</li>
                                    <li>Date of birth</li>
                                    <li>Passport or ID details (if required for travel)</li>
                                    <li>Payment information</li>
                                </ul>
                            </div>
                            <div>
                                <h3 className="text-xl font-medium text-gray-900 dark:text-white mb-2">
                                    b. Booking and Travel Information
                                </h3>
                                <ul className="list-disc list-inside space-y-2 text-gray-700 dark:text-gray-300">
                                    <li>Travel preferences</li>
                                    <li>Tour history</li>
                                    <li>Special requests (e.g., dietary, accessibility)</li>
                                </ul>
                            </div>
                            <div>
                                <h3 className="text-xl font-medium text-gray-900 dark:text-white mb-2">
                                    c. Technical Data
                                </h3>
                                <ul className="list-disc list-inside space-y-2 text-gray-700 dark:text-gray-300">
                                    <li>IP address</li>
                                    <li>Browser type and version</li>
                                    <li>Device information</li>
                                    <li>Cookies and usage data</li>
                                </ul>
                            </div>
                        </div>
                    </section>

                    {/* How We Use Your Information */}
                    <section className="mb-8">
                        <h2 className="text-2xl font-semibold text-gray-900 dark:text-white mb-4">
                            2. How We Use Your Information
                        </h2>
                        <ul className="list-disc list-inside space-y-2 text-gray-700 dark:text-gray-300">
                            <li>To process bookings and provide our travel services</li>
                            <li>To communicate with you about your trip</li>
                            <li>To improve our services and customer experience</li>
                            <li>To comply with legal obligations</li>
                            <li>To send marketing materials (if consent is provided)</li>
                        </ul>
                    </section>

                    {/* Sharing Your Information */}
                    <section className="mb-8">
                        <h2 className="text-2xl font-semibold text-gray-900 dark:text-white mb-4">
                            3. Sharing Your Information
                        </h2>
                        <p className="text-gray-700 dark:text-gray-300 mb-4">
                            We do not sell your personal information. We may share your information with:
                        </p>
                        <ul className="list-disc list-inside space-y-2 text-gray-700 dark:text-gray-300">
                            <li>Third-party service providers (e.g., hotels, transport providers)</li>
                            <li>Payment processors</li>
                            <li>Government agencies (when legally required)</li>
                            <li>Our affiliates and partners (only as needed for your services)</li>
                        </ul>
                        <p className="text-gray-700 dark:text-gray-300 mt-4">
                            All third parties are required to handle your information securely and only for specified purposes.
                        </p>
                    </section>

                    {/* Data Security */}
                    <section className="mb-8">
                        <h2 className="text-2xl font-semibold text-gray-900 dark:text-white mb-4">
                            4. Data Security
                        </h2>
                        <p className="text-gray-700 dark:text-gray-300">
                            We implement appropriate security measures to protect your data from unauthorized access, disclosure, or misuse. However, no system is 100% secure.
                        </p>
                    </section>

                    {/* Your Rights */}
                    <section className="mb-8">
                        <h2 className="text-2xl font-semibold text-gray-900 dark:text-white mb-4">
                            5. Your Rights
                        </h2>
                        <p className="text-gray-700 dark:text-gray-300 mb-4">
                            Depending on your location, you may have rights including:
                        </p>
                        <ul className="list-disc list-inside space-y-2 text-gray-700 dark:text-gray-300">
                            <li>Accessing your personal data</li>
                            <li>Correcting or deleting your data</li>
                            <li>Objecting to or limiting data processing</li>
                            <li>Withdrawing consent for marketing</li>
                        </ul>
                    </section>

                    {/* Cookies and Tracking */}
                    <section className="mb-8">
                        <h2 className="text-2xl font-semibold text-gray-900 dark:text-white mb-4">
                            6. Cookies and Tracking Technologies
                        </h2>
                        <p className="text-gray-700 dark:text-gray-300">
                            Our website uses cookies to enhance your experience. You can choose to disable cookies through your browser settings.
                        </p>
                    </section>

                    {/* Data Retention */}
                    <section className="mb-8">
                        <h2 className="text-2xl font-semibold text-gray-900 dark:text-white mb-4">
                            7. Data Retention
                        </h2>
                        <p className="text-gray-700 dark:text-gray-300">
                            We retain your information only as long as necessary to provide our services and comply with legal obligations.
                        </p>
                    </section>

                    {/* International Data Transfers */}
                    <section className="mb-8">
                        <h2 className="text-2xl font-semibold text-gray-900 dark:text-white mb-4">
                            8. International Data Transfers
                        </h2>
                        <p className="text-gray-700 dark:text-gray-300">
                            If you are located outside the United Kingdom, your data may be transferred to and processed in countries that may not have the same data protection laws. We take steps to ensure your data is treated securely.
                        </p>
                    </section>

                    {/* Updates to Policy */}
                    <section className="mb-8">
                        <h2 className="text-2xl font-semibold text-gray-900 dark:text-white mb-4">
                            9. Updates to This Policy
                        </h2>
                        <p className="text-gray-700 dark:text-gray-300">
                            We may update this Privacy Policy from time to time. Any changes will be posted on our website with an updated effective date.
                        </p>
                    </section>

                    {/* Contact Information */}
                    <section className="mt-12 pt-8 border-t border-gray-200 dark:border-gray-700">
                        <h2 className="text-2xl font-semibold text-gray-900 dark:text-white mb-4">
                            10. Contact Us
                        </h2>
                        <p className="text-gray-700 dark:text-gray-300 mb-4">
                            If you have any questions or concerns about this Privacy Policy, contact us at:
                        </p>
                        <div className="text-gray-700 dark:text-gray-300">
                            <p>The Visit London Group</p>
                            <p>Email: support@thevisitlondon.com</p>
                            <p>Phone: +44 7860 175230</p>
                        </div>
                    </section>
                </div>
            </div>
        </div>
    );
};

export default PrivacyPolicy;
