import React, { useState } from "react";
import BaseInput from "@/components/ui/BaseInput";
import { useChangePasswordMutation } from "@/features/auth/authApi";
import { toast } from "sonner";
import BaseButton from "@/components/ui/BaseButton";
import { useApiError } from "@/hooks/useApiError";
import BaseError from "@/components/ui/BaseError";

export default function ChangePassword() {
  const [form, setForm] = useState({
    currentPassword: "",
    newPassword: "",
    confirmPassword: "",
  });
  const [showPassword, setShowPassword] = useState({
    currentPassword: false,
    newPassword: false,
    confirmPassword: false,
  });

  // RTK Query mutation hook
  const [changePassword, { isLoading, error }] = useChangePasswordMutation();
  const errorDetails = useApiError(error);

  const handleChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    setForm({ ...form, [e.target.name]: e.target.value });
  };

  const togglePasswordVisibility = (field: keyof typeof showPassword) => {
    setShowPassword((prev) => ({
      ...prev,
      [field]: !prev[field],
    }));
  };

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();

    // Validate passwords match
    if (form.newPassword !== form.confirmPassword) {
      toast.error("New password and confirm password do not match.");
      return;
    }

    // Validate password strength
    if (form.newPassword.length < 8) {
      toast.error("Password must be at least 8 characters long.");
      return;
    }

    try {
      const result = await changePassword({
        current_password: form.currentPassword,
        new_password: form.newPassword,
        new_password_confirmation: form.confirmPassword,
      }).unwrap();

      if (result.code === "success") {
        toast.success(result.message || "Password updated successfully!");
        // Clear form after successful update
        setForm({
          currentPassword: "",
          newPassword: "",
          confirmPassword: "",
        });
      }
    } catch (error) {
      console.error('Password change failed:', error);
    }
  };

  if (error) {
    return (
      <BaseError
        title="Failed to Change Password"
        message={errorDetails?.message || "An error occurred while changing your password. Please try again."}
        onRetry={() => window.location.reload()}
      />
    );
  }

  return (
    <div className="min-h-screen bg-gray-50 dark:bg-gray-900 flex items-center justify-center p-6">
      <div className="bg-white dark:bg-gray-800 rounded-lg shadow-lg p-6 max-w-md w-full">
        <h2 className="text-2xl font-semibold text-gray-800 dark:text-white mb-6 text-center">
          Change Password
        </h2>
        <form onSubmit={handleSubmit} className="space-y-4">
          <BaseInput
            label="Current Password"
            name="currentPassword"
            type={showPassword.currentPassword ? "text" : "password"}
            value={form.currentPassword}
            onChange={handleChange}
            showPassword={showPassword.currentPassword}
            onTogglePassword={() => togglePasswordVisibility("currentPassword")}
            placeholder="Enter current password"
            required
            disabled={isLoading}
          />
          <BaseInput
            label="New Password"
            name="newPassword"
            type={showPassword.newPassword ? "text" : "password"}
            value={form.newPassword}
            onChange={handleChange}
            showPassword={showPassword.newPassword}
            onTogglePassword={() => togglePasswordVisibility("newPassword")}
            placeholder="Enter new password"
            required
            disabled={isLoading}
          />
          <BaseInput
            label="Confirm Password"
            name="confirmPassword"
            type={showPassword.confirmPassword ? "text" : "password"}
            value={form.confirmPassword}
            onChange={handleChange}
            showPassword={showPassword.confirmPassword}
            onTogglePassword={() => togglePasswordVisibility("confirmPassword")}
            placeholder="Confirm new password"
            required
            disabled={isLoading}
          />
          <BaseButton
            type="submit"
            className="w-full px-4 py-2 bg-blue-500 text-white rounded-md hover:bg-blue-600 focus:outline-none focus:ring-2 focus:ring-blue-500 disabled:opacity-50 disabled:cursor-not-allowed"
            disabled={isLoading}
            isLoading={isLoading}
          >
            {isLoading ? "Updating Password..." : "Update Password"}
          </BaseButton>
        </form>
      </div>
    </div>
  );
}
