import React, { useEffect, useState } from "react";
import BaseTable from "@/components/table/BaseTable";
import BaseButton from "@/components/ui/BaseButton";
import { Edit, Plus, Trash } from "lucide-react";
import { useSelector } from "react-redux";
import { RootState } from "@/store";
import DeleteModal from "@/components/ui/DeleteModal";
import FormModal from "./modal/FormModal";
import { toast } from "sonner";
import { useDeleteBookingSourceMutation, useGetBookingSourceQuery } from "@/features/bookingSource/bookingSourceApi";

interface BookingSource {
    id: string;
    name: string;
    created_at: string;
}

export default function BookingSource() {
    const [isModalOpen, setIsModalOpen] = useState(false);
    const [isDeleteModalOpen, setIsDeleteModalOpen] = useState(false);
    const [selectedBookingSource, setSeelctedBookingSource] = useState<BookingSource | null>(null);
    const [editData, setEditData] = useState<BookingSource | null>(null);

    const { search, page, start_date, end_date } = useSelector((state: RootState) => state.tableFilter);

    const { data: bookingSourceData, isLoading, isFetching, error, refetch } = useGetBookingSourceQuery({
        page,
        search,
        start_date,
        end_date,
    });

    const [deleteBookingSource, { isLoading: isDeleting }] = useDeleteBookingSourceMutation();

    const columns = [
        {
            title: "Action",
            key: "actions",
            sort: false,
            width: "70px",
            className: "text-center",
            dropdownItems: [
                {
                    icon: <Edit className="w-5 h-5" />,
                    label: "Edit",
                    onClick: (row: BookingSource) => {
                        setEditData(row);
                        setIsModalOpen(true);
                    },
                    show: true,
                },
                {
                    icon: <Trash className="w-5 h-5" />,
                    label: "Delete",
                    onClick: (row: BookingSource) => {
                        setSeelctedBookingSource(row);
                        setIsDeleteModalOpen(true);
                    },
                    className: "text-red-500 hover:text-red-600",
                    show: true,
                },
            ],
        },
        {
            title: "Sl No",
            key: "index",
            sort: false,
            width: "70px",
            className: "text-center",
        },
        {
            title: "Name",
            key: "name",
            sort: true,
            width: "150px",
        },
        {
            title: "Created At",
            key: "created_at",
            sort: true,
            width: "150px",
            render: (value: string) => new Date(value).toLocaleDateString(),
        },
    ];

    const handleDelete = async () => {
        if (!selectedBookingSource) return;
        try {
            await deleteBookingSource(selectedBookingSource?.id).unwrap();
            refetch();
            setIsDeleteModalOpen(false);
            setSeelctedBookingSource(null);
            toast.success("Booking Source deleted successfully");
        } catch (error) {
            console.error("Failed to delete booking source:", error);
            toast.error("Failed to delete booking source");
        }
    };

    return (
        <>
            <BaseTable
                title="All Booking Source"
                columns={columns}
                data={bookingSourceData?.data || []}
                searchPlaceholder="Search booking source..."
                showSearch={true}
                loading={isLoading || isFetching}
                limit={bookingSourceData?.per_page || 100}
                lastPage={bookingSourceData?.last_page || 0}
                error={error}
                refetch={refetch}
                total={bookingSourceData?.total || 0}
                actionButton={
                    <BaseButton
                        onClick={() => {
                            setEditData(null);
                            setIsModalOpen(true);
                        }}
                        icon={<Plus className="w-5 h-5" />}
                    >
                        Add
                    </BaseButton>
                }
            />

            <FormModal
                isOpen={isModalOpen}
                onClose={() => setIsModalOpen(false)}
                editData={editData}
                afterSubmit={refetch}
            />

            {isDeleteModalOpen && (
                <DeleteModal
                    isOpen={isDeleteModalOpen}
                    onClose={() => {
                        setIsDeleteModalOpen(false);
                        setSeelctedBookingSource(null);
                    }}
                    onConfirm={handleDelete}
                    isLoading={isDeleting}
                    title="Delete Booking Source"
                    message={`Are you sure you want to delete ${selectedBookingSource?.name}?`}
                />
            )}
        </>
    );
}

