import React, { useState, useCallback } from 'react';
import { Upload, X, FileText } from 'lucide-react';
import { BaseModal } from '@/components/ui/BaseModal';
import BaseButton from '@/components/ui/BaseButton';
import { toast } from 'sonner';

interface ImportModalProps {
    isOpen: boolean;
    onClose: () => void;
    onUpload: (file: File) => void;
}

export default function ImportModal({ isOpen, onClose, onUpload }: ImportModalProps) {
    const [file, setFile] = useState<File | null>(null);
    const [isDragging, setIsDragging] = useState(false);
    const [uploadProgress, setUploadProgress] = useState(0);

    const handleDragOver = useCallback((e: React.DragEvent) => {
        e.preventDefault();
        setIsDragging(true);
    }, []);

    const handleDragLeave = useCallback((e: React.DragEvent) => {
        e.preventDefault();
        setIsDragging(false);
    }, []);

    const handleDrop = useCallback((e: React.DragEvent) => {
        e.preventDefault();
        setIsDragging(false);
        const droppedFile = e.dataTransfer.files[0];
        if (droppedFile && droppedFile.type === 'text/csv') {
            setFile(droppedFile);
        }
    }, []);

    const handleFileSelect = (e: React.ChangeEvent<HTMLInputElement>) => {
        const selectedFile = e.target.files?.[0];
        if (selectedFile) {
            setFile(selectedFile);
        }
    };

    const handleUpload = () => {
        if (file) {
            // Simulate upload progress
            let progress = 0;
            const interval = setInterval(() => {
                progress += 10;
                setUploadProgress(progress);
                if (progress >= 100) {
                    clearInterval(interval);
                    // Show "Coming Soon" message
                    toast.info("CSV Import feature will be available soon!", {
                        description: "We're working on implementing this feature. Stay tuned!",
                        duration: 5000,
                    });
                    // Reset state and close modal
                    setFile(null);
                    setUploadProgress(0);
                    onClose();
                }
            }, 200);
        }
    };

    if (!isOpen) return null;

    return (
        <BaseModal onClose={onClose}>
            <div className="relative">
                {/* Header */}
                <div className="flex items-center justify-between border-b dark:border-gray-700 pb-4 mb-4">
                    <h2 className="text-xl font-semibold text-gray-800 dark:text-white">
                        Import Bookings
                    </h2>
                </div>

                {/* Content */}
                <div
                    className={`border-2 border-dashed rounded-lg p-6 text-center ${isDragging ? 'border-blue-500 bg-blue-50 dark:bg-blue-900/20' : 'border-gray-300 dark:border-gray-600'
                        }`}
                    onDragOver={handleDragOver}
                    onDragLeave={handleDragLeave}
                    onDrop={handleDrop}
                >
                    {file ? (
                        <div className="space-y-4">
                            <div className="flex items-center gap-3">
                                <FileText className="w-8 h-8 text-blue-500" />
                                <div className="text-left">
                                    <p className="text-sm font-medium text-gray-900 dark:text-white">{file.name}</p>
                                    <p className="text-xs text-gray-500 dark:text-gray-400">
                                        {(file.size / 1024).toFixed(2)} KB
                                    </p>
                                </div>
                            </div>
                            {uploadProgress > 0 && (
                                <div className="w-full bg-gray-200 dark:bg-gray-700 rounded-full h-2">
                                    <div
                                        className="bg-blue-500 h-2 rounded-full transition-all duration-300"
                                        style={{ width: `${uploadProgress}%` }}
                                    />
                                </div>
                            )}
                        </div>
                    ) : (
                        <div className="space-y-4">
                            <Upload className="w-12 h-12 mx-auto text-gray-400" />
                            <div>
                                <p className="text-sm text-gray-600 dark:text-gray-300">
                                    Drag and drop your CSV file here, or{' '}
                                    <label className="text-blue-500 hover:text-blue-600 cursor-pointer">
                                        browse
                                        <input
                                            type="file"
                                            accept=".csv"
                                            className="hidden"
                                            onChange={handleFileSelect}
                                        />
                                    </label>
                                </p>
                                <p className="text-xs text-gray-500 dark:text-gray-400 mt-1">
                                    Only CSV files are supported
                                </p>
                            </div>
                        </div>
                    )}
                </div>

                {/* Footer */}
                <div className="flex justify-end gap-3 mt-6">
                    <BaseButton
                        variant="outline"
                        onClick={onClose}
                        icon={<X className="w-5 h-5" />}
                    >
                        Cancel
                    </BaseButton>
                    <BaseButton
                        onClick={handleUpload}
                        disabled={!file || uploadProgress > 0}
                        icon={<Upload className="w-5 h-5" />}
                    >
                        {uploadProgress > 0 ? 'Uploading...' : 'Upload'}
                    </BaseButton>
                </div>
            </div>
        </BaseModal>
    );
}