import React from "react";
import { Eye, EyeOff } from "lucide-react";

interface BaseInputProps extends React.InputHTMLAttributes<HTMLInputElement> {
  label: string;
  name: string;
  value: string;
  type?: string;
  showPassword?: boolean;
  onChange: (e: React.ChangeEvent<HTMLInputElement>) => void;
  onTogglePassword?: () => void;
  error?: string;
  disabled?: boolean;
}

const BaseInput: React.FC<BaseInputProps> = ({
  label,
  name,
  value,
  onChange,
  type = "text",
  className = "",
  showPassword,
  onTogglePassword,
  error,
  disabled,
  ...rest
}) => (
  <div className="relative">
    <label className="block text-sm font-medium text-gray-700 dark:text-gray-300 mb-1">
      {label} {rest.required && <span className="text-red-500">*</span>}
    </label>
    <input
      type={type}
      name={name}
      value={value}
      disabled={disabled}
      onChange={onChange}
      className={`w-full px-3 py-2 border rounded-md text-sm text-gray-700 dark:text-gray-300 dark:bg-gray-800 border-gray-300 dark:border-gray-700 focus:outline-none focus:ring-2 focus:ring-blue-500 ${error ? 'border-red-500 focus:ring-red-500' : ''} ${disabled ? 'bg-gray-100 cursor-not-allowed' : ''} ${className}`}
      {...rest}
    />
    {onTogglePassword && (
      <button
        type="button"
        onClick={onTogglePassword}
        className="absolute top-9 right-3 text-gray-500 dark:text-gray-300 focus:outline-none"
        tabIndex={-1}
      >
        {showPassword ? (
          <Eye className="w-4 h-4" />
        ) : (
          <EyeOff className="w-4 h-4" />
        )}
      </button>
    )}
    {error && (
      <p className="mt-1 text-sm text-red-500">{error}</p>
    )}
  </div>
);

export default BaseInput;
