import * as React from "react"
import { Check, ChevronsUpDown } from "lucide-react"
import { cn } from "@/lib/utils"
import { Button } from "@/components/ui/button"
import {
    Command,
    CommandEmpty,
    CommandGroup,
    CommandInput,
    CommandItem,
} from "@/components/ui/command"
import {
    Popover,
    PopoverContent,
    PopoverTrigger,
} from "@/components/ui/popover"

export interface BookingSource {
    id: string;
    name: string;
    description: string;
    created_at: string;
}

interface BookingSourceComboboxProps {
    bookingSources: BookingSource[];
    value: BookingSource | null;
    onChange: (value: BookingSource | null) => void;
    placeholder?: string;
    className?: string;
    label: string;
    required?: boolean;
}

export function BookingSourceCombobox({
    bookingSources,
    value,
    onChange,
    placeholder = "Select booking source...",
    className,
    label,
    required,
}: BookingSourceComboboxProps) {
    const [open, setOpen] = React.useState(false)

    return (
        <div>
            <label className="block text-sm font-medium text-gray-700 dark:text-gray-300 mb-1">
                {label} {required && <span className="text-red-500">*</span>}
            </label>

            <Popover open={open} onOpenChange={setOpen}>
                <PopoverTrigger asChild>
                    <Button
                        variant="outline"
                        role="combobox"
                        aria-expanded={open}
                        className={cn("w-full justify-between", className)}
                    >
                        {value ? value.name : placeholder}
                        <ChevronsUpDown className="ml-2 h-4 w-4 shrink-0 opacity-50" />
                    </Button>
                </PopoverTrigger>
                <PopoverContent className="w-full p-0" style={{ maxHeight: '300px' }}>
                    <Command className="h-full">
                        <CommandInput placeholder={placeholder} className="h-9" />
                        <CommandEmpty>No booking source found.</CommandEmpty>
                        <CommandGroup className="overflow-auto" style={{ maxHeight: '250px' }}>
                            {bookingSources.map((bookingSource) => (
                                <CommandItem
                                    key={bookingSource.id}
                                    value={bookingSource.name}
                                    onSelect={() => {
                                        onChange(bookingSource)
                                        setOpen(false)
                                    }}
                                    className="h-9"
                                >
                                    <Check
                                        className={cn(
                                            "mr-2 h-4 w-4",
                                            value?.id === bookingSource.id ? "opacity-100" : "opacity-0"
                                        )}
                                    />
                                    <span className="truncate">{bookingSource.name}</span>
                                </CommandItem>
                            ))}
                        </CommandGroup>
                    </Command>
                </PopoverContent>
            </Popover>
        </div>
    )
} 