import { baseApi } from "../baseApi";
import apiRoutes from "@/api/apiRoutes";

export const popularDestinationApi = baseApi.injectEndpoints({
  endpoints: (builder) => ({
    getPopularDestinations: builder.query({
      query: ({
        page = 1,
        search = "",
        start_date = null,
        end_date = null,
      }) => ({
        url: `${apiRoutes.popularDestination.getAll}${
          page ? `?page=${page}` : ""
        }${search ? `&search=${search}` : ""}${
          start_date ? `&start_date=${start_date}` : ""
        }${end_date ? `&end_date=${end_date}` : ""}`,
        method: "GET",
      }),
      transformResponse: (response: any) => {
        console.log("response from api", response);
        return response.data;
      },
      providesTags: ["PopularDestination"],
    }),
    getPopularDestinationById: builder.query({
      query: (id) => ({
        url: apiRoutes.popularDestination.getById(id),
        method: "GET",
      }),
      transformResponse: (response: any) => response.data,
      providesTags: ["PopularDestination"],
    }),
    createPopularDestination: builder.mutation({
      query: (data) => ({
        url: apiRoutes.popularDestination.create,
        method: "POST",
        body: data,
        formData: true,
      }),
      invalidatesTags: ["PopularDestination"],
    }),
    updatePopularDestination: builder.mutation({
      query: ({ id, data }) => ({
        url: apiRoutes.popularDestination.update(id),
        method: "POST",
        body: data,
        formData: true,
      }),
      invalidatesTags: ["PopularDestination"],
    }),
    deletePopularDestination: builder.mutation({
      query: (id) => ({
        url: apiRoutes.popularDestination.delete(id),
        method: "DELETE",
      }),
      invalidatesTags: ["PopularDestination"],
    }),
  }),
});

export const {
  useGetPopularDestinationsQuery,
  useGetPopularDestinationByIdQuery,
  useCreatePopularDestinationMutation,
  useUpdatePopularDestinationMutation,
  useDeletePopularDestinationMutation,
} = popularDestinationApi;
