import React, { useState, useRef, useEffect } from "react";

interface Props extends React.InputHTMLAttributes<HTMLInputElement> {
    label: string;
    value: string;
    onChange: (e: React.ChangeEvent<HTMLInputElement>) => void;
    placeholder?: string;
}

const LocationAutocomplete: React.FC<Props> = ({ label, value, onChange, placeholder, ...props }) => {
    const [suggestions, setSuggestions] = useState<any[]>([]);
    const [showDropdown, setShowDropdown] = useState(false);
    const timeoutRef = useRef<number | null>(null);
    const containerRef = useRef<HTMLDivElement>(null);

    useEffect(() => {
        const handleClickOutside = (event: MouseEvent) => {
            if (containerRef.current && !containerRef.current.contains(event.target as Node)) {
                setShowDropdown(false);
            }
        };

        document.addEventListener('mousedown', handleClickOutside);
        return () => {
            document.removeEventListener('mousedown', handleClickOutside);
        };
    }, []);

    const handleInputChange = (e: React.ChangeEvent<HTMLInputElement>) => {
        onChange(e);
        const val = e.target.value;
        if (timeoutRef.current) clearTimeout(timeoutRef.current);
        if (val.length < 2) {
            setSuggestions([]);
            setShowDropdown(false);
            return;
        }
        // Debounce API call
        timeoutRef.current = window.setTimeout(async () => {
            const res = await fetch(
                `https://nominatim.openstreetmap.org/search?format=json&q=${encodeURIComponent(val)}&addressdetails=1&limit=5`
            );
            const data = await res.json();
            setSuggestions(data);
            setShowDropdown(true);
        }, 300);
    };

    const handleSelect = (suggestion: any) => {
        onChange({
            target: {
                value: suggestion.display_name,
                name: props.name,
            },
        } as React.ChangeEvent<HTMLInputElement>);
        setSuggestions([]);
        setShowDropdown(false);
    };

    return (
        <div style={{ position: "relative" }} ref={containerRef}>
            <label className="block text-sm font-medium mb-1">{label}</label>
            <input
                {...props}
                value={value}
                onChange={handleInputChange}
                autoComplete="off"
                placeholder={placeholder}
                className="w-full border px-3 py-2 rounded-md text-sm text-gray-700 dark:text-gray-300 dark:bg-gray-800 border-gray-300 dark:border-gray-700 focus:outline-none focus:ring-2 focus:ring-blue-500"
            />
            {showDropdown && suggestions.length > 0 && (
                <ul className="absolute z-10 bg-white border w-full mt-1 rounded shadow max-h-48 overflow-auto">
                    {suggestions.map((s, i) => (
                        <li
                            key={i}
                            className="px-3 py-2 hover:bg-blue-100 cursor-pointer"
                            onClick={() => handleSelect(s)}
                        >
                            {s.display_name}
                        </li>
                    ))}
                </ul>
            )}
        </div>
    );
};

export default LocationAutocomplete;
