import React from "react";
import {
  Bell,
  Moon,
  Sun,
  Calendar,
  CheckCircle,
  AlertTriangle,
  Gift,
  ChevronDown,
} from "lucide-react";
import { useState, useEffect, useRef } from "react";
import { SidebarTrigger } from "@/components/ui/sidebar";
import { NavLink, useNavigate } from "react-router-dom";
import { DateRangePicker } from "@/components/ui/DateRangePicker";
import { useDispatch, useSelector } from "react-redux";
import { useLogoutMutation } from "@/features/auth/authApi";
import { clearAuth } from "@/features/auth/authSlice";
import { constData } from "@/api/constData";
import { toast } from "sonner";
import defaultAvatar from "@/assets/images/avatar-ractangle.jpg";

export default function Topbar() {
  const [showNotifications, setShowNotifications] = useState(false);
  const [showProfileMenu, setShowProfileMenu] = useState(false);
  const [isDarkMode, setIsDarkMode] = useState(() => {
    return localStorage.getItem("theme") === "dark";
  });

  const { user } = useSelector((state: any) => state.auth);
  const dispatch = useDispatch();
  const navigate = useNavigate();
  const [logout, { isLoading }] = useLogoutMutation();

  const notificationsRef = useRef<HTMLDivElement>(null);
  const profileMenuRef = useRef<HTMLDivElement>(null);

  const toggleNotifications = () => setShowNotifications(!showNotifications);
  const toggleProfileMenu = () => setShowProfileMenu(!showProfileMenu);

  const toggleDarkMode = () => {
    const newTheme = isDarkMode ? "light" : "dark";
    setIsDarkMode(!isDarkMode);
    localStorage.setItem("theme", newTheme);
    document.documentElement.classList.toggle("dark", newTheme === "dark");
  };

  useEffect(() => {
    const savedTheme = localStorage.getItem("theme") || "light";
    document.documentElement.classList.toggle("dark", savedTheme === "dark");
  }, []);

  useEffect(() => {
    const handleClickOutside = (event: MouseEvent) => {
      if (
        notificationsRef.current &&
        !notificationsRef.current.contains(event.target as Node)
      ) {
        setShowNotifications(false);
      }
      if (
        profileMenuRef.current &&
        !profileMenuRef.current.contains(event.target as Node)
      ) {
        setShowProfileMenu(false);
      }
    };

    document.addEventListener("mousedown", handleClickOutside);
    return () => {
      document.removeEventListener("mousedown", handleClickOutside);
    };
  }, []);

  const handleLogout = async () => {
    try {
      await logout().unwrap();
      dispatch(clearAuth());
      toast.success("Logged out successfully");
      navigate("/login");
    } catch (error) {
      console.error('Logout failed:', error);
      toast.error("Failed to logout. Please try again.");
    }
  };

  const getRoleName = (role: string) => {
    switch (role) {
      case "admin":
        return "Admin";
      case "super_admin":
        return "Super Admin";
      case "user":
        return "User";
      case "driver":
        return "Driver";
      case "passenger":
        return "Passenger";
      default:
        return role;
    }
  };

  return (
    <header className="flex justify-between items-start sm:items-center p-3 border-b shadow-sm bg-white dark:bg-gray-900">
      {/* Left: Sidebar Toggle and Title */}
      <div className="flex items-center w-full sm:w-auto justify-between sm:justify-start sm:mb-0">
        <div className="flex items-center">
          <SidebarTrigger className="cursor-pointer" />
          <h1 className="ml-4 text-xl font-bold text-gray-800 dark:text-white">
            Dashboard
          </h1>
        </div>
      </div>

      {/* Right side controls - always visible, grouped on mobile */}
      <div className="flex items-center gap-2 sm:gap-5 w-full sm:w-auto justify-end">
        {/* Date Picker - Hidden on mobile */}
        {/* <div className="hidden sm:block"> */}
        {/* <DateRangePicker /> */}
        {/* </div> */}

        {/* Icons group for mobile and desktop */}
        <div className="flex items-center gap-5" ref={profileMenuRef}>
          {/* Dark Mode */}
          <button
            title="Toggle Dark Mode"
            className="text-gray-600 dark:text-gray-300 cursor-pointer"
            onClick={toggleDarkMode}
          >
            {isDarkMode ? <Sun className="w-5 h-5" /> : <Moon className="w-5 h-5" />}
          </button>

          {/* Notifications */}
          <div className="relative flex" ref={notificationsRef}>
            <button
              title="Notifications"
              className="text-gray-600 dark:text-gray-300 cursor-pointer relative"
              onClick={toggleNotifications}
            >
              <Bell className="w-5 h-5" />
              <span
                className="absolute top-0 right-0 w-2 h-2 bg-red-500 rounded-full"
                style={{ transform: "translate(50%, -50%)" }}
              ></span>
            </button>
            {showNotifications && (
              <div className="absolute right-0 mt-12 w-72 bg-white dark:bg-gray-800 border rounded-lg shadow-lg z-50 p-4">
                <h4 className="font-semibold text-base mb-3 text-gray-700 dark:text-gray-200">
                  Notifications
                </h4>
                <ul className="space-y-3">
                  <li className="flex items-center gap-3 p-2 rounded-md hover:bg-gray-100 dark:hover:bg-gray-700 cursor-pointer">
                    <span className="text-blue-500">
                      <Bell className="w-5 h-5" />
                    </span>
                    <span className="text-sm text-gray-700 dark:text-gray-300">
                      New booking received
                    </span>
                  </li>
                  <li className="flex items-center gap-3 p-2 rounded-md hover:bg-gray-100 dark:hover:bg-gray-700 cursor-pointer">
                    <span className="text-green-500">
                      <Calendar className="w-5 h-5" />
                    </span>
                    <span className="text-sm text-gray-700 dark:text-gray-300">
                      Schedule updated
                    </span>
                  </li>
                  <li className="flex items-center gap-3 p-2 rounded-md hover:bg-gray-100 dark:hover:bg-gray-700 cursor-pointer">
                    <span className="text-yellow-500">
                      <CheckCircle className="w-5 h-5" />
                    </span>
                    <span className="text-sm text-gray-700 dark:text-gray-300">
                      Task completed
                    </span>
                  </li>
                  <li className="flex items-center gap-3 p-2 rounded-md hover:bg-gray-100 dark:hover:bg-gray-700 cursor-pointer">
                    <span className="text-red-500">
                      <AlertTriangle className="w-5 h-5" />
                    </span>
                    <span className="text-sm text-gray-700 dark:text-gray-300">
                      Payment pending
                    </span>
                  </li>
                  <li className="flex items-center gap-3 p-2 rounded-md hover:bg-gray-100 dark:hover:bg-gray-700 cursor-pointer">
                    <span className="text-purple-500">
                      <Gift className="w-5 h-5" />
                    </span>
                    <span className="text-sm text-gray-700 dark:text-gray-300">
                      Offer applied
                    </span>
                  </li>
                </ul>
              </div>
            )}
          </div>

          {/* Profile Icon (always visible) */}
          <div className="relative flex">
            <button
              onClick={toggleProfileMenu}
              className="flex items-center gap-2 text-gray-700 dark:text-gray-200 cursor-pointer bg-gray-100 dark:bg-gray-800 rounded-full p-0 sm:px-3 sm:py-1"
            >
              <img
                src={user?.profile_image_url || defaultAvatar}
                alt="Profile"
                className="w-8 h-8 rounded-full object-cover"
              />
              <div className="flex-col hidden sm:flex">
                <span className="text-sm text-gray-700 dark:text-gray-200 font-semibold">{user?.name}</span>
                <span className="text-xs text-gray-500 dark:text-gray-400 font-normal">{getRoleName(user?.role)}</span>
              </div>

            </button>

            {/* Profile Dropdown (works on both mobile and desktop) */}
            {showProfileMenu && (
              <div className="absolute right-0 mt-14 w-48 bg-white dark:bg-gray-800 border rounded shadow-lg z-50">
                <ul className="text-sm text-gray-700 dark:text-gray-200">
                  <NavLink
                    to="/update-profile"
                    className={({ isActive }) =>
                      isActive
                        ? "text-gray-800 hover:text-blue-500 dark:hover:text-blue-400 no-active"
                        : "hover:text-blue-500 dark:hover:text-blue-400"
                    }
                  >
                    <li className="p-2 hover:bg-gray-100 dark:hover:bg-gray-700 cursor-pointer">
                      Update Profile
                    </li>
                  </NavLink>
                  <NavLink
                    to="/change-password"
                    className={({ isActive }) =>
                      isActive
                        ? "text-gray-800 hover:text-blue-500 dark:hover:text-blue-400 no-active"
                        : "hover:text-blue-500 dark:hover:text-blue-400"
                    }
                  >
                    <li className="p-2 hover:bg-gray-100 dark:hover:bg-gray-700 cursor-pointer">
                      Change Password
                    </li>
                  </NavLink>
                  <li
                    onClick={handleLogout}
                    className={`p-2 hover:bg-gray-100 dark:hover:bg-gray-700 cursor-pointer ${isLoading ? "opacity-50 cursor-not-allowed" : ""}`}
                  >
                    {isLoading ? "Logging out..." : "Logout"}
                  </li>
                </ul>
              </div>
            )}
          </div>
        </div>
      </div>
    </header>
  );
}
