import { baseApi } from "../baseApi";
import apiRoutes from "@/api/apiRoutes";

export const promotionApi = baseApi.injectEndpoints({
  endpoints: (builder) => ({
    getPromotions: builder.query({
      query: ({ page = 1, search = "", start_date = null, end_date = null }) => ({
        url: `${apiRoutes.promotion.getAll}${page ? `?page=${page}` : ''}${search ? `&search=${search}` : ''}${start_date ? `&start_date=${start_date}` : ''}${end_date ? `&end_date=${end_date}` : ''}`,
        method: "GET",
      }),
      transformResponse: (response: any) => {
        console.log("response from api", response);
        return response.data;
      },
      providesTags: ["Promotion"],
    }),
    getPromotionById: builder.query({
      query: (id) => ({
        url: apiRoutes.promotion.getById(id),
        method: "GET",
      }),
      transformResponse: (response: any) => response.data,
      providesTags: ["Promotion"],
    }),
    createPromotion: builder.mutation({
      query: (data) => ({
        url: apiRoutes.promotion.create,
        method: "POST",
        body: data,
        formData: true,
      }),
      invalidatesTags: ["Promotion"],
    }),
    updatePromotion: builder.mutation({
      query: ({ id, data }) => ({
        url: apiRoutes.promotion.update(id),
        method: "POST",
        body: data,
        formData: true,
      }),
      invalidatesTags: ["Promotion"],
    }),
    deletePromotion: builder.mutation({
      query: (id) => ({
        url: apiRoutes.promotion.delete(id),
        method: "DELETE",
      }),
      invalidatesTags: ["Promotion"],
    }),
  }),
});

export const {
  useGetPromotionsQuery,
  useGetPromotionByIdQuery,
  useCreatePromotionMutation,
  useUpdatePromotionMutation,
  useDeletePromotionMutation,
} = promotionApi;
