import { BaseModal } from "@/components/ui/BaseModal";
import BaseInput from "@/components/ui/BaseInput";
import React, { use, useEffect, useState } from "react";
import { toast } from "sonner";
import {
  useGetCustomerByIdQuery,
  useUpdateCustomerInfoMutation,
} from "@/features/user/userApi";
import { FormValidationController } from "@/utils/formValidation";
import { Trash2 } from "lucide-react";
import defaultAvatar from "@/assets/images/avatar-ractangle.jpg";
import { customService } from "@/api/customService";

interface ValidationError {
  field: string;
  message: string;
}

interface FormModalProps {
  setIsModalOpen: (isOpen: boolean) => void;
  editId?: string | null; // Add this prop to handle edit mode
  setIsCustomerUpdateSuccess: (isSuccess: boolean) => void
}

export default function EditCustomerModal({
  setIsModalOpen,
  editId,
  setIsCustomerUpdateSuccess
}: FormModalProps) {
  const [formValidation, setFormValidation] =
    useState<FormValidationController | null>(null);
  const [formErrors, setFormErrors] = useState<ValidationError[]>([]);
  const [isCustomerUpdating, setIsCustomerUpdating] = useState(false)

  const [formData, setFormData] = useState({
    id: undefined,
    username: "",
    name: "",
    email: "",
    mobile: "",
    address: "",
    profileImage: null as string | null,
    imageFile: null as File | null,
  });

  // Initialize form validation
  useEffect(() => {
    const requiredFields = ["username", "email", "mobile"];
    setFormValidation(
      new FormValidationController(formData, requiredFields, (errors) =>
        setFormErrors(errors)
      )
    );
  }, [formData, editId]);

  const { data: customerData, isLoading: isLoadingCustomer } =
    useGetCustomerByIdQuery(editId || "", {
      skip: !editId,
    });

  useEffect(() => {
    if (customerData) {
      console.log("customerData", customerData);
      setFormData({
        id: customerData.data.id ?? undefined,
        name: customerData.data.name || "",
        username: customerData.data.username || "",
        email: customerData.data.email || "",
        mobile: customerData.data.mobile || "",
        address: customerData.data.address || "",
        profileImage: customerData.data.profile_image_url || "",
        imageFile: null,
      });
    }
  }, [customerData]);

  const handleChange = (
    e: React.ChangeEvent<HTMLInputElement | HTMLTextAreaElement>
  ) => {
    const { name, value } = e.target;
    setFormData((prev) => ({
      ...prev,
      [name]: value,
    }));
  };

  const handleImageChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    if (e.target.files && e.target.files[0]) {
      const file = e.target.files[0];
      setFormData((prev) => ({
        ...prev,
        profileImage: URL.createObjectURL(file),
        imageFile: file,
      }));
    }
  };

  const handleImageRemove = () => {
    setFormData((prev) => ({
      ...prev,
      profileImage: null,
      imageFile: null,
    }));
  };

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    try {
      setIsCustomerUpdating(true)
      // Prepare form data for API
      const formSubmitData = new FormData();
      formSubmitData.append("name", formData.name);
      formSubmitData.append("email", formData.email);
      formSubmitData.append("mobile", formData.mobile);
      formSubmitData.append("address", formData.address);

      if (formData.imageFile) {
        formSubmitData.append("profile_image", formData.imageFile);
      }

      // await updateCustomerInfo({ id: editId, data: formSubmitData }).unwrap();
      const response = await customService.updateCustomerInfo(editId, formSubmitData);
      console.log("Response", response)
      if (response.success) {
        toast.success(response.message || "Customer updated successfully");
        setIsCustomerUpdateSuccess(true)
        setIsModalOpen(false)
      }
    } catch (error) {
      console.error("Failed to update profile:", error);
    } finally {
      setIsCustomerUpdating(false)
    }
  };

  return (
    <BaseModal
      onClose={() => setIsModalOpen(false)}
      title="Edit Customer"
      btnCaption="Update Customer"
      btnLoading={isCustomerUpdating}
      onSubmit={(e) => handleSubmit(e as React.FormEvent)}
    >
      {isLoadingCustomer ? (
        <div className="flex items-center justify-center p-4">
          <div className="animate-spin rounded-full h-8 w-8 border-b-2 border-blue-500"></div>
        </div>
      ) : (
        <form
          onSubmit={(e) => handleSubmit(e as React.FormEvent)}
          className="space-y-4"
        >
          {/* Profile Image */}
          <div className="relative w-32 h-32 mx-auto mb-6 group">
            <img
              src={
                formData.profileImage ||
                customerData?.data?.profile_image_url ||
                defaultAvatar
              }
              alt="Profile"
              className="w-32 h-32 rounded-full object-cover border-4 border-blue-500"
            />
            <label
              htmlFor="profileImage"
              className="absolute bottom-0 right-0 bg-blue-500 text-white p-2 rounded-full cursor-pointer hover:bg-blue-600"
            >
              <svg
                xmlns="http://www.w3.org/2000/svg"
                className="h-5 w-5"
                fill="none"
                viewBox="0 0 24 24"
                stroke="currentColor"
              >
                <path
                  strokeLinecap="round"
                  strokeLinejoin="round"
                  strokeWidth={2}
                  d="M15.232 5.232l3.536 3.536M16 3a2.828 2.828 0 114 4L10 17H6v-4L16 3z"
                />
              </svg>
            </label>
            <input
              type="file"
              id="profileImage"
              accept="image/*"
              className="hidden"
              onChange={handleImageChange}
            />
            {formData.profileImage && (
              <button
                type="button"
                onClick={handleImageRemove}
                className="absolute inset-0 flex items-center justify-center bg-black bg-opacity-40 text-white rounded-full opacity-0 group-hover:opacity-100 transition-opacity cursor-pointer"
              >
                <Trash2 className="text-red-500 z-10" />
              </button>
            )}
          </div>
          <BaseInput
            label="Name"
            name="name"
            value={formData.name}
            onChange={handleChange}
            placeholder="Enter your name"
            required
          />
          <BaseInput
            label="Email"
            name="email"
            type="email"
            value={formData.email}
            onChange={handleChange}
            placeholder="Enter your email"
            required
          />
          <BaseInput
            label="Phone"
            name="phone"
            type="tel"
            value={formData.mobile}
            onChange={handleChange}
            placeholder="Enter your phone number"
            required
          />
          <div>
            <label className="block text-sm font-medium text-gray-700 dark:text-gray-300 mb-1">
              Address
            </label>
            <textarea
              name="address"
              value={formData.address}
              onChange={handleChange}
              placeholder="Enter your address"
              className="w-full px-3 py-2 border rounded-md text-sm text-gray-700 dark:text-gray-300 dark:bg-gray-800 border-gray-300 dark:border-gray-700 focus:outline-none focus:ring-2 focus:ring-blue-500"
              rows={3}
            ></textarea>
          </div>
        </form>
      )}
    </BaseModal>
  );
}
