import { baseApi } from "../baseApi";
import apiRoutes from "@/api/apiRoutes";

export const userApi = baseApi.injectEndpoints({
  endpoints: (builder) => ({
    getUsers: builder.query({
      query: ({ page = 1, search = "" }) => ({
        url: `${apiRoutes.user.getAll}?page=${page}&search=${search}`,
        method: "GET",
      }),
      transformResponse: (response: any) => {
        console.log("response from api", response);
        return response.data;
      },
      providesTags: ["User"],
    }),
    getStaff: builder.query({
      query: ({ page = 1, search = "" }) => ({
        url: `${apiRoutes.user.getAllStaff}?page=${page}&search=${search}`,
        method: "GET",
      }),
      transformResponse: (response: any) => {
        return response.data;
      },
      providesTags: ["Staff"],
    }),
    getStaffById: builder.query({
      query: (id) => ({
        url: apiRoutes.user.getStaffById(id),
        method: "GET",
      }),
      providesTags: ["Staff"],
    }),
    getCustomerById: builder.query({
      query: (id) => ({
        url: apiRoutes.user.getCustomerById(id),
        method: "GET",
      }),
      providesTags: ["Customer"],
    }),
    createStaff: builder.mutation({
      query: (data) => ({
        url: apiRoutes.user.create,
        method: "POST",
        body: data,
      }),
      invalidatesTags: ["Staff"],
    }),
    updateStaff: builder.mutation({
      query: ({ id, data }) => ({
        url: apiRoutes.user.updateStaff(id),
        method: "PUT",
        body: data,
      }),
      invalidatesTags: ["Staff"],
    }),
    deleteStaff: builder.mutation({
      query: (id) => ({
        url: apiRoutes.user.deleteStaff(id),
        method: "DELETE",
      }),
      invalidatesTags: ["Staff"],
    }),
    updateCustomerInfo: builder.mutation({
      query: ({ id, data }) => ({
        url: apiRoutes.user.updateCustomerInfo(id),
        method: "POST",
        body: data,
        formData: true,
      }),
      invalidatesTags: ["Customer"],
    }),
  }),
});

export const {
  useGetUsersQuery,
  useGetStaffQuery,
  useCreateStaffMutation,
  useDeleteStaffMutation,
  useGetStaffByIdQuery,
  useUpdateStaffMutation,
  useGetCustomerByIdQuery,
  useUpdateCustomerInfoMutation,
} = userApi;
