import BaseTable from "@/components/table/BaseTable";
import BaseButton from "@/components/ui/BaseButton";
import { Edit, Eye, Plus, Trash } from "lucide-react";
import { useEffect, useState } from "react";
import FormModal from "./modal/FormModal";
import { Tabs, TabsList, TabsTrigger, TabsContent } from "@/components/ui/tabs";
import {
  useGetStaffQuery,
  useGetUsersQuery,
  useDeleteStaffMutation,
} from "@/features/user/userApi";
import { toast } from "sonner";
import DeleteModal from "@/components/ui/DeleteModal";
import ViewModal from "./modal/ViewModal";
import { useSelector } from "react-redux";
import { constData } from "@/api/constData";
import EditCustomerModal from "./modal/EditCustomerModal";
import { toCapitalized } from "@/utils";

export default function Users() {
  const [isModalOpen, setIsModalOpen] = useState(false);
  const [isCustomerEditModalOpen, setIsCustomerEditModalOpen] = useState(false);
  const [isDeleteModalOpen, setIsDeleteModalOpen] = useState(false);
  const [selectedUser, setSelectedUser] = useState<any>(null);
  const [editId, setEditId] = useState<string | null>(null);
  const [tab, setTab] = useState<"customer" | "staff">("customer");
  const [data, setData] = useState([]);
  const [isViewModalOpen, setIsViewModalOpen] = useState(false);
  const [selectedUserForView, setSelectedUserForView] = useState<any>(null);
  const [isCustomerUpdateSuccess, setIsCustomerUpdateSuccess] = useState(false)

  const { search, page, start_date, end_date } = useSelector((state: any) => state.tableFilter);

  const { user } = useSelector((state: any) => state.auth);

  // Use the queries
  const {
    data: usersData,
    isLoading: usersLoading,
    isFetching: usersFetching,
    error: usersError,
    refetch: usersRefetch,
  } = useGetUsersQuery({
    page,
    search,
    start_date,
    end_date,
  });

  const {
    data: staffsData,
    isLoading: staffsLoading,
    isFetching: staffsFetching,
    error: staffsError,
    refetch: staffsRefetch,
  } = useGetStaffQuery({
    page,
    search,
  });

  // Delete mutation
  const [deleteStaff, { isLoading: isDeleting }] = useDeleteStaffMutation();

  const handleDelete = async () => {
    if (!selectedUser) return;

    try {
      await deleteStaff(selectedUser.id).unwrap();
      toast.success("User deleted successfully");
      setIsDeleteModalOpen(false);
      setSelectedUser(null);
      // Refetch the data
      if (tab === "staff") {
        staffsRefetch();
      } else {
        usersRefetch();
      }
    } catch (error) {
      toast.error("Failed to delete user");
    }
  };

  const handleEdit = (row: any) => {
    setEditId(row.id);
    if (tab === "customer") {
      setIsCustomerEditModalOpen(true);
    } else {
      setIsModalOpen(true);
    }
  };

  const handleCloseModal = () => {
    setIsModalOpen(false);
    setEditId(null);
  };

  const handleView = (row: any) => {
    setSelectedUserForView(row.id);
    setIsViewModalOpen(true);
  };

  useEffect(() => {
    if (isCustomerUpdateSuccess) {
      usersRefetch()
    }
  }, [isCustomerUpdateSuccess])

  const columns = [
    {
      title: "Action",
      key: "actions",
      sort: false,
      width: "70px",
      className: "text-center",
      dropdownItems: [
        {
          icon: <Eye className="w-5 h-5" />,
          label: "View",
          onClick: handleView,
          show: true,
        },
        {
          icon: <Edit className="w-5 h-5" />,
          label: "Edit",
          onClick: (row: any) => handleEdit(row),
          show: tab === "staff" || user?.role === constData.roles.super_admin,
        },
        {
          icon: <Trash className="w-5 h-5" />,
          label: "Delete",
          onClick: (row) => {
            console.log("Delete clicked for row:", row);
            setSelectedUser(row);
            setIsDeleteModalOpen(true);
          },
          className: "text-red-500 hover:text-red-600",
          show: tab === "staff",
        },
      ],
    },
    {
      title: "Sl No",
      key: "index",
      sort: false,
      width: "70px",
      className: "text-center",
    },
    {
      title: "Username",
      key: "username",
      sort: true,
      width: "150px",
    },
    {
      title: "Name",
      key: "name",
      sort: true,
      width: "180px",
    },
    {
      title: "Phone",
      key: "mobile",
      sort: true,
      width: "150px",
    },
    {
      title: "Email",
      key: "email",
      sort: true,
      width: "200px",
      className: "truncate",
      render: (value: string) => (
        <div className="truncate" title={value}>
          {value}
        </div>
      ),
    },
    {
      title: "Gender",
      key: "gender",
      sort: true,
      width: "150px",
      className: "text-center",
    },
    {
      title: "Role",
      key: "role",
      sort: true,
      width: "150px",
      className: "text-center",
      render: (value: string) => toCapitalized(value)
    },
    {
      title: "Address",
      key: "address",
      sort: true,
      width: "200px",
    },
  ];

  useEffect(() => {
    if (tab === "customer" && usersData) {
      setData(usersData.data);
    } else if (tab === "staff" && staffsData) {
      setData(staffsData);
    }
  }, [usersData, staffsData, tab]);

  return (
    <>
      <BaseTable
        title={`All ${tab === "customer" ? "Customers" : "Staffs"}`}
        columns={columns}
        data={data || []}
        searchPlaceholder={
          tab === "customer" ? "Search customer..." : "Search staff..."
        }
        showSearch={true}
        loading={usersLoading || staffsLoading || usersFetching || staffsFetching}
        limit={tab === "customer" ? usersData?.per_page || 100 : staffsData?.per_page || 100}
        lastPage={tab === "customer" ? usersData?.last_page || 1 : staffsData?.last_page || 1}
        error={tab === "customer" ? usersError : staffsError}
        total={tab === "customer" ? usersData?.total || 0 : staffsData?.total || 0}
        refetch={() => {
          usersRefetch();
          staffsRefetch();
        }}
        actionButton={
          <>
            {user?.role === constData.roles.super_admin && (
              <div className="flex items-center gap-2">
                <Tabs defaultValue="customer" className="w-full">
                  <div className="flex justify-between items-center">
                    <TabsList>
                      <TabsTrigger
                        value="customer"
                        onClick={() => setTab("customer")}
                      >
                        Customers
                      </TabsTrigger>
                      <TabsTrigger value="staff" onClick={() => setTab("staff")}>
                        Staff
                      </TabsTrigger>
                    </TabsList>
                  </div>
                </Tabs>
                <BaseButton
                  onClick={() => setIsModalOpen(true)}
                  icon={<Plus className="w-5 h-5" />}
                >
                  Add
                </BaseButton>
              </div>
            )}
          </>
        }
      />

      {isModalOpen && (
        <FormModal setIsModalOpen={handleCloseModal} editId={editId} />
      )}

      {
        isCustomerEditModalOpen && (
          <EditCustomerModal setIsModalOpen={setIsCustomerEditModalOpen} editId={editId} setIsCustomerUpdateSuccess={setIsCustomerUpdateSuccess} />
        )
      }

      {isDeleteModalOpen && (
        <DeleteModal
          isOpen={isDeleteModalOpen}
          onClose={() => setIsDeleteModalOpen(false)}
          onConfirm={handleDelete}
          isLoading={isDeleting}
          title="Delete User"
          message={`Are you sure you want to delete ${selectedUser?.name}?`}
        />
      )}

      {isViewModalOpen && (
        <ViewModal
          isOpen={isViewModalOpen}
          onClose={() => {
            setIsViewModalOpen(false);
            setSelectedUserForView(null);
          }}
          userId={selectedUserForView}
          type={tab}
        />
      )}
    </>
  );
}
