import { createApi, fetchBaseQuery } from '@reduxjs/toolkit/query/react';
import { RootState } from '@/store';
import apiRoutes from '@/api/apiRoutes';
import { useLogoutMutation } from './auth/authApi';

interface AuthState {
    token: string | null;
}

interface RootState {
    auth: AuthState;
}

interface RefreshResponse {
    data: {
        access: string;
    };
}

// Create a custom base query with refresh token logic
const baseQuery = fetchBaseQuery({
    baseUrl: '/api',
    prepareHeaders: (headers, { getState }) => {
        const token = (getState() as RootState).auth.token || localStorage.getItem('token');
        if (token) {
            headers.set('authorization', `Bearer ${token}`);
        }
        headers.set('X-Requested-With', 'XMLHttpRequest');
        return headers;
    },
    credentials: 'include',
});

// Custom base query with refresh token logic and error handling
const baseQueryWithReauth = async (args: any, api: any, extraOptions: any) => {
    let result = await baseQuery(args, api, extraOptions);

    // Handle authentication errors
    if (result.error?.status === 419) {
        console.error("Authentication Error: Session expired or invalid");
        try {
            // Retry the original request
            result = await baseQuery(args, api, extraOptions);
        } catch (retryError) {
            console.error("Failed to refresh session:", retryError);
            localStorage.removeItem("token");
            window.location.href = "/login";
            await useLogoutMutation();
            return Promise.reject(new Error("Session expired. Please login again."));
        }
    }

    // Handle CORS and redirect errors
    if (result.error?.status === 0) {
        console.error("CORS Error: Unable to reach the server");
        return Promise.reject(new Error("Unable to reach the server. Please check your connection."));
    }

    // Handle redirect errors
    if (result.error?.status === 301 || result.error?.status === 302) {
        console.error("Redirect Error: Server is trying to redirect the request");
        return Promise.reject(new Error("Server configuration issue. Please contact the administrator."));
    }

    // Handle 401 Unauthorized
    if (result.error?.status === 401) {
        // Try to get a new token
        const refreshResult = await baseQuery(
            {
                url: apiRoutes.auth.refreshToken,
                method: 'POST',
                credentials: 'include'
            },
            api,
            extraOptions
        ) as RefreshResponse;

        if (refreshResult.data?.access) {
            // Store the new access token
            const newToken = refreshResult.data.access;
            localStorage.setItem('token', newToken);

            // Retry the original query with new token
            result = await baseQuery(args, api, extraOptions);
        } else {
            // If refresh token fails, logout the user
            localStorage.removeItem('token');
            window.location.href = '/login';
            await useLogoutMutation();
        }
    }

    // Handle other authentication errors
    // if (result.error?.status === 500) {
        // localStorage.removeItem('token');
        // window.location.href = '/login';
        // await useLogoutMutation();
    // }

    return result;
};

export const baseApi = createApi({
    reducerPath: 'api',
    baseQuery: baseQueryWithReauth,
    tagTypes: ['User', 'Staff', 'Booking', 'Promotion', 'Profile', 'Customer', 'Review', 'Inclusion', 'Dashboard', 'PopularDestination', 'HappyCustomer', 'BookingSource'],
    endpoints: () => ({}),
});
