import { baseApi } from "../baseApi";
import apiRoutes from "@/api/apiRoutes";

interface BookingSource {
    id: string;
    name: string;
    description: string;
    created_at: string;
}

interface BookingSourceResponse {
    data: BookingSource[];
    total: number;
    current_page: number;
    per_page: number;
}

export const bookingSourceApi = baseApi.injectEndpoints({
    endpoints: (builder) => ({
        getBookingSource: builder.query<BookingSourceResponse, { page?: number; search?: string, start_date?: string, end_date?: string, limit?: number, last_page?: number, total?: number }>({
            query: ({ page = 1, search = "", start_date = null, end_date = null, limit = 100, last_page = 1, total = 0 }) => ({
                url: `${apiRoutes.bookingSource.getAll}?page=${page}&search=${search}&start_date=${start_date}&end_date=${end_date}&last_page=${last_page}&total=${total}&per_page=${limit}`,
                method: "GET",
            }),
            transformResponse: (response: any) => response,
            providesTags: ["BookingSource"],
        }),
        getBookingSourceById: builder.query<BookingSource, string>({
            query: (id) => ({
                url: apiRoutes.bookingSource.getById(id),
                method: "GET",
            }),
            transformResponse: (response: any) => response,
            providesTags: ["BookingSource"],
        }),
        createBookingSource: builder.mutation<BookingSource, BookingSource>({
            query: (bookingSource) => ({
                url: apiRoutes.bookingSource.create,
                method: "POST",
                body: bookingSource,
            }),
            invalidatesTags: ["BookingSource"],
        }),
        updateBookingSource: builder.mutation<BookingSource, BookingSource>({
            query: (bookingSource) => ({
                url: apiRoutes.bookingSource.update(bookingSource.id),
                method: "PUT",
                body: bookingSource,
            }),
            invalidatesTags: ["BookingSource"],
        }),
        deleteBookingSource: builder.mutation<BookingSource, string>({
            query: (id) => ({
                url: apiRoutes.bookingSource.delete(id),
                method: "DELETE",
            }),
            invalidatesTags: ["BookingSource"],
        }),
    }),
});

export const {
    useGetBookingSourceQuery,
    useGetBookingSourceByIdQuery,
    useCreateBookingSourceMutation,
    useUpdateBookingSourceMutation,
    useDeleteBookingSourceMutation,
} = bookingSourceApi;
