import React from "react";
import { BaseModal } from "@/components/ui/BaseModal";
import BaseButton from "@/components/ui/BaseButton";
import { AlertTriangle, Trash, Trash2, X } from "lucide-react";

interface DeleteModalProps {
    isOpen: boolean;
    onClose: () => void;
    onConfirm: () => void;
    isLoading?: boolean;
    title?: string;
    message?: string;
}

export default function DeleteModal({
    isOpen,
    onClose,
    onConfirm,
    isLoading = false,
    title = "Delete Confirmation",
    message = "Are you sure you want to delete this item? This action cannot be undone."
}: DeleteModalProps) {
    return (
        <BaseModal onClose={onClose} title={title} btnCaption="Delete" isSubmit={false} isDelete={true} btnLoading={isLoading} onSubmit={onConfirm}>
            <div className="relative">
                {/* Icon and Title */}
                <div className="flex flex-col items-center text-center px-6 pt-4">
                    <div className="mx-auto flex h-12 w-12 flex-shrink-0 items-center justify-center rounded-full bg-red-100 dark:bg-red-900/30">
                        <AlertTriangle className="h-6 w-6 text-red-600 dark:text-red-400" />
                    </div>
                    <h3 className="mt-4 text-lg font-semibold text-gray-900 dark:text-gray-100">
                        {title}
                    </h3>
                    <p className="mt-2 text-sm text-gray-500 dark:text-gray-400">
                        {message}
                    </p>
                </div>
            </div>
        </BaseModal>
    );
} 