import { Routes, Route } from "react-router-dom";
import DashboardLayout from "@/layouts/DashboardLayout";
import Dashboard from "@/pages/dashboard/Dashboard";
import Bookings from "@/pages/booking/Bookings";
import Login from "@/pages/auth/Login";
import Users from "@/pages/user/Users";
import Promotions from "@/pages/promotion/Promotions";
import AddBooking from "@/pages/booking/AddBooking";
import UpdateProfile from "@/pages/profile/UpdateProfile";
import ChangePassword from "@/pages/profile/ChangePassword";
import ForgotPassword from "@/pages/auth/ForgotPassword";
import PrivateRoute from "./PrivateRoute";
import PublicRoute from "./PublicRoute";
import Reviews from "@/pages/reviews/Reviews";
import Inclusions from "@/pages/inclusions/Inclusions";
import PrivacyPolicy from "@/pages/policy/Privacypolicy";
import TermsAndConditions from "@/pages/policy/TearmsAndConditons";
import PopularDestination from "@/pages/popular-destination/PopularDestination";
import NotFound from "@/pages/NotFound";
import HappyCustomer from "@/pages/happy-customer/happyCustomer";
import BookingSource from "@/pages/booking-source/BookingSource";

function AppRoutes() {
  return (
    <Routes>
      {/* No layout */}
      <Route
        path="/login"
        element={
          <PublicRoute>
            <Login />
          </PublicRoute>
        }
      />
      <Route
        path="/forgot-password"
        element={
          <PublicRoute>
            <ForgotPassword />
          </PublicRoute>
        }
      />

      {/* Routes with Dashboard Layout */}
      <Route
        path="/"
        element={
          <PrivateRoute>
            <DashboardLayout>
              <Dashboard />
            </DashboardLayout>
          </PrivateRoute>
        }
      />
      <Route
        path="/update-profile"
        element={
          <PrivateRoute>
            <DashboardLayout>
              <UpdateProfile />
            </DashboardLayout>
          </PrivateRoute>
        }
      />
      <Route
        path="/change-password"
        element={
          <DashboardLayout>
            <ChangePassword />
          </DashboardLayout>
        }
      />

      <Route
        path="/bookings"
        element={
          <PrivateRoute>
            <DashboardLayout>
              <Bookings />
            </DashboardLayout>
          </PrivateRoute>
        }
      />
      <Route
        path="/bookings/add-booking"
        element={
          <PrivateRoute>
            <DashboardLayout>
              <AddBooking />
            </DashboardLayout>
          </PrivateRoute>
        }
      />
      <Route
        path="/bookings/edit-booking/:id"
        element={
          <PrivateRoute>
            <DashboardLayout>
              <AddBooking />
            </DashboardLayout>
          </PrivateRoute>
        }
      />
      <Route
        path="/users"
        element={
          <PrivateRoute>
            <DashboardLayout>
              <Users />
            </DashboardLayout>
          </PrivateRoute>
        }
      />
      <Route
        path="/promotions"
        element={
          <PrivateRoute>
            <DashboardLayout>
              <Promotions />
            </DashboardLayout>
          </PrivateRoute>
        }
      />
      <Route
        path="/popular-destinations"
        element={
          <PrivateRoute>
            <DashboardLayout>
              <PopularDestination />
            </DashboardLayout>
          </PrivateRoute>
        }
      />
      <Route
        path="/reviews"
        element={
          <PrivateRoute>
            <DashboardLayout>
              <Reviews />
            </DashboardLayout>
          </PrivateRoute>
        }
      />
      <Route
        path="/inclusions"
        element={
          <PrivateRoute>
            <DashboardLayout>
              <Inclusions />
            </DashboardLayout>
          </PrivateRoute>
        }
      />
      <Route
        path="/booking-sources"
        element={
          <PrivateRoute>
            <DashboardLayout>
              <BookingSource />
            </DashboardLayout>
          </PrivateRoute>
        }
      />
      <Route
        path="/happy-customers"
        element={
          <PrivateRoute>
            <DashboardLayout>
              <HappyCustomer />
            </DashboardLayout>
          </PrivateRoute>
        }
      />
      <Route path="/privacy-policy" element={<PrivacyPolicy />} />
      <Route path="/terms-and-conditions" element={<TermsAndConditions />} />

      {/* Catch all route - 404 */}
      <Route path="*" element={<NotFound />} />
    </Routes>
  );
}

export default AppRoutes;
