import { FetchBaseQueryError } from '@reduxjs/toolkit/query';
import { SerializedError } from '@reduxjs/toolkit';

interface ValidationError {
  field: string;
  message: string;
}

interface ServerError {
  errors?: {
    [key: string]: string[];
  };
  message?: string;
}

export class FormValidationController {
  private errors: ValidationError[] = [];
  private requiredFields: string[] = [];
  private formData: Record<string, any>;
  private onErrorChange?: (errors: ValidationError[]) => void;

  constructor(formData: Record<string, any>, requiredFields: string[], onErrorChange?: (errors: ValidationError[]) => void) {
    this.formData = formData;
    this.requiredFields = requiredFields;
    this.onErrorChange = onErrorChange;
  }

  // Validate required fields
  validateRequired(): boolean {
    this.errors = [];
    let isValid = true;

    this.requiredFields.forEach(field => {
      const value = this.formData[field];
      console.log("value", value);
      console.log("field", field);
      if (!value || (typeof value === 'string' && !value.trim())) {
        this.errors.push({
          field,
          message: `${field.charAt(0).toUpperCase() + field.slice(1)} is required`
        });
        isValid = false;
      }
    });

    // Notify about error changes
    if (this.onErrorChange) {
      this.onErrorChange(this.errors);
    }

    return isValid;
  }

  // Handle server errors
  handleServerError(error: FetchBaseQueryError | SerializedError): ValidationError[] {
    this.errors = [];

    if ('data' in error) {
      const serverError = error.data as ServerError;
      
      if (serverError.errors) {
        // Handle field-specific errors
        Object.entries(serverError.errors).forEach(([field, messages]) => {
          this.errors.push({
            field,
            message: messages[0] // Take the first error message
          });
        });
      } else if (serverError.message) {
        // Handle general error message
        this.errors.push({
          field: 'general',
          message: serverError.message
        });
      }
    }

    // Notify about error changes
    if (this.onErrorChange) {
      this.onErrorChange(this.errors);
    }

    return this.errors;
  }

  // Get error for specific field
  getFieldError(fieldName: string): string | undefined {
    return this.errors.find(error => error.field === fieldName)?.message;
  }

  // Check if field has error
  hasFieldError(fieldName: string): boolean {
    return this.errors.some(error => error.field === fieldName);
  }

  // Get all errors
  getAllErrors(): ValidationError[] {
    return this.errors;
  }

  // Clear all errors
  clearErrors(): void {
    this.errors = [];
    if (this.onErrorChange) {
      this.onErrorChange(this.errors);
    }
  }

  // Update form data
  updateFormData(newFormData: Record<string, any>): void {
    this.formData = newFormData;
  }
} 