import React, { useEffect, useState } from "react";
import { BaseModal } from "@/components/ui/BaseModal";
import { toast } from "sonner";
import {
  useCreatePopularDestinationMutation,
  useUpdatePopularDestinationMutation,
} from "@/features/popularDestination/popularDestinationApi";

interface FormModalProps {
  isOpen: boolean;
  onClose: () => void;
  editData?: any;
}

function objectToFormData(obj: Record<string, any>) {
  const fd = new FormData();
  Object.entries(obj).forEach(([key, value]) => {
    if (value !== undefined && value !== null) {
      fd.append(key, value);
    }
  });
  return fd;
}

export default function FormModal({
  isOpen,
  onClose,
  editData,
}: FormModalProps) {
  const [formData, setFormData] = useState({
    id: undefined,
    title: "",
    description: "",
    image_url: null as string | File | null,
    target_url: "",
  });

  const [createPopularDestination, { isLoading: isCreating }] =
    useCreatePopularDestinationMutation();
  const [updatePopularDestination, { isLoading: isUpdating }] =
    useUpdatePopularDestinationMutation();

  useEffect(() => {
    if (editData) {
      setFormData({
        ...editData,
        image_url: editData.image_url || null,
      });
    } else {
      setFormData({
        id: undefined,
        title: "",
        description: "",
        image_url: null,
        target_url: "",
      });
    }
  }, [editData, isOpen]);

  const handleChange = (
    e: React.ChangeEvent<HTMLInputElement | HTMLTextAreaElement>
  ) => {
    const { name, value } = e.target;
    setFormData((prev) => ({
      ...prev,
      [name]: value,
    }));
  };

  const onImageChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const files = e.target.files;
    if (files && files.length > 0) {
      setFormData((prev) => ({
        ...prev,
        image_url: files[0],
      }));
    }
  };

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();

    // Validate required fields
    if (!formData.title.trim()) {
      toast.error("Title is required");
      return;
    }
    if (!formData.description.trim()) {
      toast.error("Description is required");
      return;
    }
    if (!formData.target_url.trim()) {
      toast.error("Target URL is required");
      return;
    }
    if (!formData.image_url && !formData.id) {
      toast.error("Image is required");
      return;
    }

    // Build FormData directly
    const formDataToSubmit = new FormData();
    formDataToSubmit.append("title", formData.title.trim());
    formDataToSubmit.append("description", formData.description.trim());
    formDataToSubmit.append("target_url", formData.target_url.trim());
    if (formData.image_url instanceof File) {
      formDataToSubmit.append("image", formData.image_url); // Use 'image' as field name
    }

    try {
      if (formData?.id) {
        await updatePopularDestination({
          id: formData.id,
          data: formDataToSubmit,
        }).unwrap();
        toast.success("Popular Destination updated successfully");
      } else {
        await createPopularDestination(formDataToSubmit).unwrap();
        toast.success("Popular Destination created successfully");
      }
      onClose();
    } catch (error: any) {
      console.error("Failed to save popular destination:", error);
      toast.error(error?.data?.message || "Failed to save popular destination");
    }
  };

  if (!isOpen) return null;

  return (
    <BaseModal
      onClose={onClose}
      title={`${formData.id ? "Edit" : "Add"} Popular Destination`}
      onSubmit={handleSubmit}
      btnCaption={formData.id ? "Update" : "Add"}
      btnLoading={isCreating || isUpdating}
    >
      <div className="max-h-[90vh] flex flex-col">
        <div className="flex-1 pr-2">
          <form className="space-y-4">
            <div>
              <label className="block text-sm font-medium text-gray-700 dark:text-gray-300 mb-1">
                Title
              </label>
              <input
                type="text"
                name="title"
                value={formData.title}
                onChange={handleChange}
                placeholder="Enter promotion title"
                className="w-full px-3 py-2 border rounded-md text-sm text-gray-700 dark:text-gray-300 dark:bg-gray-800 border-gray-300 dark:border-gray-700 focus:outline-none focus:ring-2 focus:ring-blue-500"
                required
              />
            </div>
            <div>
              <label className="block text-sm font-medium text-gray-700 dark:text-gray-300 mb-1">
                Description
              </label>
              <textarea
                name="description"
                value={formData.description}
                onChange={handleChange}
                placeholder="Enter promotion description"
                rows={4}
                className="w-full px-3 py-2 border rounded-md text-sm text-gray-700 dark:text-gray-300 dark:bg-gray-800 border-gray-300 dark:border-gray-700 focus:outline-none focus:ring-2 focus:ring-blue-500"
                required
              />
            </div>

            <div>
              <label className="block text-sm font-medium text-gray-700 dark:text-gray-300 mb-1">
                Image
              </label>
              <input
                type="file"
                name="image_url"
                onChange={onImageChange}
                className="w-full px-3 py-2 border rounded-md text-sm text-gray-700 dark:text-gray-300 dark:bg-gray-800 border-gray-300 dark:border-gray-700 focus:outline-none focus:ring-2 focus:ring-blue-500"
                required={!formData.id}
              />
              {formData.image_url && (
                <img
                  src={
                    formData.image_url instanceof File
                      ? URL.createObjectURL(formData.image_url)
                      : formData.image_url
                  }
                  alt="Preview"
                  className="w-32 h-32 mt-2 rounded-md object-cover"
                />
              )}
            </div>

            <div>
              <label className="block text-sm font-medium text-gray-700 dark:text-gray-300 mb-1">
                Target URL
              </label>
              <input
                type="url"
                name="target_url"
                value={formData.target_url}
                onChange={handleChange}
                placeholder="Enter target URL"
                className="w-full px-3 py-2 border rounded-md text-sm text-gray-700 dark:text-gray-300 dark:bg-gray-800 border-gray-300 dark:border-gray-700 focus:outline-none focus:ring-2 focus:ring-blue-500"
                required
              />
            </div>
          </form>
        </div>
      </div>
    </BaseModal>
  );
}
