import BaseButton from "@/components/ui/BaseButton";
import { BaseModal } from "@/components/ui/BaseModal";
import BaseInput from "@/components/ui/BaseInput";
import BaseSelect from "@/components/ui/BaseSelect";
import React, { useEffect, useState } from "react";
import { toast } from "sonner";
import { useCreateStaffMutation, useGetStaffByIdQuery, useUpdateStaffMutation } from "@/features/user/userApi";
import { FormValidationController } from "@/utils/formValidation";

interface ValidationError {
  field: string;
  message: string;
}

interface FormModalProps {
  setIsModalOpen: (isOpen: boolean) => void;
  editId?: string | null; // Add this prop to handle edit mode
}

interface StaffFormData {
  username: string;
  email: string;
  mobile: string;
  password: string;
  password_confirmation: string;
  role: "admin" | "employee";
  permissions: string[];
  account_status: boolean;
}

const permissionOptions = [
  { value: "booking", label: "Booking Management" },
  { value: "promotions", label: "Promotions" },
  { value: "users", label: "User Management" },
  { value: "reports", label: "Reports" },
  { value: "reviews", label: "Reviews" },
  { value: "inclusions", label: "Inclusions" },
];

export default function FormModal({ setIsModalOpen, editId }: FormModalProps) {
  const [showPassword, setShowPassword] = useState(false);
  const [showConfirmPassword, setShowConfirmPassword] = useState(false);
  const [formValidation, setFormValidation] = useState<FormValidationController | null>(null);
  const [formErrors, setFormErrors] = useState<ValidationError[]>([]);

  const [formData, setFormData] = useState<StaffFormData>({
    username: "",
    email: "",
    mobile: "",
    password: "",
    password_confirmation: "",
    role: "admin",
    permissions: [],
    account_status: true,
  });

  // Initialize form validation
  useEffect(() => {
    const requiredFields = ['username', 'email', 'mobile', 'role'];
    if (!editId) {
      requiredFields.push('password', 'password_confirmation');
    }
    setFormValidation(new FormValidationController(
      formData,
      requiredFields,
      (errors) => setFormErrors(errors)
    ));
  }, [formData, editId]);

  // Get single staff data if in edit mode
  const { data: staffData, isLoading: isLoadingStaff } = useGetStaffByIdQuery(editId || '', {
    skip: !editId, // Skip the query if not in edit mode
  });

  // Create and Update mutations
  const [createStaff, { isLoading: isLoadingCreateStaff, isSuccess: isSuccessCreateStaff, isError: isErrorCreateStaff, error: errorCreateStaff }] = useCreateStaffMutation();
  const [updateStaff, { isLoading: isLoadingUpdateStaff, isSuccess: isSuccessUpdateStaff, isError: isErrorUpdateStaff, error: errorUpdateStaff }] = useUpdateStaffMutation();

  // Populate form when staff data is loaded
  useEffect(() => {
    if (staffData) {
      console.log("staffData", staffData);
      setFormData({
        username: staffData.data.username || "",
        email: staffData.data.email || "",
        mobile: staffData.data.mobile || "",
        password: "", // Don't populate password in edit mode
        password_confirmation: "", // Don't populate password in edit mode
        role: staffData.data.role || "employee",
        permissions: staffData.data.permissions || [],
        account_status: staffData.data.account_status === "active" ? true : false,
      });
    }
  }, [staffData]);

  const handleChange = (
    e: React.ChangeEvent<HTMLInputElement | HTMLSelectElement>
  ) => {
    const { name, value, type } = e.target;
    const newFormData = {
      ...formData,
      [name]: type === 'checkbox' ? (e.target as HTMLInputElement).checked : value,
    };
    setFormData(newFormData);

    // Update form validation with new data
    if (formValidation) {
      formValidation.updateFormData(newFormData);
    }
  };

  const handlePermissionChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const { value, checked } = e.target;
    setFormData((prev) => ({
      ...prev,
      permissions: checked
        ? [...prev.permissions, value]
        : prev.permissions.filter((p) => p !== value),
    }));
  };

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();

    if (!formValidation) return;

    // Validate required fields
    if (!formValidation.validateRequired()) {
      // Show first error in toast
      const firstError = formErrors[0];
      if (firstError) {
        toast.error(firstError.message);
      }
      return;
    }

    // Only check password match if both fields are filled
    if (formData.password && formData.password !== formData.password_confirmation) {
      toast.error("Passwords do not match!");
      return;
    }

    try {
      // Prepare payload based on role
      const payload = {
        ...formData,
        // Remove permissions if role is admin
        ...(formData.role === "admin" && { permissions: undefined }),
        // Remove password fields if they're empty in edit mode
        ...(editId && !formData.password && {
          password: undefined,
          password_confirmation: undefined,
        }),
      };

      if (editId) {
        // Update staff
        await updateStaff({ id: editId, data: { ...payload, account_status: formData.account_status ? "active" : "inactive" } }).unwrap();
      } else {
        // Create staff
        await createStaff(payload).unwrap();
      }

      // Reset form and close modal
      setFormData({
        username: "",
        email: "",
        mobile: "",
        password: "",
        password_confirmation: "",
        role: "employee",
        permissions: [],
        account_status: true,
      });
      setFormErrors([]);
      setIsModalOpen(false);
    } catch (error: any) {
      if (formValidation) {
        formValidation.handleServerError(error);
      }
      toast.error(error?.data?.message || "An error occurred");
    }
  };

  useEffect(() => {
    if (isSuccessCreateStaff) {
      toast.success("Staff created successfully!");
    }
    if (isSuccessUpdateStaff) {
      toast.success("Staff updated successfully!");
    }
    if (isErrorCreateStaff && errorCreateStaff && formValidation) {
      formValidation.handleServerError(errorCreateStaff);
    }
    if (isErrorUpdateStaff && errorUpdateStaff && formValidation) {
      formValidation.handleServerError(errorUpdateStaff);
    }
  }, [isSuccessCreateStaff, isSuccessUpdateStaff, isErrorCreateStaff, isErrorUpdateStaff, formValidation]);

  return (
    <BaseModal
      onClose={() => setIsModalOpen(false)}
      title={`${editId ? "Edit Staff" : "Add Staff"}`}
      btnCaption={editId ? "Update Staff" : "Add Staff"}
      btnLoading={isLoadingCreateStaff || isLoadingUpdateStaff}
      onSubmit={(e) => handleSubmit(e as React.FormEvent)}
    >
      {isLoadingStaff ? (
        <div className="flex items-center justify-center p-4">
          <div className="animate-spin rounded-full h-8 w-8 border-b-2 border-blue-500"></div>
        </div>
      ) : (
        <form onSubmit={(e) => handleSubmit(e as React.FormEvent)} className="space-y-4">
          <div className="grid grid-cols-2 gap-4">
            <BaseInput
              label="Username"
              name="username"
              value={formData.username}
              onChange={handleChange}
              placeholder="Enter username"
              required
              disabled={!!editId}
              error={formErrors.find(e => e.field === 'username')?.message}
            />
            <BaseInput
              label="Email"
              name="email"
              type="email"
              value={formData.email}
              onChange={handleChange}
              placeholder="Enter staff email"
              required
              error={formErrors.find(e => e.field === 'email')?.message}
            />
          </div>
          <div className="grid grid-cols-2 gap-4">
            <BaseInput
              label="Phone"
              name="mobile"
              type="tel"
              value={formData.mobile}
              onChange={handleChange}
              placeholder="Enter phone number"
              required
              error={formErrors.find(e => e.field === 'mobile')?.message}
            />
            <BaseSelect
              label="Role"
              name="role"
              value={formData.role}
              onChange={handleChange}
              options={[
                { value: "admin", label: "Admin" },
                { value: "employee", label: "Employee" },
              ]}
              required
              error={formErrors.find(e => e.field === 'role')?.message}
            />
          </div>

          {!editId && (
            <div className="grid grid-cols-2 gap-4">
              <BaseInput
                label="Password"
                name="password"
                type={showPassword ? "text" : "password"}
                value={formData.password}
                onChange={handleChange}
                placeholder="Enter password"
                required
                showPassword={showPassword}
                onTogglePassword={() => setShowPassword((prev) => !prev)}
                error={formErrors.find(e => e.field === 'password')?.message}
              />
              <BaseInput
                label="Confirm Password"
                name="password_confirmation"
                value={formData.password_confirmation}
                type={showConfirmPassword ? "text" : "password"}
                onChange={handleChange}
                placeholder="Confirm password"
                required
                showPassword={showConfirmPassword}
                onTogglePassword={() => setShowConfirmPassword((prev) => !prev)}
                error={formErrors.find(e => e.field === 'password_confirmation')?.message}
              />
            </div>
          )}

          {formData.role === "employee" && (
            <div>
              <h3 className="text-sm font-medium text-gray-700 dark:text-gray-300 mb-3">
                Permissions
              </h3>
              <div className="grid grid-cols-2 gap-3">
                {permissionOptions.map((permission) => (
                  <label
                    key={permission.value}
                    className="flex items-center space-x-2 p-2 rounded border dark:border-gray-700 hover:bg-gray-50 dark:hover:bg-gray-700 cursor-pointer"
                  >
                    <input
                      type="checkbox"
                      value={permission.value}
                      checked={formData.permissions.includes(permission.value)}
                      onChange={handlePermissionChange}
                      className="rounded border-gray-300 text-blue-600 focus:ring-blue-500"
                    />
                    <span className="text-sm">{permission.label}</span>
                  </label>
                ))}
              </div>
            </div>
          )}

          {editId && (
            <div className="mt-4 p-4 bg-gray-50 dark:bg-gray-800 rounded-lg">
              <div className="flex items-center justify-between">
                <div>
                  <h3 className="text-sm font-medium text-gray-700 dark:text-gray-300">
                    Account Status
                  </h3>
                  <p className="text-xs text-gray-500 dark:text-gray-400 mt-1">
                    {formData.account_status
                      ? "This account is currently active"
                      : "This account is currently inactive"}
                  </p>
                </div>
                <label className="relative inline-flex items-center cursor-pointer">
                  <input
                    type="checkbox"
                    name="account_status"
                    checked={formData.account_status}
                    onChange={handleChange}
                    className="sr-only peer"
                  />
                  <div className="w-11 h-6 bg-gray-200 peer-focus:outline-none peer-focus:ring-4 peer-focus:ring-blue-300 dark:peer-focus:ring-blue-800 rounded-full peer dark:bg-gray-700 peer-checked:after:translate-x-full peer-checked:after:border-white after:content-[''] after:absolute after:top-[2px] after:left-[2px] after:bg-white after:border-gray-300 after:border after:rounded-full after:h-5 after:w-5 after:transition-all dark:border-gray-600 peer-checked:bg-blue-600"></div>
                  <span className="ml-3 text-sm font-medium text-gray-700 dark:text-gray-300">
                    {formData.account_status ? "Active" : "Inactive"}
                  </span>
                </label>
              </div>
            </div>
          )}
        </form>
      )}
    </BaseModal>
  );
}
