import apiClient from "./apiClient";
import apiRoutes from "./apiRoutes";

export const customService = {
  createHappyCustomer: async (data: any) => {
    const response = await apiClient.post(apiRoutes.happyCustomer.create, data, {
      headers: {
        "Content-Type": "multipart/form-data",
      },
    });
    return response.data;
  },

  updateCustomerInfo: async(id, data) => {
    const response = await apiClient.post(apiRoutes.user.updateCustomerInfo(id), data, {
      headers: {
        "Content-Type": "multipart/form-data",
      },
    })
    return response.data
  }
};

