import React, { useEffect, useState } from "react";
import BaseButton from "@/components/ui/BaseButton";
import BaseTable from "@/components/table/BaseTable";
import { Edit, Eye, Plus, Trash } from "lucide-react";
import {
  useGetPromotionsQuery,
  useDeletePromotionMutation,
} from "@/features/promotion/promotionApi";
import FormModal from "./modal/FormModal";
import DeleteModal from "@/components/ui/DeleteModal";
import ViewModal from "./modal/ViewModal";
import { useSelector } from "react-redux";
import { RootState } from "@/store";
import { toast } from "sonner";

interface Promotion {
  id: number;
  title: string;
  subtitle: string;
  description: string;
  target_url: string;
  order: string;
  is_active: boolean;
  start_at: string;
  end_at: string;
  image_url: string;
}

export default function Promotions() {
  const [isModalOpen, setIsModalOpen] = useState(false);
  const [isDeleteModalOpen, setIsDeleteModalOpen] = useState(false);
  const [isViewModalOpen, setIsViewModalOpen] = useState(false);
  const [selectedPromotion, setSelectedPromotion] = useState<Promotion | null>(null);
  const [editData, setEditData] = useState<Promotion | null>(null);

  const { search, page, start_date, end_date } = useSelector((state: RootState) => state.tableFilter);

  const {
    data: promotionsData,
    isLoading,
    isFetching,
    error,
    refetch,
  } = useGetPromotionsQuery({
    page,
    search,
    start_date,
    end_date,
  });

  const [deletePromotion, { isLoading: isDeleting }] = useDeletePromotionMutation();


  const handleDelete = async () => {
    if (!selectedPromotion) return;
    try {
      await deletePromotion(selectedPromotion.id).unwrap();
      refetch();
      setIsDeleteModalOpen(false);
      setSelectedPromotion(null);
      toast.success("Promotion deleted successfully");
    } catch (error) {
      console.error("Failed to delete promotion:", error);
      toast.error("Failed to delete promotion");
    }
  };

  const columns = [
    {
      title: "Action",
      key: "actions",
      width: "70px",
      dropdownItems: [
        {
          icon: <Eye className="w-5 h-5" />,
          label: "View",
          onClick: (row: Promotion) => {
            setSelectedPromotion(row);
            setIsViewModalOpen(true);
          },
          show: true,
        },
        {
          icon: <Edit className="w-5 h-5" />,
          label: "Edit",
          onClick: (row: Promotion) => {
            setEditData(row);
            setIsModalOpen(true);
          },
          show: true,
        },
        {
          icon: <Trash className="w-5 h-5" />,
          label: "Delete",
          onClick: (row: Promotion) => {
            setSelectedPromotion(row);
            setIsDeleteModalOpen(true);
          },
          className: "text-red-500 hover:text-red-600",
          show: true,
        },
      ],
    },
    {
      title: "Sl No",
      key: "index",
      sort: false,
      width: "70px",
      className: "text-center",
    },
    {
      title: "Banner Image",
      key: "image_url",
      sort: false,
      width: "180px",
      render: (value: string) => (
        <div className="w-full h-12 flex items-center justify-center">
          <img
            src={value}
            alt="Banner"
            className="w-full h-full object-contain"
          />
        </div>
      ),
    },
    {
      title: "Title",
      key: "title",
      sort: true,
      width: "180px",
    },
    {
      title: "Start Date",
      key: "start_at",
      sort: true,
      width: "180px",
      render: (value: string) => new Date(value).toLocaleDateString(),
    },
    {
      title: "End Date",
      key: "end_at",
      sort: true,
      width: "180px",
      render: (value: string) => new Date(value).toLocaleDateString(),
    },
    {
      title: "Status",
      key: "is_active",
      render: (value: boolean) => (
        <span
          className={`px-2 py-1 rounded text-sm font-medium ${value ? "bg-blue-100 text-blue-600" : "bg-red-100 text-red-600"
            }`}
        >
          {value ? "Active" : "Inactive"}
        </span>
      ),
      sort: true,
      width: "100px",
    },
  ];

  return (
    <>
      <BaseTable
        title="All Promotions"
        columns={columns}
        data={promotionsData?.items || []}
        searchPlaceholder="Search promotions..."
        loading={isLoading || isFetching}
        limit={promotionsData?.per_page || 100}
        lastPage={promotionsData?.last_page || 1}
        total={promotionsData?.total || 0}
        showSearch={true}
        error={error}
        refetch={refetch}
        actionButton={
          <BaseButton
            onClick={() => {
              setEditData(null);
              setIsModalOpen(true);
            }}
            icon={<Plus className="w-5 h-5" />}
          >
            Add
          </BaseButton>
        }
      />

      <FormModal
        isOpen={isModalOpen}
        onClose={() => setIsModalOpen(false)}
        editData={editData}
        afterSubmit={refetch}
      />

      {isDeleteModalOpen && (
        <DeleteModal
          isOpen={isDeleteModalOpen}
          onClose={() => {
            setIsDeleteModalOpen(false);
            setSelectedPromotion(null);
          }}
          onConfirm={handleDelete}
          isLoading={isDeleting}
          title="Delete Promotion"
          message={`Are you sure you want to delete ${selectedPromotion?.title}?`}
        />
      )}

      <ViewModal
        isOpen={isViewModalOpen}
        onClose={() => {
          setIsViewModalOpen(false);
          setSelectedPromotion(null);
        }}
        promotion={selectedPromotion}
      />
    </>
  );
}
