import React from "react";
import { BaseModal } from "@/components/ui/BaseModal";
import { Calendar, ExternalLink, Globe, Image, Info, Clock, Tag } from "lucide-react";
import { format } from "date-fns";

interface ViewModalProps {
    isOpen: boolean;
    onClose: () => void;
    promotion?: {
        id: number;
        title: string;
        subtitle: string;
        description: string;
        image_url: string;
        target_url: string;
        order: string;
        is_active: boolean;
        start_at: string;
        end_at: string;
        formatted_start_at: string;
        formatted_end_at: string;
    };
}

export default function ViewModal({ isOpen, onClose, promotion }: ViewModalProps) {
    if (!isOpen) return null;

    console.log("promotion", promotion)

    if (!promotion) {
        return (
            <BaseModal onClose={onClose} width="max-w-xl" isSubmit={false} title="Promotion Details">
                <div className="flex items-center justify-center py-8">
                    <p className="text-gray-500 dark:text-gray-400">No promotion data available</p>
                </div>
            </BaseModal>
        );
    }

    return (
        <BaseModal onClose={onClose} width="max-w-xl" isSubmit={false} title="Promotion Details">
            <div className="flex flex-col">
                <div className="space-y-6">
                    {/* Header Section with Image */}
                    <div className="relative">
                        <div className="aspect-video w-full overflow-hidden rounded-lg">
                            <img
                                src={promotion.image_url}
                                alt={promotion.title}
                                className="w-full h-full object-cover"
                            />
                        </div>
                        <div className="absolute bottom-0 left-0 right-0 bg-gradient-to-t from-black/70 to-transparent p-4">
                            <h3 className="text-xl font-bold text-white mb-1">
                                {promotion.title}
                            </h3>
                            <p className="text-sm text-gray-200">
                                {promotion.subtitle}
                            </p>
                        </div>
                    </div>

                    {/* Status and Order Badges */}
                    <div className="flex gap-3">
                        <span className={`px-3 py-1 rounded-full text-sm font-medium flex items-center gap-1.5
                            ${promotion.is_active
                                ? 'bg-green-100 text-green-800 dark:bg-green-900/30 dark:text-green-400'
                                : 'bg-red-100 text-red-800 dark:bg-red-900/30 dark:text-red-400'
                            }`}>
                            <span className={`w-2 h-2 rounded-full ${promotion.is_active ? 'bg-green-500' : 'bg-red-500'}`}></span>
                            {promotion.is_active ? 'Active' : 'Inactive'}
                        </span>
                        <span className="px-3 py-1 rounded-full bg-blue-100 text-blue-800 dark:bg-blue-900/30 dark:text-blue-400 text-sm font-medium flex items-center gap-1.5">
                            <Tag className="w-4 h-4" />
                            Order: {promotion.order}
                        </span>
                    </div>

                    {/* Main Content Grid */}
                    <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
                        {/* Left Column */}
                        <div className="space-y-6">
                            {/* Description Card */}
                            <div className="bg-white dark:bg-gray-800 rounded-lg border border-gray-200 dark:border-gray-700 p-4">
                                <h4 className="text-sm font-medium text-gray-500 dark:text-gray-400 mb-3 flex items-center gap-2">
                                    <Info className="w-4 h-4" />
                                    Description
                                </h4>
                                <p className="text-sm text-gray-600 dark:text-gray-300">
                                    {promotion.description}
                                </p>
                            </div>

                            {/* Target URL Card */}
                            <div className="bg-white dark:bg-gray-800 rounded-lg border border-gray-200 dark:border-gray-700 p-4">
                                <h4 className="text-sm font-medium text-gray-500 dark:text-gray-400 mb-3 flex items-center gap-2">
                                    <Globe className="w-4 h-4" />
                                    Target URL
                                </h4>
                                <a
                                    href={promotion.target_url}
                                    target="_blank"
                                    rel="noopener noreferrer"
                                    className="text-sm text-blue-600 dark:text-blue-400 hover:underline flex items-center gap-1.5"
                                >
                                    {promotion.target_url}
                                    <ExternalLink className="w-4 h-4" />
                                </a>
                            </div>
                        </div>

                        {/* Right Column */}
                        <div className="space-y-6">
                            {/* Duration Card */}
                            <div className="bg-white dark:bg-gray-800 rounded-lg border border-gray-200 dark:border-gray-700 p-4">
                                <h4 className="text-sm font-medium text-gray-500 dark:text-gray-400 mb-3 flex items-center gap-2">
                                    <Calendar className="w-4 h-4" />
                                    Duration
                                </h4>
                                <div className="space-y-3">
                                    <div className="flex items-start gap-3">
                                        <Clock className="w-4 h-4 text-gray-400 mt-0.5" />
                                        <div>
                                            <div className="text-sm text-gray-500 dark:text-gray-400">Start Date</div>
                                            <div className="text-sm font-medium text-gray-900 dark:text-white">
                                                {promotion.formatted_start_at}
                                            </div>
                                        </div>
                                    </div>
                                    <div className="flex items-start gap-3">
                                        <Clock className="w-4 h-4 text-gray-400 mt-0.5" />
                                        <div>
                                            <div className="text-sm text-gray-500 dark:text-gray-400">End Date</div>
                                            <div className="text-sm font-medium text-gray-900 dark:text-white">
                                                {promotion.formatted_end_at}
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </BaseModal>
    );
}
