import BaseTable from "@/components/table/BaseTable";
import BaseButton from "@/components/ui/BaseButton";
import { Check, CheckCheck, Edit, Eye, Plus, Upload, ListFilter, MailCheck } from "lucide-react";
import React, { useEffect, useState } from "react";
import { NavLink, useNavigate, useParams } from "react-router-dom";
import { useGetBookingsQuery, useMarkResendEmailMutation, useUpdateBookingStatusMutation } from "@/features/booking/bookingApi";
import ImportModal from "./modal/ImportModal";
import ViewModal from "./modal/ViewModal";
import { useSelector } from "react-redux";
import { toast } from "sonner";
import { constData } from "@/api/constData";

export default function Bookings() {
  const [isImportModalOpen, setIsImportModalOpen] = useState(false);
  const [viewModalOpen, setViewModalOpen] = useState(false);
  const [selectedBookingId, setSelectedBookingId] = useState<
    string | number | null
  >(null);
  const [statusFilter, setStatusFilter] = useState<string | null>(null);
  const [isStatusDropdownOpen, setIsStatusDropdownOpen] = useState(false);

  const { search, page, start_date, end_date } = useSelector(
    (state: any) => state.tableFilter
  );

  const [updateBookingStatus, { isLoading: isUpdating, isSuccess: isSuccessUpdate, isError: isErrorUpdate }] = useUpdateBookingStatusMutation();

  const navigate = useNavigate();

  // RTK Query hooks
  const {
    data: bookingsData,
    isLoading,
    isFetching,
    error,
    refetch,
  } = useGetBookingsQuery({
    page,
    search,
    per_page: 20,
    start_date,
    end_date,
    status: statusFilter,
  });

  const [resendEmail,
    {
      isLoading: isRendEmailLoading,
      isSuccess: isResendEmailSuccess,
      error: resendEmailError,
      isError: isResendEmailError
    }
  ] = useMarkResendEmailMutation()

  const handleChangeStatus = (id: string) => {
    updateBookingStatus(id);
  }

  useEffect(() => {
    if (isSuccessUpdate) {
      toast.success("Booking status updated successfully");
    }
    if (isErrorUpdate) {
      toast.error("Failed to update booking status");
    }
  }, [isSuccessUpdate, isErrorUpdate]);


  const columns = [
    {
      title: "Action",
      key: "actions",
      sort: false,
      width: "70px",
      className: "text-center",
      dropdownItems: [
        {
          icon: <Eye className="w-5 h-5" />,
          label: "View",
          onClick: (row) => {
            setSelectedBookingId(row.id);
            setViewModalOpen(true);
          },
          show: true,
        },
        {
          icon: <Edit className="w-5 h-5" />,
          label: "Edit",
          onClick: (row: object) => {
            navigate(`/bookings/edit-booking/${row.id}`);
          },
          show: (row: object) => {
            return row.status !== constData.status.completed;
          },
        },
        {
          icon: <CheckCheck className="w-5 h-5" />,
          label: "Mark as Completed",
          onClick: (row: object) => { handleChangeStatus(row.id) },
          show: (row: object) => {
            return row.status !== constData.status.completed;
          },
          isLoading: isUpdating,
        },
        {
          icon: <MailCheck className="w-5 h-5" />,
          label: "Resend Email",
          onClick: (row: object) => { handleResendEmail(row.id) },
          show: true,
          isLoading: isRendEmailLoading,
        },
      ],
    },
    {
      title: "Sl No",
      key: "index",
      sort: false,
      width: "70px",
      className: "text-center",
    },
    {
      title: "Job Ref",
      key: "job_reference",
      sort: false,
      width: "180px",
    },
    {
      title: "Date",
      key: "booking_datetime",
      sort: true,
      width: "120px",
      render: (value: string) => value.split(" ").slice(0, 3).join(" "),
    },
    {
      title: "Day",
      key: "booking_day_name",
      sort: true,
      width: "120px",
    },
    {
      title: "Time",
      key: "pickup_time",
      sort: true,
      width: "100px",
      render: (value: string) =>
        new Date(value).toLocaleTimeString([], {
          hour: "2-digit",
          minute: "2-digit",
          hour12: false,
        }),
    },
    {
      title: "Traveler",
      key: "lead_traveler",
      sort: true,
      width: "150px",
    },
    {
      title: "No of Travelers",
      key: "traveler_count",
      sort: true,
      width: "150px",
      className: "text-center",
    },
    {
      title: "Pickup",
      key: "pickup_location",
      sort: true,
      width: "150px",
    },
    {
      title: "Tour Destination",
      key: "description",
      sort: true,
      width: "250px",
    },
    {
      title: "Tour Duration",
      key: "tour_duration",
      sort: true,
      width: "150px",
      className: "text-center",
    },
    {
      title: "Status",
      key: "status",
      render: (value: string) => (
        <span
          className={`px-2 py-1 rounded text-sm font-medium ${value === constData.status.confirmed
            ? "bg-blue-100 text-blue-600"
            : value === constData.status.cpending
              ? "bg-yellow-100 text-yellow-600"
              : value === constData.status.completed
                ? "bg-green-100 text-green-600"
                : "bg-red-100 text-red-600"
            }`}
        >
          {value.charAt(0).toUpperCase() + value.slice(1)}
        </span>
      ),
      sort: true,
    },
    {
      title: "Reference",
      key: "booking_reference",
      sort: true,
      width: "150px",
    },
    {
      title: "Drop Off Date",
      key: "drop_off_time",
      sort: true,
      width: "140px",
      render: (value: string) => value.split(" ").slice(0, 3).join(" "),
    },
    {
      title: "Drop Off Time",
      key: "drop_off_time",
      sort: true,
      width: "150px",
      render: (value: string) =>
        new Date(value).toLocaleTimeString([], {
          hour: "2-digit",
          minute: "2-digit",
          hour12: false,
        }),
    },
    // {
    // title: "Seller Info",
    // key: "seller_info",
    // sort: true,
    // width: "180px",
    // },
    {
      title: "Booking Source",
      key: "booking_source",
      sort: true,
      width: "150px",
      render: (value: string) => value?.name
    },
    {
      title: "Created By",
      key: "created_by",
      sort: true,
      width: "150px",
    },
    {
      title: "Created At",
      key: "created_at",
      sort: true,
      width: "180px",
    },
  ];

  const handleFileUpload = (file: File) => {
    // Here you can implement the actual file upload logic
    console.log("File to upload:", file);
    // You might want to call an API to upload the fil
    // uploadBookingCSV(file);
  };

  const handleResendEmail = async (id: string) => {
    await resendEmail(id)
  }

  const filterItems = [
    { label: "All Status", value: "" },
    { label: "Completed", value: "completed" },
    { label: "Confirmed", value: "confirmed" },
    { label: "Pending", value: "pending" },
    { label: "Cancelled", value: "cancelled" }
  ]

  useEffect(() => {
    console.log("bookingsData", bookingsData);
  }, [bookingsData]);

  useEffect(() => {
    function handleClickOutside(event) {
      if (!event.target.closest('.status-dropdown')) {
        setIsStatusDropdownOpen(false);
      }
    }
    if (isStatusDropdownOpen) {
      document.addEventListener('mousedown', handleClickOutside);
    }
    return () => {
      document.removeEventListener('mousedown', handleClickOutside);
    };
  }, [isStatusDropdownOpen]);

  useEffect(() => {
    if (isResendEmailSuccess) {
      toast.success("Email Sent Successfully")
    }
    if (isResendEmailError) {
      toast.error(resendEmailError?.data || "Faild to Resend Email")
    }
  }, [isResendEmailSuccess, isResendEmailError])

  return (
    <>
      <BaseTable
        title="All Bookings"
        columns={columns}
        data={bookingsData?.data || []}
        searchPlaceholder="Search bookings..."
        showSearch={true}
        loading={isLoading || isFetching}
        limit={bookingsData?.per_page || 100}
        lastPage={bookingsData?.last_page || 1}
        error={error}
        total={bookingsData?.total || 0}
        refetch={refetch}
        dropdownItemWidth="w-44"
        actionButton={
          <div className="flex items-center gap-2">
            <BaseButton
              icon={<Upload className="w-5 h-5" />}
              variant="outline"
              onClick={() => setIsImportModalOpen(true)}
            />
            <div className="relative status-dropdown">
              <BaseButton
                icon={<ListFilter className="w-5 h-5" />}
                variant="outline"
                onClick={() => setIsStatusDropdownOpen((open) => !open)}
              />
              {isStatusDropdownOpen && (
                <div className="absolute right-0 mt-2 w-40 bg-white border rounded shadow-lg z-50">
                  {filterItems.map((status) => (
                    <button
                      key={status.value}
                      className={`flex items-center w-full px-4 py-2 text-left hover:bg-blue-50 ${(statusFilter || "") === status.value ? "font-bold text-blue-600" : ""
                        }`}
                      onClick={() => {
                        setStatusFilter(status.value || null);
                        setIsStatusDropdownOpen(false);
                      }}
                    >
                      {(statusFilter || "") === status.value && (
                        <Check className="w-4 h-4 mr-2 text-blue-600" />
                      )}
                      {status.label}
                    </button>
                  ))}
                </div>
              )}
            </div>
            <NavLink to="/bookings/add-booking">
              <BaseButton icon={<Plus className="w-5 h-5" />}>Add</BaseButton>
            </NavLink>
          </div>
        }
      />

      <ImportModal
        isOpen={isImportModalOpen}
        onClose={() => setIsImportModalOpen(false)}
        onUpload={handleFileUpload}
      />

      <ViewModal
        isOpen={viewModalOpen}
        onClose={() => {
          setViewModalOpen(false);
          setSelectedBookingId(null);
        }}
        bookingId={selectedBookingId}
      />
    </>
  );
}
