import React, { useEffect, useMemo, useRef, useState } from "react";
import TableSkeleton from "./TableSkeleton";
import { EllipsisVertical, AlertCircle, Loader2 } from "lucide-react";
import { useApiError } from "@/hooks/useApiError";
import BaseError from "../ui/BaseError";
import { useDispatch } from "react-redux";
import { setPage, setSearch } from "@/features/tableFilter/tableFileterSlice";
import Tooltip from "@/components/ui/Tooltip";

interface DropdownItem {
  icon?: React.ReactNode;
  label: string;
  onClick: (row: any) => void;
  className?: string;
  show?: (row: any) => boolean;
  isLoading?: boolean;
}

interface Column {
  title: string;
  key: string;
  render?: (value: any, row: any) => React.ReactNode;
  sort?: boolean;
  width?: string; // e.g. "120px", "20%", "8rem"
  className?: string;
  isActionMenu?: boolean;
  dropdownItems?: DropdownItem[];
}

interface TableProps {
  columns: Column[];
  data: any[];
  title?: string;
  searchPlaceholder?: string;
  showSearch?: boolean;
  actionButton?: React.ReactNode;
  containerClassName?: string; // <-- Add this line
  loading?: boolean;
  limit?: number;
  lastPage?: number;
  error?: any;
  refetch?: () => void;
  total?: number;
  dropdownItemWidth?: string; // e.g. "160px", "200px"
}

export default function BaseTable({
  columns,
  data,
  title,
  searchPlaceholder = "Search...",
  showSearch = true,
  actionButton,
  containerClassName = "h-[calc(100vh_-_100px)]",
  loading = false,
  limit = 100,
  lastPage = 1,
  error,
  refetch,
  total = 0,
  dropdownItemWidth = "160px", // Default width for dropdown items
}: TableProps) {
  const [searchInput, setSearchInput] = useState("");
  const [sortConfig, setSortConfig] = useState<{
    key: string;
    direction: "asc" | "desc";
  } | null>(null);
  const [currentPage, setCurrentPage] = useState(1);
  const [isRefreshing, setIsRefreshing] = useState(false);
  const [itemsPerPage, setItemsPerPage] = useState(100);

  const dispatch = useDispatch();

  // Debounce effect for search
  useEffect(() => {
    const handler = setTimeout(() => {
      dispatch(setSearch(searchInput));
    }, 500);

    return () => clearTimeout(handler);
  }, [searchInput, dispatch]);

  // Update current page in Redux when it changes
  useEffect(() => {
    dispatch(setPage(currentPage));
  }, [currentPage, dispatch]);

  const handleSearch = (e: React.ChangeEvent<HTMLInputElement>) => {
    setSearchInput(e.target.value);
  };

  const handleSort = (key: string) => {
    setSortConfig((prevConfig) => {
      if (prevConfig && prevConfig.key === key) {
        return {
          key,
          direction: prevConfig.direction === "asc" ? "desc" : "asc",
        };
      }
      return { key, direction: "asc" };
    });
  };

  const sortedData = useMemo(() => {
    if (!Array.isArray(data)) return [];
    if (!sortConfig) return data;
    return [...data].sort((a, b) => {
      if (a[sortConfig.key] < b[sortConfig.key])
        return sortConfig.direction === "asc" ? -1 : 1;
      if (a[sortConfig.key] > b[sortConfig.key])
        return sortConfig.direction === "asc" ? 1 : -1;
      return 0;
    });
  }, [data, sortConfig]);

  useEffect(() => {
    setItemsPerPage(limit);
  }, [limit]);

  const startIndex = (currentPage - 1) * itemsPerPage;

  const [activeDropdownRow, setActiveDropdownRow] = useState<number | null>(
    null
  );

  // Create a refs object to store refs for each row
  const dropdownRefs = useRef<{ [key: number]: HTMLDivElement | null }>({});

  const toggleDropdown = (rowIndex: number) => {
    setActiveDropdownRow(activeDropdownRow === rowIndex ? null : rowIndex);
  };

  useEffect(() => {
    const handleClickOutside = (event: MouseEvent) => {
      // Check if click is outside all dropdowns
      const isClickOutside = Object.values(dropdownRefs.current).every(
        (ref) => ref && !ref.contains(event.target as Node)
      );

      if (isClickOutside) {
        setActiveDropdownRow(null);
      }
    };

    document.addEventListener("mousedown", handleClickOutside);
    return () => {
      document.removeEventListener("mousedown", handleClickOutside);
    };
  }, []);

  const errorDetails = useApiError(error);

  // Error component
  const ErrorDisplay = () => {
    if (!error) return null;

    return (
      <tr>
        <td colSpan={columns.length} className="p-4">
          <BaseError
            title={errorDetails?.title || "Error Loading Data"}
            message={
              errorDetails?.message ||
              "An error occurred while loading the data. Please try again."
            }
            onRetry={refetch}
            className="w-full"
          />
        </td>
      </tr>
    );
  };

  // Loading component
  const LoadingDisplay = () => {
    if (!loading) return null;

    return (
      <tr>
        <td colSpan={columns.length}>
          <TableSkeleton columns={columns.length} rows={10} />
        </td>
      </tr>
    );
  };

  // Empty state component
  const EmptyDisplay = () => {
    if (loading || error) return null;

    return (
      <tr className="h-80">
        <td colSpan={columns.length} className="p-8 text-center">
          <div className="flex flex-col items-center justify-center">
            <AlertCircle className="w-12 h-12 text-gray-400 mb-4" />
            <h3 className="text-lg font-medium text-gray-900 dark:text-gray-100 mb-2">
              No Data Available
            </h3>
            <p className="text-sm text-gray-500 dark:text-gray-400">
              There are no records to display at this time.
            </p>
          </div>
        </td>
      </tr>
    );
  };

  const handleRefresh = async () => {
    if (!refetch) return;
    setIsRefreshing(true);
    try {
      await refetch();
    } finally {
      setIsRefreshing(false);
    }
  };

  return (
    <div
      className={`bg-white border-[1.5px] border-gray-200 dark:border-gray-700 dark:bg-gray-900 rounded-lg p-4 flex flex-col ${containerClassName}`}
    >
      {/* Table Header */}
      <div className="flex flex-col sm:flex-row sm:justify-between sm:items-center mb-4 gap-3">
        {title && (
          <h2 className="text-lg font-semibold text-gray-800 dark:text-white">
            {title}
          </h2>
        )}
        <div className="flex flex-col sm:flex-row sm:items-center gap-2">
          {showSearch && (
            <input
              type="text"
              placeholder={searchPlaceholder}
              value={searchInput}
              onChange={handleSearch}
              className="px-3 py-2 border rounded-md text-sm text-gray-700 dark:text-gray-300 dark:bg-gray-800 border-gray-300 dark:border-gray-700 focus:outline-none focus:ring-2 focus:ring-blue-500"
            />
          )}
          <div className="flex items-center gap-2">
            {refetch && (
              <button
                onClick={handleRefresh}
                disabled={isRefreshing}
                className="p-2 cursor-pointer rounded-md border border-gray-300 dark:border-gray-700 text-gray-600 dark:text-gray-300 hover:bg-gray-100 dark:hover:bg-gray-800 focus:outline-none focus:ring-2 focus:ring-blue-500 disabled:opacity-50 disabled:cursor-not-allowed"
                title="Refresh data"
              >
                <svg
                  xmlns="http://www.w3.org/2000/svg"
                  width="20"
                  height="20"
                  viewBox="0 0 24 24"
                  fill="none"
                  stroke="currentColor"
                  strokeWidth="2"
                  strokeLinecap="round"
                  strokeLinejoin="round"
                  className={`${isRefreshing ? "animate-spin" : ""}`}
                >
                  <path d="M21 12a9 9 0 0 0-9-9 9.75 9.75 0 0 0-6.74 2.74L3 8" />
                  <path d="M3 3v5h5" />
                  <path d="M3 12a9 9 0 0 0 9 9 9.75 9.75 0 0 0 6.74-2.74L21 16" />
                  <path d="M16 21h5v-5" />
                </svg>
              </button>
            )}
            {actionButton}
          </div>
        </div>
      </div>

      {/* Table Content */}
      <div className="w-full" style={{ overflowX: "auto" }}>
        <div className="min-w-full inline-block align-middle">
          <table className="w-full border-collapse border border-gray-200 dark:border-gray-700" style={{ tableLayout: "fixed", width: "100%" }}>
            <thead>
              <tr className="bg-[#e8f5fe] dark:bg-gray-800">
                {columns?.map((column, idx) => (
                  <th
                    key={column?.key}
                    style={{ width: column?.width || "120px" }}
                    className={`px-4 py-2 text-left text-sm font-medium text-gray-700 dark:text-gray-300 border-b border-gray-200 dark:border-gray-700 cursor-pointer
                    ${idx !== columns.length - 1
                        ? "border-r border-gray-300 dark:border-gray-700"
                        : ""
                      }`}
                    onClick={() => column?.sort && handleSort(column?.key)}
                  >
                    <div className="flex items-center gap-1">
                      {column.title}
                      {column?.sort && (
                        <span>
                          {sortConfig?.key === column.key
                            ? sortConfig.direction === "asc"
                              ? "↑↑"
                              : "↓↓"
                            : "⇅"}
                        </span>
                      )}
                    </div>
                  </th>
                ))}
              </tr>
            </thead>
            <tbody>
              {error ? (
                <ErrorDisplay />
              ) : loading ? (
                <LoadingDisplay />
              ) : !Array.isArray(sortedData) || sortedData.length === 0 ? (
                <EmptyDisplay />
              ) : (
                sortedData.map((row, rowIndex) => (
                  <tr
                    key={rowIndex}
                    className="hover:bg-gray-50 dark:hover:bg-gray-800"
                  >
                    {columns.map((column, idx) => (
                      <td
                        key={column.key}
                        style={{
                          width: column?.width || "120px",
                          overflow: "visible",
                        }} // <-- add overflow: visible
                        className={`text-wrap z-10 px-4 py-2 text-sm text-gray-700 dark:text-gray-300 border-b border-gray-200 dark:border-gray-700
                        ${idx !== columns.length - 1
                            ? "border-r border-gray-200 dark:border-gray-700"
                            : ""
                          } ${column.className || ""} truncate
                          ${column.key === "actions"
                            ? "relative overflow-visible"
                            : ""
                          } // <-- add this
                        `}
                      >
                        {column.key === "index" ? (
                          startIndex + rowIndex + 1
                        ) : column.key === "actions" ? (
                          <div
                            className="relative"
                            ref={(el) => {
                              dropdownRefs.current[rowIndex] = el;
                            }}
                          >
                            <button
                              className="w-7 h-7 flex items-center justify-center rounded-full border border-gray-300 dark:border-gray-700 text-gray-600 dark:text-gray-300 cursor-pointer relative"
                              onClick={(e) => {
                                e.stopPropagation();
                                toggleDropdown(rowIndex);
                              }}
                            >
                              <EllipsisVertical className="w-4 h-4" />
                            </button>
                            {activeDropdownRow === rowIndex &&
                              column?.dropdownItems && (
                                <div
                                  className={`absolute left-full top-0 ml-2 bg-white dark:bg-gray-800 border rounded-lg shadow-lg z-50 ${dropdownItemWidth}`}
                                  style={{ minWidth: 160, overflow: "visible" }} // <-- ensure overflow visible
                                  onClick={(e) => e.stopPropagation()}
                                >
                                  <ul className="space-y-1">
                                    {column.dropdownItems
                                      .filter((item) => {
                                        if (typeof item.show === 'function') {
                                          return item.show(row);
                                        }
                                        return item.show !== false;
                                      })
                                      .map((item, index) => (
                                        <li
                                          key={index}
                                          className={`flex items-center gap-3 p-2 hover:bg-gray-100 dark:hover:bg-gray-700 cursor-pointer ${item.className || ""
                                            }`}
                                          onClick={(e) => {
                                            e.stopPropagation();
                                            item.onClick(row);
                                            setActiveDropdownRow(null);
                                          }}
                                        >
                                          {item.icon && (
                                            <>
                                              {item?.isLoading ? (
                                                <span className="text-gray-700 dark:text-gray-300">
                                                  <Loader2 className="w-4 h-4 animate-spin" />
                                                </span>
                                              ) : (
                                                <span className="text-gray-700 dark:text-gray-300">
                                                  {item.icon}
                                                </span>
                                              )}
                                            </>
                                          )}
                                          <span className="text-sm text-gray-700 dark:text-gray-300">
                                            {item.label}
                                          </span>
                                        </li>
                                      ))}
                                  </ul>
                                </div>
                              )}
                          </div>
                        ) : column.render ? (
                          column.render(row[column.key], row)
                        ) : (
                          typeof row[column.key] === 'object' && row[column.key] !== null
                            ? JSON.stringify(row[column.key])
                            : row[column.key]
                        )}
                      </td>
                    ))}
                  </tr>
                ))
              )}
            </tbody>
          </table>
        </div>
      </div>

      {/* Pagination */}
      {!error && !loading && sortedData?.length > 0 && lastPage > 1 && (
        <div className="flex justify-between items-center mt-4">
          <div className="text-sm text-gray-600 dark:text-gray-400">
            Showing {sortedData?.length} out of {total}
          </div>
          <div className="flex items-center gap-2">
            <button
              onClick={() => setCurrentPage((prev) => Math.max(prev - 1, 1))}
              disabled={currentPage === 1}
              className="px-3 py-1 text-sm cursor-pointer bg-gray-200 dark:bg-gray-700 text-gray-600 dark:text-gray-300 rounded disabled:opacity-50"
            >
              Previous
            </button>
            {Array.from({ length: lastPage }, (_, i) => i + 1).map((page) => (
              <button
                key={page}
                onClick={() => setCurrentPage(page)}
                className={`px-3 py-1 text-sm rounded cursor-pointer ${currentPage === page
                  ? "bg-blue-500 text-white"
                  : "bg-gray-200 dark:bg-gray-700 text-gray-600 dark:text-gray-300"
                  }`}
              >
                {page}
              </button>
            ))}
            <button
              onClick={() =>
                setCurrentPage((prev) => Math.min(prev + 1, lastPage))
              }
              disabled={currentPage === lastPage}
              className="px-3 py-1 cursor-pointer text-sm bg-gray-200 dark:bg-gray-700 text-gray-600 dark:text-gray-300 rounded disabled:opacity-50"
            >
              Next
            </button>
          </div>
        </div>
      )}
    </div>
  );
}
