import * as React from "react"
import { Check, ChevronsUpDown } from "lucide-react"
import { cn } from "@/lib/utils"
import { Button } from "@/components/ui/button"
import {
    Command,
    CommandEmpty,
    CommandGroup,
    CommandInput,
    CommandItem,
} from "@/components/ui/command"
import {
    Popover,
    PopoverContent,
    PopoverTrigger,
} from "@/components/ui/popover"

interface Inclusion {
    id: number;
    name: string;
    created_at: string;
    updated_at: string;
}

interface InclusionComboboxProps {
    inclusions: Inclusion[];
    value: string;
    onChange: (value: string) => void;
    placeholder?: string;
    className?: string;
}

export function InclusionCombobox({
    inclusions,
    value,
    onChange,
    placeholder = "Select inclusion...",
    className,
}: InclusionComboboxProps) {
    const [open, setOpen] = React.useState(false)

    return (
        <Popover open={open} onOpenChange={setOpen}>
            <PopoverTrigger asChild>
                <Button
                    variant="outline"
                    role="combobox"
                    aria-expanded={open}
                    className={cn("w-full justify-between", className)}
                >
                    {value
                        ? inclusions.find((inclusion) => inclusion.name === value)?.name
                        : placeholder}
                    <ChevronsUpDown className="ml-2 h-4 w-4 shrink-0 opacity-50" />
                </Button>
            </PopoverTrigger>
            <PopoverContent className="w-full p-0" style={{ maxHeight: '300px' }}>
                <Command className="h-full">
                    <CommandInput placeholder={placeholder} className="h-9" />
                    <CommandEmpty>No inclusion found.</CommandEmpty>
                    <CommandGroup className="overflow-auto" style={{ maxHeight: '250px' }}>
                        {inclusions.map((inclusion) => (
                            <CommandItem
                                key={inclusion.id}
                                value={inclusion.name}
                                onSelect={(currentValue) => {
                                    onChange(currentValue === value ? "" : currentValue)
                                    setOpen(false)
                                }}
                                className="h-9"
                            >
                                <Check
                                    className={cn(
                                        "mr-2 h-4 w-4",
                                        value === inclusion.name ? "opacity-100" : "opacity-0"
                                    )}
                                />
                                <span className="truncate">{inclusion.name}</span>
                            </CommandItem>
                        ))}
                    </CommandGroup>
                </Command>
            </PopoverContent>
        </Popover>
    )
} 