import React, { useEffect, useState } from "react";
import { useDispatch } from "react-redux";
import BaseInput from "@/components/ui/BaseInput";
import BaseButton from "@/components/ui/BaseButton";
import BaseError from "@/components/ui/BaseError";
import { Trash2 } from "lucide-react";
import {
  useGetProfileQuery,
  useUpdateProfileMutation,
} from "@/features/auth/authApi";
import { setUser } from "@/features/auth/authSlice";
import { useApiError } from "@/hooks/useApiError";
import { toast } from "sonner";
import defaultAvatar from "@/assets/images/avatar-ractangle.jpg";

export default function UpdateProfile() {
  const dispatch = useDispatch();
  const [formData, setFormData] = useState({
    name: "",
    email: "",
    phone: "",
    address: "",
    profileImage: null as string | null,
    imageFile: null as File | null,
  });

  // RTK Query hooks
  const {
    data: profile,
    isLoading: isLoadingProfile,
    error: profileError,
  } = useGetProfileQuery();

  const [updateProfile, { isLoading: isUpdating, error: updateError }] =
    useUpdateProfileMutation();

  const profileErrorDetails = useApiError(profileError);
  const updateErrorDetails = useApiError(updateError);

  // Populate form with user data from profile
  useEffect(() => {
    if (profile) {
      setFormData({
        name: profile.name || "",
        email: profile.email || "",
        phone: profile.mobile || "",
        address: profile.address || "",
        profileImage: profile.profile_image_url || "",
        imageFile: null,
      });
    }
  }, [profile]);

  const handleChange = (
    e: React.ChangeEvent<HTMLInputElement | HTMLTextAreaElement>
  ) => {
    const { name, value } = e.target;
    setFormData((prev) => ({
      ...prev,
      [name]: value,
    }));
  };

  const handleImageChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    if (e.target.files && e.target.files[0]) {
      const file = e.target.files[0];
      setFormData((prev) => ({
        ...prev,
        profileImage: URL.createObjectURL(file),
        imageFile: file,
      }));
    }
  };

  const handleImageRemove = () => {
    setFormData((prev) => ({
      ...prev,
      profileImage: null,
      imageFile: null,
    }));
  };

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    try {
      // Prepare form data for API
      const formSubmitData = new FormData();
      formSubmitData.append("name", formData.name);
      formSubmitData.append("email", formData.email);
      formSubmitData.append("mobile", formData.phone);
      formSubmitData.append("address", formData.address);

      if (formData.imageFile) {
        formSubmitData.append("profile_image", formData.imageFile);
      }

      const result = await updateProfile(formSubmitData).unwrap();
      dispatch(setUser(result));
      toast.success("Profile updated successfully!");
    } catch (error) {
      console.error("Failed to update profile:", error);
    }
  };

  if (profileError) {
    return (
      <BaseError
        title={profileErrorDetails?.title || "Failed to Load Profile"}
        message={
          profileErrorDetails?.message ||
          "An error occurred while loading your profile. Please try again."
        }
        onRetry={() => window.location.reload()}
      />
    );
  }

  return (
    <div className="min-h-screen bg-gray-50 dark:bg-gray-900 flex items-center justify-center p-6">
      <div className="bg-white dark:bg-gray-800 rounded-lg shadow-lg p-6 max-w-md w-full">
        {/* Profile Image */}
        <div className="relative w-32 h-32 mx-auto mb-6 group">
          <img
            src={
              formData.profileImage ||
              profile?.profile_image_url ||
              defaultAvatar
            }
            alt="Profile"
            className="w-32 h-32 rounded-full object-cover border-4 border-blue-500"
          />
          <label
            htmlFor="profileImage"
            className="absolute bottom-0 right-0 bg-blue-500 text-white p-2 rounded-full cursor-pointer hover:bg-blue-600"
          >
            <svg
              xmlns="http://www.w3.org/2000/svg"
              className="h-5 w-5"
              fill="none"
              viewBox="0 0 24 24"
              stroke="currentColor"
            >
              <path
                strokeLinecap="round"
                strokeLinejoin="round"
                strokeWidth={2}
                d="M15.232 5.232l3.536 3.536M16 3a2.828 2.828 0 114 4L10 17H6v-4L16 3z"
              />
            </svg>
          </label>
          <input
            type="file"
            id="profileImage"
            accept="image/*"
            className="hidden"
            onChange={handleImageChange}
          />
          {formData.profileImage && (
            <button
              type="button"
              onClick={handleImageRemove}
              className="absolute inset-0 flex items-center justify-center bg-black bg-opacity-40 text-white rounded-full opacity-0 group-hover:opacity-100 transition-opacity cursor-pointer"
            >
              <Trash2 className="text-red-500 z-10" />
            </button>
          )}
        </div>

        {/* Form */}
        <form onSubmit={handleSubmit} className="space-y-4">
          <BaseInput
            label="Name"
            name="name"
            value={formData.name}
            onChange={handleChange}
            placeholder="Enter your name"
            required
          />
          <BaseInput
            label="Email"
            name="email"
            type="email"
            value={formData.email}
            onChange={handleChange}
            placeholder="Enter your email"
            required
          />
          <BaseInput
            label="Phone"
            name="phone"
            type="tel"
            value={formData.phone}
            onChange={handleChange}
            placeholder="Enter your phone number"
            required
          />
          <div>
            <label className="block text-sm font-medium text-gray-700 dark:text-gray-300 mb-1">
              Address
            </label>
            <textarea
              name="address"
              value={formData.address}
              onChange={handleChange}
              placeholder="Enter your address"
              className="w-full px-3 py-2 border rounded-md text-sm text-gray-700 dark:text-gray-300 dark:bg-gray-800 border-gray-300 dark:border-gray-700 focus:outline-none focus:ring-2 focus:ring-blue-500"
              rows={3}
            ></textarea>
          </div>

          {updateError && (
            <div className="text-red-500 text-sm">
              {updateErrorDetails?.message ||
                "Failed to update profile. Please try again."}
            </div>
          )}

          <BaseButton
            isLoading={isLoadingProfile || isUpdating}
            type="submit"
            className="w-full bg-blue-500 text-white"
          >
            Update Profile
          </BaseButton>
        </form>
      </div>
    </div>
  );
}
