import React, { useEffect, useState } from "react";
import { BaseModal } from "@/components/ui/BaseModal";
import {
  useCreatePromotionMutation,
  useUpdatePromotionMutation,
} from "@/features/promotion/promotionApi";
import { toast } from "sonner";

interface FormModalProps {
  isOpen: boolean;
  onClose: () => void;
  editData?: any;
  afterSubmit?: () => void;
}

// Add date formatting functions
const formatDateForInput = (dateString: string) => {
  if (!dateString) return "";
  return dateString.split(" ")[0]; // Get only the date part (YYYY-MM-DD)
};

export default function FormModal({
  isOpen,
  onClose,
  editData,
  afterSubmit,
}: FormModalProps) {
  const [formData, setFormData] = useState({
    id: undefined,
    title: "",
    subtitle: "",
    description: "",
    image_url: null as string | File | null,
    target_url: "",
    start_at: "",
    end_at: "",
    is_active: true,
  });

  const [createPromotion, { isLoading: isCreating }] = useCreatePromotionMutation();
  const [updatePromotion, { isLoading: isUpdating }] = useUpdatePromotionMutation();

  useEffect(() => {
    if (editData) {
      setFormData({
        ...editData,
        image_url: editData.image_url || null,
        start_at: formatDateForInput(editData.start_at),
        end_at: formatDateForInput(editData.end_at),
      });
    } else {
      setFormData({
        id: undefined,
        title: "",
        subtitle: "",
        description: "",
        image_url: null,
        target_url: "",
        start_at: "",
        end_at: "",
        is_active: true,
      });
    }
  }, [editData, isOpen]);

  const handleChange = (
    e: React.ChangeEvent<HTMLInputElement | HTMLTextAreaElement>
  ) => {
    const { name, value, type } = e.target;
    const checked = type === 'checkbox' ? (e.target as HTMLInputElement).checked : undefined;
    setFormData((prev) => ({
      ...prev,
      [name]: type === "checkbox" ? checked : value,
    }));
  };

  const onBannerChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const files = e.target.files;
    if (files && files.length > 0) {
      setFormData((prev) => ({
        ...prev,
        image_url: files[0],
      }));
    }
  };

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();

    // Create FormData object for multipart/form-data
    const formDataToSubmit = new FormData();

    // Append all form fields
    formDataToSubmit.append('title', formData.title);
    formDataToSubmit.append('subtitle', formData.subtitle);
    formDataToSubmit.append('description', formData.description);
    formDataToSubmit.append('target_url', formData.target_url);
    formDataToSubmit.append('start_at', formData.start_at);
    formDataToSubmit.append('end_at', formData.end_at);
    formDataToSubmit.append('is_active', formData.is_active ? "1" : "0");

    // Handle image file
    if (formData.image_url instanceof File) {
      formDataToSubmit.append('image', formData.image_url);
    } else if (formData.image_url) {
      formDataToSubmit.append('image_url', formData.image_url);
    }

    try {
      if (formData?.id) {
        await updatePromotion({
          id: formData.id,
          data: formDataToSubmit
        }).unwrap();
        toast.success("Promotion updated successfully");
      } else {
        await createPromotion(formDataToSubmit).unwrap();
        toast.success("Promotion created successfully");
      }

      if (afterSubmit) afterSubmit();
      onClose();
    } catch (error) {
      console.error("Failed to save promotion:", error);
      toast.error(error.data.message || "Failed to save promotion");
    }
  };

  if (!isOpen) return null;

  return (
    <BaseModal
      onClose={onClose}
      title={`${formData.id ? "Edit" : "Add"} Promotion`}
      onSubmit={handleSubmit}
      btnCaption={formData.id ? "Update" : "Add"}
      btnLoading={isCreating || isUpdating}
    >
      <div className="max-h-[90vh] flex flex-col">
        <div className="flex-1 pr-2">
          <form className="space-y-4">
            <div>
              <label className="block text-sm font-medium text-gray-700 dark:text-gray-300 mb-1">
                Title
              </label>
              <input
                type="text"
                name="title"
                value={formData.title}
                onChange={handleChange}
                placeholder="Enter promotion title"
                className="w-full px-3 py-2 border rounded-md text-sm text-gray-700 dark:text-gray-300 dark:bg-gray-800 border-gray-300 dark:border-gray-700 focus:outline-none focus:ring-2 focus:ring-blue-500"
                required
              />
            </div>

            <div>
              <label className="block text-sm font-medium text-gray-700 dark:text-gray-300 mb-1">
                Subtitle
              </label>
              <input
                type="text"
                name="subtitle"
                value={formData.subtitle}
                onChange={handleChange}
                placeholder="Enter promotion subtitle"
                className="w-full px-3 py-2 border rounded-md text-sm text-gray-700 dark:text-gray-300 dark:bg-gray-800 border-gray-300 dark:border-gray-700 focus:outline-none focus:ring-2 focus:ring-blue-500"
                required
              />
            </div>

            <div>
              <label className="block text-sm font-medium text-gray-700 dark:text-gray-300 mb-1">
                Description
              </label>
              <textarea
                name="description"
                value={formData.description}
                onChange={handleChange}
                placeholder="Enter promotion description"
                rows={4}
                className="w-full px-3 py-2 border rounded-md text-sm text-gray-700 dark:text-gray-300 dark:bg-gray-800 border-gray-300 dark:border-gray-700 focus:outline-none focus:ring-2 focus:ring-blue-500"
                required
              />
            </div>

            <div>
              <label className="block text-sm font-medium text-gray-700 dark:text-gray-300 mb-1">
                Banner Image
              </label>
              <input
                type="file"
                name="image_url"
                onChange={onBannerChange}
                className="w-full px-3 py-2 border rounded-md text-sm text-gray-700 dark:text-gray-300 dark:bg-gray-800 border-gray-300 dark:border-gray-700 focus:outline-none focus:ring-2 focus:ring-blue-500"
                required={!formData.id}
              />
              {formData.image_url && (
                <img
                  src={
                    formData.image_url instanceof File
                      ? URL.createObjectURL(formData.image_url)
                      : formData.image_url
                  }
                  alt="Preview"
                  className="w-32 h-32 mt-2 rounded-md object-cover"
                />
              )}
            </div>

            <div>
              <label className="block text-sm font-medium text-gray-700 dark:text-gray-300 mb-1">
                Target URL
              </label>
              <input
                type="url"
                name="target_url"
                value={formData.target_url}
                onChange={handleChange}
                placeholder="Enter target URL"
                className="w-full px-3 py-2 border rounded-md text-sm text-gray-700 dark:text-gray-300 dark:bg-gray-800 border-gray-300 dark:border-gray-700 focus:outline-none focus:ring-2 focus:ring-blue-500"
                required
              />
            </div>

            <div>
              <label className="block text-sm font-medium text-gray-700 dark:text-gray-300 mb-1">
                Start Date
              </label>
              <input
                type="date"
                name="start_at"
                value={formData.start_at}
                onChange={handleChange}
                className="w-full px-3 py-2 border rounded-md text-sm text-gray-700 dark:text-gray-300 dark:bg-gray-800 border-gray-300 dark:border-gray-700 focus:outline-none focus:ring-2 focus:ring-blue-500"
                required
              />
            </div>

            <div>
              <label className="block text-sm font-medium text-gray-700 dark:text-gray-300 mb-1">
                End Date
              </label>
              <input
                type="date"
                name="end_at"
                value={formData.end_at}
                onChange={handleChange}
                className="w-full px-3 py-2 border rounded-md text-sm text-gray-700 dark:text-gray-300 dark:bg-gray-800 border-gray-300 dark:border-gray-700 focus:outline-none focus:ring-2 focus:ring-blue-500"
                required
              />
            </div>

            <div className="flex items-center mb-5">
              <input
                type="checkbox"
                name="is_active"
                checked={formData.is_active}
                onChange={handleChange}
                className="h-4 w-4 text-blue-600 focus:ring-blue-500 border-gray-300 rounded"
              />
              <label className="ml-2 block text-sm text-gray-700 dark:text-gray-300">
                Active
              </label>
            </div>
          </form>
        </div>
      </div>
    </BaseModal>
  );
}
