import { baseApi } from "../baseApi";
import apiRoutes from "@/api/apiRoutes";

export const bookingApi = baseApi.injectEndpoints({
  endpoints: (builder) => ({
    getBookings: builder.query({
      query: ({ page = 1, search = "", per_page, start_date = null, end_date = null, status = "" }) => ({
        url: `${apiRoutes.booking.getAll}${page ? `?page=${page}` : ""}${per_page ? `&per_page=${per_page}` : ""}${search ? `&search=${search}` : ""}${start_date ? `&start_date=${start_date}` : ""}${end_date ? `&end_date=${end_date}` : ""}${status ? `&status=${status}` : ""}`,
        method: "GET",
      }),
      transformResponse: (response: any) => {
        return response.data;
      },
      providesTags: ["Booking"],
    }),
    getBookingById: builder.query({
      query: (id) => ({
        url: apiRoutes.booking.getById(id),
        method: "GET",
      }),
      transformResponse: (response: any) => {
        return response.data;
      },
      providesTags: (result, error, id) => [{ type: "Booking", id }],
    }),
    createBooking: builder.mutation({
      query: (data) => ({
        url: apiRoutes.booking.create,
        method: "POST",
        body: data,
      }),
      invalidatesTags: ["Booking"],
    }),
    updateBooking: builder.mutation({
      query: ({ id, data }) => ({
        url: apiRoutes.booking.update(id),
        method: "PUT",
        body: data,
      }),
      invalidatesTags: (result, error, { id }) => [
        { type: "Booking", id },
        "Booking",
      ],
    }),
    deleteBooking: builder.mutation({
      query: (id) => ({
        url: apiRoutes.booking.delete(id),
        method: "DELETE",
      }),
      invalidatesTags: ["Booking"],
    }),
    updateBookingStatus: builder.mutation({
      query: (id) => ({
        url: apiRoutes.booking.updateStatus(id),
        method: "POST",
      }),
      invalidatesTags: (result, error, { id }) => [
        { type: "Booking", id },
        "Booking",
      ],
    }),
    markResendEmail: builder.mutation({
      query: (id) => ({
        url: apiRoutes.booking.markResendEmail(id),
        method: "POST"
      }),
      invalidatesTags: ["Booking"]
    })
  }),
});

export const {
  useGetBookingsQuery,
  useGetBookingByIdQuery,
  useCreateBookingMutation,
  useUpdateBookingMutation,
  useDeleteBookingMutation,
  useUpdateBookingStatusMutation,
  useMarkResendEmailMutation
} = bookingApi;
