import React from "react";
import { BaseModal } from "@/components/ui/BaseModal";
import { Info } from "lucide-react";

interface ViewModalProps {
    isOpen: boolean;
    onClose: () => void;
    popularDestination?: {
        id: number;
        title: string;
        description: string;
        image_url: string;
        target_url: string;
    };
}

export default function ViewModal({ isOpen, onClose, popularDestination }: ViewModalProps) {
    if (!isOpen) return null;

    console.log("popularDestination", popularDestination)

    if (!popularDestination) {
        return (
            <BaseModal onClose={onClose} width="max-w-xl" isSubmit={false} title="Popular Destination Details">
                <div className="flex items-center justify-center py-8">
                    <p className="text-gray-500 dark:text-gray-400">No popular destination data available</p>
                </div>
            </BaseModal>
        );
    }

    return (
        <BaseModal onClose={onClose} width="max-w-xl" isSubmit={false} title="Popular Destination Details">
            <div className="flex flex-col">
                <div className="space-y-6">
                    {/* Header Section with Image */}
                    <div className="relative">
                        <div className="aspect-video w-full overflow-hidden rounded-lg">
                            <img
                                src={popularDestination.image_url}
                                alt={popularDestination.title}
                                className="w-full h-full object-cover"
                            />
                        </div>
                        <div className="absolute bottom-0 left-0 right-0 bg-gradient-to-t from-black/70 to-transparent p-4">
                            <a href={popularDestination?.target_url} target="_blank">
                                <h3 className="text-xl font-bold text-white mb-1 hover:underline">
                                    {popularDestination.title}
                                </h3>
                            </a>
                        </div>
                    </div>
                    <div className="bg-white dark:bg-gray-800 rounded-lg border border-gray-200 dark:border-gray-700 p-4">
                        <h4 className="text-sm font-medium text-gray-500 dark:text-gray-400 mb-3 flex items-center gap-2">
                            <Info className="w-4 h-4" />
                            Description
                        </h4>
                        <p className="text-sm text-gray-600 dark:text-gray-300">
                            {popularDestination.description}
                        </p>
                    </div>
                </div>
            </div>
        </BaseModal>
    );
}
