import React, { useState } from "react";
import { useGetReviewsQuery } from "@/features/review/reviewApi";
import BaseTable from "@/components/table/BaseTable";
import BaseButton from "@/components/ui/BaseButton";
import { Eye, RefreshCw } from "lucide-react";
import ViewModal from "./modal/ViewModal";
import { useSelector } from "react-redux";
import { RootState } from "@/store";

interface Review {
    id: string;
    user_name: string;
    rating: number;
    status: string;
    created_at: string;
}

export default function Reviews() {
    const [selectedReview, setSelectedReview] = useState<Review | null>(null);
    const [viewModalOpen, setViewModalOpen] = useState(false);

    const { search, page, start_date, end_date } = useSelector((state: RootState) => state.tableFilter);

    const { data, isLoading, isFetching, error, refetch } = useGetReviewsQuery({
        page,
        search,
        start_date,
        end_date,
    });

    const columns = [
        {
            title: "Action",
            key: "actions",
            sort: false,
            width: "70px",
            className: "text-center",
            dropdownItems: [
                {
                    icon: <Eye className="w-5 h-5" />,
                    label: "View",
                    onClick: (row: Review) => {
                        setSelectedReview(row);
                        setViewModalOpen(true);
                    },
                    show: true,
                },
            ],
        },
        {
            title: "Sl No",
            key: "index",
            sort: false,
            width: "70px",
            className: "text-center",
        },
        {
            title: "User Name",
            key: "user",
            sort: true,
            width: "180px",
            render: (value: any) => value?.username
        },
        {
            title: "Rating",
            key: "rating",
            sort: true,
            width: "100px",
            render: (value: number) => `${value}/5`,
        },
        {
            title: "Status",
            key: "status",
            sort: true,
            width: "120px",
            render: (value: string) => (
                <span
                    className={`px-2 py-1 rounded text-sm font-medium ${value === "approved"
                        ? "bg-green-100 text-green-600"
                        : value === "pending"
                            ? "bg-yellow-100 text-yellow-600"
                            : "bg-red-100 text-red-600"
                        }`}
                >
                    {value?.charAt(0).toUpperCase() + value.slice(1)}
                </span>
            ),
        },
        {
            title: "Date",
            key: "created_at",
            sort: true,
            width: "150px",
            render: (value: string) => new Date(value).toLocaleDateString(),
        },
    ];

    return (
        <>
            <BaseTable
                title="All Reviews"
                columns={columns}
                data={data?.data || []}
                searchPlaceholder="Search reviews..."
                showSearch={true}
                loading={isLoading || isFetching}
                limit={data?.per_page || 10}
                lastPage={data?.last_page || 1}
                total={data?.total || 0}
                error={error}
                refetch={refetch}
            />

            <ViewModal
                isOpen={viewModalOpen}
                onClose={() => {
                    setViewModalOpen(false);
                    setSelectedReview(null);
                }}
                review={selectedReview}
            />
        </>
    );
}

