import axios from "axios";

const apiClient = axios.create({
  baseURL: '/api', // Using the proxy path defined in vite.config.ts
  withCredentials: true, // Enable sending cookies in cross-origin requests
  maxRedirects: 0, // Prevent redirects
});

// Add a request interceptor
apiClient.interceptors.request.use(
  async (config) => {
    const token = localStorage.getItem("token");
    if (token) {
      config.headers.Authorization = `Bearer ${token}`;
    }
    // Ensure headers are set for every request
    config.headers["Accept"] = "application/json";
    config.headers["X-Requested-With"] = "XMLHttpRequest";

    return config;
  },
  (error) => {
    return Promise.reject(error);
  }
);

// Add a response interceptor
apiClient.interceptors.response.use(
  (response) => response,
  async (error) => {
    if (error.response) {
      // Handle authentication errors
      if (error.response.status === 419) {
        console.error("Authentication Error: Session expired or invalid");
        // Try to get a new session
        try {
          // Retry the original request
          const config = error.config;
          return apiClient(config);
        } catch (retryError) {
          console.error("Failed to refresh session:", retryError);
          localStorage.removeItem("token");
          window.location.href = "/login";
          return Promise.reject(new Error("Session expired. Please login again."));
        }
      }

      // Handle CORS and redirect errors
      if (error.response.status === 0) {
        console.error("CORS Error: Unable to reach the server");
        return Promise.reject(new Error("Unable to reach the server. Please check your connection."));
      }

      // Handle redirect errors
      if (error.response.status === 301 || error.response.status === 302) {
        console.error("Redirect Error: Server is trying to redirect the request");
        return Promise.reject(new Error("Server configuration issue. Please contact the administrator."));
      }

      if (error.response.status === 401) {
        localStorage.removeItem("token");
        window.location.href = "/login";
      }
    }
    console.error("API Error:", error.response || error.message);
    return Promise.reject(error);
  }
);

export default apiClient;
