import React from "react";
import { BrowserRouter, Routes, Route, Navigate } from "react-router-dom";
import AppRoutes from "./routes/AppRoutes";
import { Toaster } from "sonner";
import { useEffect } from "react";
import { useDispatch } from "react-redux";
import { useGetProfileQuery } from "./features/auth/authApi";
import { setUser, clearAuth } from "./features/auth/authSlice";
import ErrorBoundary from "./components/hoc/withErrorBoundary";
import { useApiError } from "./hooks/useApiError";
import { toast } from "sonner";
import MaintenancePage from "@/pages/MaintenancePage";
import { isMaintenanceActive } from "@/config/maintenance";

function App() {
  const dispatch = useDispatch();
  const token = localStorage.getItem("token");

  // RTK Query hook for fetching profile
  const {
    data: profile,
    error: profileError,
    isLoading: isLoadingProfile,
    isSuccess: isProfileSuccess
  } = useGetProfileQuery(undefined, {
    skip: !token, // Skip the query if there's no token
  });

  // Handle profile error
  const profileErrorDetails = useApiError(profileError);

  // Show error toast if profile fetch fails
  useEffect(() => {
    if (profileError) {
      toast.error(profileErrorDetails?.message || "Failed to load user profile");
      // If profile fetch fails, clear the token and user data
      dispatch(clearAuth());
    }
  }, [profileError, profileErrorDetails, dispatch]);

  return (
    <ErrorBoundary>
      <BrowserRouter>
        <Toaster
          position="top-right"
          richColors
          closeButton
          expand={false}
          duration={3000}
        />
        {isLoadingProfile ? (
          <div className="min-h-screen flex items-center justify-center">
            <div className="animate-spin rounded-full h-12 w-12 border-t-2 border-b-2 border-blue-500"></div>
          </div>
        ) : (
          <Routes>
            {isMaintenanceActive() ? (
              <>
                <Route path="/maintenance" element={<MaintenancePage />} />
                <Route path="*" element={<Navigate to="/maintenance" replace />} />
              </>
            ) : (
              <Route path="*" element={<AppRoutes />} />
            )}
          </Routes>
        )}
      </BrowserRouter>
    </ErrorBoundary>
  );
}

export default App;
