import { baseApi } from "../baseApi";
import apiRoutes from "@/api/apiRoutes";

interface Review {
    id: string;
    user_name: string;
    rating: number;
    comment: string;
    status: "pending" | "approved" | "cancelled";
    created_at: string;
}

interface ReviewResponse {
    data: Review[];
    total: number;
    current_page: number;
    per_page: number;
}

export const reviewApi = baseApi.injectEndpoints({
    endpoints: (builder) => ({
        getReviews: builder.query<ReviewResponse, { page?: number; search?: string }>({
            query: ({ page = 1, search = "" }) => ({
                url: `${apiRoutes.review.getAll}?page=${page}&search=${search}`,
                method: "GET",
            }),
            transformResponse: (response: any) => response.data,
            providesTags: ["Review"],
        }),
        getReviewById: builder.query<Review, string>({
            query: (id) => ({
                url: apiRoutes.review.getById(id),
                method: "GET",
            }),
            transformResponse: (response: any) => response.data,
            providesTags: ["Review"],
        }),
        approveReview: builder.mutation<Review, { id: string }>({
            query: ({ id }) => ({
                url: apiRoutes.review.approve(id),
                method: "POST",
            }),
            invalidatesTags: ["Review"],
        }),
        rejectReview: builder.mutation<Review, { id: string }>({
            query: ({ id }) => ({
                url: apiRoutes.review.reject(id),
                method: "POST",
            }),
            invalidatesTags: ["Review"],
        }),
    }),
});

export const {
    useGetReviewsQuery,
    useGetReviewByIdQuery,
    useApproveReviewMutation,
    useRejectReviewMutation,
} = reviewApi;
