import React, { useEffect, useState } from "react";
import BaseButton from "@/components/ui/BaseButton";
import BaseTable from "@/components/table/BaseTable";
import { Edit, Eye, Plus, Trash } from "lucide-react";
import FormModal from "./modal/FormModal";
import DeleteModal from "@/components/ui/DeleteModal";
import ViewModal from "./modal/ViewModal";
import { useSelector } from "react-redux";
import { RootState } from "@/store";
import { toast } from "sonner";
import { useDeletePopularDestinationMutation, useGetPopularDestinationsQuery } from "@/features/popularDestination/popularDestinationApi";

interface PopularDestination {
  id: number;
  title: string;
  subtitle: string;
  description: string;
  target_url: string;
  order: string;
  is_active: boolean;
  start_at: string;
  end_at: string;
  image_url: string;
}

export default function PopularDestination() {
  const [isModalOpen, setIsModalOpen] = useState(false);
  const [isDeleteModalOpen, setIsDeleteModalOpen] = useState(false);
  const [isViewModalOpen, setIsViewModalOpen] = useState(false);
  const [selectedPopularDestination, setSelectedPopularDestinaiton] =
    useState<PopularDestination | null>(null);
  const [editData, setEditData] = useState<PopularDestination | null>(null);

  const { search, page, start_date, end_date } = useSelector(
    (state: RootState) => state.tableFilter
  );

  const {
    data: popularDestinationsData,
    isLoading,
    isFetching,
    error,
    refetch,
  } = useGetPopularDestinationsQuery({
    page,
    search,
    start_date,
    end_date,
  });

  const [deletePopularDestination, { isLoading: isDeleting }] =
    useDeletePopularDestinationMutation();

  const handleDelete = async () => {
    if (!selectedPopularDestination) return;
    try {
      await deletePopularDestination(selectedPopularDestination.id).unwrap();
      refetch();
      setIsDeleteModalOpen(false);
      setSelectedPopularDestinaiton(null);
      toast.success("Popular destination deleted successfully");
    } catch (error) {
      console.error("Failed to delete popular destination:", error);
      toast.error("Failed to delete popular destination");
    }
  };

  const columns = [
    {
      title: "Action",
      key: "actions",
      width: "70px",
      dropdownItems: [
        {
          icon: <Eye className="w-5 h-5" />,
          label: "View",
          onClick: (row: PopularDestination) => {
            setSelectedPopularDestinaiton(row);
            setIsViewModalOpen(true);
          },
          show: true,
        },
        {
          icon: <Edit className="w-5 h-5" />,
          label: "Edit",
          onClick: (row: PopularDestination) => {
            setEditData(row);
            setIsModalOpen(true);
          },
          show: true,
        },
        {
          icon: <Trash className="w-5 h-5" />,
          label: "Delete",
          onClick: (row: PopularDestination) => {
            setSelectedPopularDestinaiton(row);
            setIsDeleteModalOpen(true);
          },
          className: "text-red-500 hover:text-red-600",
          show: true,
        },
      ],
    },
    {
      title: "Sl No",
      key: "index",
      sort: false,
      width: "70px",
      className: "text-center",
    },
    {
      title: "Banner Image",
      key: "image_url",
      sort: false,
      width: "180px",
      render: (value: string) => (
        <div className="w-full h-12 flex items-center justify-center">
          <img
            src={value}
            alt="Banner"
            className="w-full h-full object-contain"
          />
        </div>
      ),
    },
    {
      title: "Title",
      key: "title",
      sort: true,
      width: "180px",
    },
  ];

  return (
    <>
      <BaseTable
        title="All Popular Destinations"
        columns={columns}
        data={popularDestinationsData?.items || []}
        searchPlaceholder="Search popular destinations..."
        loading={isLoading || isFetching}
        limit={popularDestinationsData?.pagination?.per_page || 100}
        lastPage={popularDestinationsData?.pagination?.last_page || 1}
        total={popularDestinationsData?.pagination?.total || 0}
        showSearch={true}
        error={error}
        refetch={refetch}
        actionButton={
          <BaseButton
            onClick={() => {
              setEditData(null);
              setIsModalOpen(true);
            }}
            icon={<Plus className="w-5 h-5" />}
          >
            Add
          </BaseButton>
        }
      />

      <FormModal
        isOpen={isModalOpen}
        onClose={() => setIsModalOpen(false)}
        editData={editData}
      />

      {isDeleteModalOpen && (
        <DeleteModal
          isOpen={isDeleteModalOpen}
          onClose={() => {
            setIsDeleteModalOpen(false);
            setSelectedPopularDestinaiton(null);
          }}
          onConfirm={handleDelete}
          isLoading={isDeleting}
          title="Delete Popular Destination"
          message={`Are you sure you want to delete ${selectedPopularDestination?.title}?`}
        />
      )}

      <ViewModal
        isOpen={isViewModalOpen}
        onClose={() => {
          setIsViewModalOpen(false);
          setSelectedPopularDestinaiton(null);
        }}
        popularDestination={selectedPopularDestination}
      />
    </>
  );
}
