import React from "react";
import BaseButton from "@/components/ui/BaseButton";
import { Check, X, Star } from "lucide-react";
import { BaseModal } from "@/components/ui/BaseModal";
import { format } from "date-fns";
import { useApproveReviewMutation, useRejectReviewMutation } from "@/features/review/reviewApi";
import { toast } from "sonner";

interface ReviewUser {
    id: number;
    username: string;
    email: string;
    profile_image: string | null;
    profile_image_url: string | null;
}

interface Review {
    id: number;
    user_id: string;
    rating: string;
    comment: string;
    status: string;
    admin_notes?: string | null;
    created_at: string;
    updated_at: string;
    user: ReviewUser;
}

interface ViewModalProps {
    isOpen: boolean;
    onClose: () => void;
    review?: Review;
}

export default function ViewModal({ isOpen, onClose, review, }: ViewModalProps) {
    if (!isOpen) return null;

    const [approveReview, { isLoading: isApproveLoading }] = useApproveReviewMutation();
    const [rejectReview, { isLoading: isRejectLoading }] = useRejectReviewMutation();

    const handleApprove = async () => {
        try {
            await approveReview({ id: review?.id });
            toast.success("Review approved successfully");
            onClose();
        } catch (error) {
            console.error(error);
            toast.error("Failed to approve review");
        }
    }

    const handleReject = async () => {
        try {
            await rejectReview({ id: review?.id });
            toast.success("Review rejected successfully");
            onClose();
        } catch (error) {
            console.error(error);
            toast.error("Failed to reject review");
        }
    }

    return (
        <BaseModal onClose={onClose} width="max-w-md" title="Review Details" btnCaption="Apporved" btnLoading={isApproveLoading} onSubmit={handleApprove} footerSlot={
            <BaseButton variant="destructive" onClick={handleReject} isLoading={isRejectLoading}>Reject</BaseButton>
        }>
            <div className="flex flex-col">
                {!review ? (
                    <div className="text-center py-8 text-gray-500 dark:text-gray-400">
                        No review details found
                    </div>
                ) : (
                    <div className="space-y-6">
                        {/* User Info */}
                        <div className="flex flex-col items-center gap-2">
                            <img
                                src={review.user.profile_image_url || review.user.profile_image || "https://i.pravatar.cc/80"}
                                alt={review.user.username}
                                className="w-16 h-16 rounded-full object-cover border border-gray-200 dark:border-gray-700"
                            />
                            <div className="text-lg font-medium text-gray-900 dark:text-white">{review.user.username}</div>
                            <div className="text-sm text-gray-500 dark:text-gray-400">{review.user.email}</div>
                        </div>

                        {/* Review Info */}
                        <div className="bg-white dark:bg-gray-800 rounded-lg border border-gray-200 dark:border-gray-700 p-4 flex flex-col gap-3">
                            <div className="flex items-center gap-2">
                                <span className="text-yellow-500"><Star className="w-5 h-5 fill-yellow-400" /></span>
                                <span className="text-lg font-semibold text-gray-900 dark:text-white">{review.rating}/5</span>
                            </div>
                            <div className="flex items-center gap-2">
                                <span className={`px-3 py-1 rounded-full text-xs font-medium capitalize
                                    ${review.status === "approved"
                                        ? "bg-green-100 text-green-700 dark:bg-green-900/30 dark:text-green-400"
                                        : review.status === "pending"
                                            ? "bg-yellow-100 text-yellow-700 dark:bg-yellow-900/30 dark:text-yellow-400"
                                            : "bg-red-100 text-red-700 dark:bg-red-900/30 dark:text-red-400"
                                    }`}>
                                    {review.status}
                                </span>
                                <span className="text-xs text-gray-400 ml-2">{format(new Date(review.created_at), "MMM dd, yyyy HH:mm")}</span>
                            </div>
                            <div>
                                <div className="text-sm text-gray-500 dark:text-gray-400 mb-1">Comment:</div>
                                <div className="text-base text-gray-900 dark:text-white">{review.comment}</div>
                            </div>
                        </div>
                    </div>
                )}
            </div>
        </BaseModal>
    );
}
