import React from "react";
import { BaseModal } from "@/components/ui/BaseModal";
import { X, Mail, Phone, Calendar, MapPin, User, Shield, Clock } from "lucide-react";
import { useGetStaffByIdQuery, useGetCustomerByIdQuery } from "@/features/user/userApi";
import BaseError from "@/components/ui/BaseError";
import { format } from "date-fns";

interface ViewModalProps {
    isOpen: boolean;
    onClose: () => void;
    userId: string | null;
    type: "staff" | "customer";
}

// Add this helper function at the top of the file
const formatDate = (dateString: string | null | undefined) => {
    if (!dateString) return 'Not specified';
    try {
        // Handle both ISO and custom date formats
        const date = new Date(dateString);
        if (isNaN(date.getTime())) {
            // If date is invalid, try parsing custom format
            const [day, month, year] = dateString.split('-');
            const parsedDate = new Date(parseInt(year), parseInt(month) - 1, parseInt(day));
            if (isNaN(parsedDate.getTime())) {
                return 'Invalid date';
            }
            return format(parsedDate, 'PPP');
        }
        return format(date, 'PPP');
    } catch (error) {
        return 'Invalid date';
    }
};

export default function ViewModal({ isOpen, onClose, userId, type }: ViewModalProps) {
    // Use different queries based on type
    const {
        data: staffData,
        isLoading: isStaffLoading,
        error: staffError
    } = useGetStaffByIdQuery(userId || '', {
        skip: !userId || type !== "staff",
    });

    const {
        data: customerData,
        isLoading: isCustomerLoading,
        error: customerError
    } = useGetCustomerByIdQuery(userId || '', {
        skip: !userId || type !== "customer",
    });

    // Determine which data to use
    const data = type === "staff" ? staffData : customerData;
    const isLoading = type === "staff" ? isStaffLoading : isCustomerLoading;
    const error = type === "staff" ? staffError : customerError;

    if (!isOpen) return null;

    return (
        <BaseModal onClose={onClose} title={`${type === "staff" ? "Staff Details" : "Customer Details"}`} isSubmit={false}>
            <div className="relative max-h-[90vh] flex flex-col">
                {/* Header - Fixed */}

                {/* Scrollable Content */}
                <div className="flex-1 overflow-y-auto pr-2">
                    {isLoading ? (
                        <div className="flex items-center justify-center p-8">
                            <div className="animate-spin rounded-full h-8 w-8 border-b-2 border-blue-500"></div>
                        </div>
                    ) : error ? (
                        <BaseError
                            title={`Error Loading ${type === "staff" ? "Staff" : "Customer"}`}
                            message="Failed to load details. Please try again."
                            onRetry={() => window.location.reload()}
                        />
                    ) : data?.data ? (
                        <div className="space-y-6">
                            {/* Profile Section */}
                            <div className="flex items-center gap-4 p-4 bg-gray-50 dark:bg-gray-800 rounded-lg">
                                {data?.data?.profile_image_url ? (
                                    <img
                                        src={data.data.profile_image_url}
                                        alt="Profile"
                                        className="w-16 h-16 rounded-full object-cover"
                                    />
                                ) : (
                                    <div className="w-16 h-16 rounded-full bg-blue-100 dark:bg-blue-900/30 flex items-center justify-center">
                                        <span className="text-2xl font-semibold text-blue-600 dark:text-blue-400">
                                            {data?.data?.username?.charAt(0)?.toUpperCase()}
                                        </span>
                                    </div>
                                )}
                                <div>
                                    <h3 className="text-lg font-medium text-gray-900 dark:text-white">
                                        {data?.data?.name}
                                    </h3>
                                    <p className="text-sm text-gray-500 dark:text-gray-400">
                                        @{data?.data?.username}
                                    </p>
                                </div>
                            </div>

                            {/* Status Badge - Only for staff */}
                            {type === "staff" && (
                                <div className="flex justify-center">
                                    <span className={`px-3 py-1 rounded-full text-sm font-medium ${data?.data?.account_status === 'active'
                                        ? 'bg-green-100 text-green-800 dark:bg-green-900/30 dark:text-green-400'
                                        : 'bg-red-100 text-red-800 dark:bg-red-900/30 dark:text-red-400'
                                        }`}>
                                        {data?.data?.account_status === 'active' ? 'Active Account' : 'Inactive Account'}
                                    </span>
                                </div>
                            )}

                            {/* Details Grid */}
                            <div className="grid grid-cols-2 gap-4">
                                <div className="space-y-1">
                                    <label className="text-sm font-medium text-gray-500 dark:text-gray-400 flex items-center gap-2">
                                        <User className="w-4 h-4" />
                                        Username
                                    </label>
                                    <p className="text-sm text-gray-900 dark:text-white">
                                        {data?.data?.username}
                                    </p>
                                </div>

                                <div className="space-y-1">
                                    <label className="text-sm font-medium text-gray-500 dark:text-gray-400 flex items-center gap-2">
                                        <Mail className="w-4 h-4" />
                                        Email
                                    </label>
                                    <p className="text-sm text-gray-900 dark:text-white">
                                        {data?.data?.email}
                                    </p>
                                </div>

                                <div className="space-y-1">
                                    <label className="text-sm font-medium text-gray-500 dark:text-gray-400 flex items-center gap-2">
                                        <Phone className="w-4 h-4" />
                                        Phone
                                    </label>
                                    <p className="text-sm text-gray-900 dark:text-white">
                                        {data?.data?.mobile}
                                    </p>
                                </div>

                                {/* Role - Only for staff */}
                                {type === "staff" && (
                                    <div className="space-y-1">
                                        <label className="text-sm font-medium text-gray-500 dark:text-gray-400 flex items-center gap-2">
                                            <Shield className="w-4 h-4" />
                                            Role
                                        </label>
                                        <p className="text-sm text-gray-900 dark:text-white">
                                            {data?.data?.role === "admin" ? "Administrator" : "Staff Member"}
                                        </p>
                                    </div>
                                )}

                                <div className="space-y-1">
                                    <label className="text-sm font-medium text-gray-500 dark:text-gray-400 flex items-center gap-2">
                                        <Calendar className="w-4 h-4" />
                                        Birthdate
                                    </label>
                                    <p className="text-sm text-gray-900 dark:text-white">
                                        {formatDate(data?.data?.birthdate)}
                                    </p>
                                </div>

                                <div className="space-y-1">
                                    <label className="text-sm font-medium text-gray-500 dark:text-gray-400 flex items-center gap-2">
                                        <MapPin className="w-4 h-4" />
                                        Address
                                    </label>
                                    <p className="text-sm text-gray-900 dark:text-white">
                                        {data?.data?.address || "Not specified"}
                                    </p>
                                </div>
                            </div>

                            {/* Additional Info */}
                            <div className="border-t dark:border-gray-700 pt-4">
                                <div className="grid grid-cols-2 gap-4">
                                    <div className="space-y-1">
                                        <label className="text-sm font-medium text-gray-500 dark:text-gray-400 flex items-center gap-2">
                                            <Clock className="w-4 h-4" />
                                            Created At
                                        </label>
                                        <p className="text-sm text-gray-900 dark:text-white">
                                            {formatDate(data?.data?.created_at)}
                                        </p>
                                    </div>

                                    <div className="space-y-1">
                                        <label className="text-sm font-medium text-gray-500 dark:text-gray-400 flex items-center gap-2">
                                            <Clock className="w-4 h-4" />
                                            Last Updated
                                        </label>
                                        <p className="text-sm text-gray-900 dark:text-white">
                                            {formatDate(data?.data?.updated_at)}
                                        </p>
                                    </div>
                                </div>
                            </div>

                            {/* Email Verification Status */}
                            <div className="border-t dark:border-gray-700 pt-4">
                                <div className="flex items-center justify-between">
                                    <span className="text-sm font-medium text-gray-500 dark:text-gray-400">
                                        Email Verification
                                    </span>
                                    <span className={`px-2 py-1 rounded-full text-xs font-medium ${data?.data?.email_verified_at
                                        ? 'bg-green-100 text-green-800 dark:bg-green-900/30 dark:text-green-400'
                                        : 'bg-yellow-100 text-yellow-800 dark:bg-yellow-900/30 dark:text-yellow-400'
                                        }`}>
                                        {data?.data?.email_verified_at ? 'Verified' : 'Not Verified'}
                                    </span>
                                </div>
                            </div>
                        </div>
                    ) : null}
                </div>
            </div>
        </BaseModal >
    );
} 