import { baseApi } from "../baseApi";
import apiRoutes from "@/api/apiRoutes";
import { setUser } from "./authSlice";


export const authApi = baseApi.injectEndpoints({
  endpoints: (builder) => ({
    login: builder.mutation({
      query: (credentials) => ({
        url: apiRoutes.auth.login,
        method: "POST",
        body: credentials,
        credentials: 'include', // Important for cookies
      }),
      transformResponse: (response: any) => {
        // Only store access token in localStorage
        if (response.data?.access) {
          localStorage.setItem("token", response.data.access);
        }
        // Refresh token will be automatically handled by cookies
        return response;
      },
    }),
    refreshToken: builder.mutation({
      query: () => ({
        url: apiRoutes.auth.refreshToken,
        method: "POST",
        credentials: 'include', // Important for cookies
      }),
      transformResponse: (response: any) => {
        if (response.data?.access) {
          localStorage.setItem("token", response.data.access);
        }
        return response;
      },
    }),
    getProfile: builder.query({
      query: () => ({
        url: apiRoutes.user.profile,
        method: "GET",
      }),
      transformResponse: (response: any) => {
        console.log("Profile API Response:", response);

        return response.data;
      },
      async onQueryStarted(_, { dispatch, queryFulfilled }) {
        try {
          const { data } = await queryFulfilled;
          dispatch(setUser(data)); // Set user in the auth slice
        } catch (err) {
          console.error("Failed to fetch profile:", err);
        }
      },
      providesTags: ["Profile"],
    }),
    updateProfile: builder.mutation({
      query: (data) => ({
        url: apiRoutes.user.updateProfile,
        method: "POST",
        body: data,
      }),
      transformResponse: (response: any) => {
        return response.data;
      },
      invalidatesTags: ["Profile"],
    }),
    logout: builder.mutation({
      query: () => ({
        url: apiRoutes.auth.logout,
        method: "POST",
        credentials: 'include', // Important for cookies
      }),
      transformResponse: () => {
        // Clear access token from localStorage
        localStorage.removeItem("token");
        // Refresh token cookie will be cleared by the server
        return null;
      },
      invalidatesTags: ["Profile"],
    }),
    changePassword: builder.mutation({
      query: (data) => ({
        url: apiRoutes.auth.changePassword,
        method: "POST",
        body: data,
      }),
    }),
    sendOtp: builder.mutation({
      query: (data) => ({
        url: apiRoutes.auth.sendOtp,
        method: "POST",
        body: data,
      }),
    }),
    resendOtp: builder.mutation({
      query: (data) => ({
        url: apiRoutes.auth.resendOtp,
        method: "POST",
        body: data,
      }),
    }),
    verifyEmail: builder.mutation({
      query: (data) => ({
        url: apiRoutes.auth.verifyEmail,
        method: "POST",
        body: data,
      }),
    }),
    resetPassword: builder.mutation({
      query: (data) => ({
        url: apiRoutes.auth.resetPassword,
        method: "POST",
        body: data,
      }),
    }),
    updateCustomerInfo: builder.mutation({
      query: (data) => ({
        url: apiRoutes.user.updateCustomerInfo(data.id),
        method: "POST",
        body: data,
      }),
    }),
  }),
});

export const {
  useLoginMutation,
  useGetProfileQuery,
  useUpdateProfileMutation,
  useLogoutMutation,
  useChangePasswordMutation,
  useLazyGetProfileQuery,
  useSendOtpMutation,
  useVerifyEmailMutation,
  useResetPasswordMutation,
  useResendOtpMutation,
  useRefreshTokenMutation,
  useUpdateCustomerInfoMutation,
} = authApi;
