import React from "react";
import BaseButton from "./BaseButton";

interface BaseModalProps {
  children: React.ReactNode;
  onClose: () => void;
  width?: string;
  title?: string;
  onSubmit?: () => void;
  btnCaption?: string;
  btnLoading?: boolean;
  isSubmit?: boolean;
  isDelete?: boolean;
  footerSlot?: React.ReactNode;
}

export function BaseModal({ children, onClose, width = "max-w-lg", title = "Modal Title", onSubmit, btnCaption = "Save", btnLoading = false, isSubmit = true, isDelete = false, footerSlot }: BaseModalProps) {
  const handleOverlayClick = (e: React.MouseEvent<HTMLDivElement>) => {
    if (e.target === e.currentTarget) {
      onClose(); // Close the modal if the overlay is clicked
    }
  };

  return (
    <div
      className="fixed inset-0 z-50 flex items-center justify-center bg-black/50 bg-opacity-30 backdrop-blur-sm p-4"
      onClick={handleOverlayClick}
    >
      <div
        className={`bg-white dark:bg-gray-800 rounded-lg shadow-lg ${width} w-full relative max-h-[calc(100vh-3rem)] flex flex-col`}
      >
        <div className="flex items-center justify-between p-3 border-b border-gray-200 dark:border-gray-700">
          <h2 className="text-xl font-semibold text-gray-800 dark:text-white">
            {title}
          </h2>
          <button
            onClick={onClose}
            className="cursor-pointer text-gray-500 dark:text-gray-300 hover:text-gray-700 dark:hover:text-gray-100 z-10"
          >
            ✕
          </button>
        </div>
        <div className="flex-1 overflow-y-auto p-5">
          {children}
        </div>
        <div className="p-3 border-t border-gray-200 dark:border-gray-700 flex justify-end gap-2 items-center">
          <BaseButton variant="dark" onClick={onClose}>Cancel</BaseButton>
          {footerSlot}
          {
            isSubmit && (
              <BaseButton onClick={onSubmit} isLoading={btnLoading}>{btnCaption}</BaseButton>
            )
          }
          {
            isDelete && (
              <BaseButton variant="destructive" onClick={onSubmit} isLoading={btnLoading}>{btnCaption}</BaseButton>
            )
          }
        </div>
      </div>
    </div>
  );
}
