import React, { useEffect, useState } from "react";
import BaseButton from "@/components/ui/BaseButton";
import BaseTable from "@/components/table/BaseTable";
import { Edit, Eye, Plus, Trash } from "lucide-react";
import FormModal from "./modal/FormModal";
import DeleteModal from "@/components/ui/DeleteModal";
import ViewModal from "./modal/ViewModal";
import { useSelector } from "react-redux";
import { RootState } from "@/store";
import { toast } from "sonner";
import { useDeleteHappyCustomerMutation, useGetHappyCustomersQuery } from "@/features/happyCustomer/happyCustomerApi";

interface HappyCustomer {
    id: number;
    name: string;
    email: string;
    phone: string;
    message: string;
    image_url: string;
    created_at: string;
    updated_at: string;
}

export default function HappyCustomer() {
    const [isModalOpen, setIsModalOpen] = useState(false);
    const [isDeleteModalOpen, setIsDeleteModalOpen] = useState(false);
    const [isViewModalOpen, setIsViewModalOpen] = useState(false);
    const [selectedHappyCustomer, setSelectedHappyCustomer] =
        useState<HappyCustomer | null>(null);
    const [editData, setEditData] = useState<HappyCustomer | null>(null);

    const { search, page, start_date, end_date } = useSelector(
        (state: RootState) => state.tableFilter
    );

    const {
        data: happyCustomersData,
        isLoading,
        isFetching,
        error,
        refetch,
    } = useGetHappyCustomersQuery({
        page,
        search,
        start_date,
        end_date,
    });

    const [deleteHappyCustomer, { isLoading: isDeleting }] =
        useDeleteHappyCustomerMutation();

    const handleDelete = async () => {
        if (!selectedHappyCustomer) return;
        try {
            await deleteHappyCustomer(selectedHappyCustomer.id).unwrap();
            refetch();
            setIsDeleteModalOpen(false);
            setSelectedHappyCustomer(null);
            toast.success("Happy customer deleted successfully");
        } catch (error) {
            console.error("Failed to delete popular destination:", error);
            toast.error("Failed to delete popular destination");
        }
    };

    const columns = [
        {
            title: "Action",
            key: "actions",
            width: "70px",
            dropdownItems: [
                {
                    icon: <Eye className="w-5 h-5" />,
                    label: "View",
                    onClick: (row: HappyCustomer) => {
                        setSelectedHappyCustomer(row);
                        setIsViewModalOpen(true);
                    },
                    show: true,
                },
                {
                    icon: <Edit className="w-5 h-5" />,
                    label: "Edit",
                    onClick: (row: HappyCustomer) => {
                        setEditData(row);
                        setIsModalOpen(true);
                    },
                    show: true,
                },
                {
                    icon: <Trash className="w-5 h-5" />,
                    label: "Delete",
                    onClick: (row: HappyCustomer) => {
                        setSelectedHappyCustomer(row);
                        setIsDeleteModalOpen(true);
                    },
                    className: "text-red-500 hover:text-red-600",
                    show: true,
                },
            ],
        },
        {
            title: "Sl No",
            key: "index",
            sort: false,
            width: "70px",
            className: "text-center",
        },
        {
            title: "Image",
            key: "image_url",
            sort: false,
            width: "180px",
            render: (value: string) => (
                <div className="w-full h-12 flex items-center justify-center">
                    <img
                        src={value}
                        alt="Banner"
                        className="w-full h-full object-contain"
                    />
                </div>
            ),
        },
        {
            title: "Title",
            key: "title",
            sort: true,
            width: "180px",
        },
    ];

    return (
        <>
            <BaseTable
                title="All Happy Customers"
                columns={columns}
                data={happyCustomersData?.items || []}
                searchPlaceholder="Search happy customers..."
                loading={isLoading || isFetching}
                limit={happyCustomersData?.pagination?.per_page || 20}
                lastPage={happyCustomersData?.pagination?.last_page || 1}
                total={happyCustomersData?.pagination?.total || 0}
                showSearch={true}
                error={error}
                refetch={refetch}
                actionButton={
                    <BaseButton
                        onClick={() => {
                            setEditData(null);
                            setIsModalOpen(true);
                        }}
                        icon={<Plus className="w-5 h-5" />}
                    >
                        Add
                    </BaseButton>
                }
            />

            <FormModal
                isOpen={isModalOpen}
                onClose={() => setIsModalOpen(false)}
                editData={editData}
            />

            {isDeleteModalOpen && (
                <DeleteModal
                    isOpen={isDeleteModalOpen}
                    onClose={() => {
                        setIsDeleteModalOpen(false);
                        setSelectedHappyCustomer(null);
                    }}
                    onConfirm={handleDelete}
                    isLoading={isDeleting}
                    title="Delete Happy Customer"
                    message={`Are you sure you want to delete ${selectedHappyCustomer?.title}?`}
                />
            )}

            <ViewModal
                isOpen={isViewModalOpen}
                onClose={() => {
                    setIsViewModalOpen(false);
                    setSelectedHappyCustomer(null);
                }}
                happyCustomer={selectedHappyCustomer}
            />
        </>
    );
}
