import React from "react";
import {
  Home,
  TicketPercent,
  LogOut,
  Users,
  BookText,
  Star,
  List,
  MapPin,
  Smile,
} from "lucide-react";
import { NavLink, useNavigate } from "react-router-dom";
import logo from "@/assets/images/logo.png";
import {
  Sidebar,
  SidebarContent,
  SidebarGroup,
  SidebarGroupContent,
  SidebarGroupLabel,
  SidebarMenu,
  SidebarMenuButton,
  SidebarMenuItem,
} from "@/components/ui/sidebar";
import { useDispatch } from "react-redux";
import { useLogoutMutation } from "@/features/auth/authApi";
import { clearAuth } from "@/features/auth/authSlice";
import { toast } from "sonner";

// Menu items.
const items = [
  {
    title: "Dashboard",
    url: "/",
    icon: Home,
  },
  {
    title: "Bookings",
    url: "/bookings",
    icon: BookText,
  },
  {
    title: "Users",
    url: "/users",
    icon: Users,
  },
  {
    title: "Promotions",
    url: "/promotions",
    icon: TicketPercent,
  },
  {
    title: "Popular Destinations",
    url: "/popular-destinations",
    icon: MapPin,
  },
  {
    title: "Reviews",
    url: "/reviews",
    icon: Star,
  },
  {
    title: "Happy Customers",
    url: "/happy-customers",
    icon: Smile,
  },
  {
    title: "Inclusions",
    url: "/inclusions",
    icon: List,
  },
  {
    title: "Booking Sources",
    url: "/booking-sources",
    icon: List,
  },
  {
    title: "Logout",
    url: "/login",
    icon: LogOut,
  },
];

export function AppSidebar() {
  const dispatch = useDispatch();
  const navigate = useNavigate();
  const [logout, { isLoading }] = useLogoutMutation();

  const handleLogout = async () => {
    try {
      await logout().unwrap();
      dispatch(clearAuth());
      toast.success("Logged out successfully");
      navigate("/login");
    } catch (error) {
      console.error("Logout failed:", error);
      toast.error("Failed to logout. Please try again.");
    }
  };

  return (
    <Sidebar>
      <SidebarContent className="bg-white dark:bg-gray-900">
        <SidebarGroup>
          <SidebarGroupLabel>
            <div className="flex items-center justify-center border-b">
              <div className="w-full relative overflow-hidden mt-24">
                <img
                  src={logo}
                  alt="Logo"
                  className="w-full h-full object-cover"
                />
              </div>
            </div>
          </SidebarGroupLabel>
          <SidebarGroupContent className="mt-40">
            <SidebarMenu>
              {items.map((item) => (
                <SidebarMenuItem key={item.title}>
                  <SidebarMenuButton asChild>
                    {item.title === "Logout" ? (
                      <button
                        onClick={handleLogout}
                        disabled={isLoading}
                        className="flex items-center gap-3 px-4 py-3 rounded-lg text-gray-700 dark:text-gray-300 hover:bg-gray-100 dark:hover:bg-gray-800 disabled:opacity-50 disabled:cursor-not-allowed transition-colors duration-200"
                      >
                        <item.icon
                          className={`w-5 h-5 ${isLoading ? "animate-spin" : ""
                            }`}
                        />
                        <span className="font-medium">
                          {isLoading ? "Logging out..." : item.title}
                        </span>
                      </button>
                    ) : (
                      <NavLink
                        to={item.url}
                        className={({ isActive }) =>
                          `flex items-center gap-3 px-4 py-3 rounded-lg transition-colors duration-200 ${isActive
                            ? "bg-blue-50 dark:bg-blue-900/20 "
                            : "text-gray-700 dark:text-gray-300 hover:bg-gray-100 dark:hover:bg-gray-800"
                          }`
                        }
                      >
                        <item.icon className="w-5 h-5" />
                        <span className="font-medium">{item.title}</span>
                      </NavLink>
                    )}
                  </SidebarMenuButton>
                </SidebarMenuItem>
              ))}
            </SidebarMenu>
          </SidebarGroupContent>
        </SidebarGroup>
      </SidebarContent>
    </Sidebar>
  );
}
