import React from "react";
import { Loader2 } from "lucide-react"; // You can use any spinner icon

interface BaseButtonProps {
  type?: "button" | "submit" | "reset";
  onClick?: () => void;
  isLoading?: boolean;
  disabled?: boolean;
  icon?: React.ReactNode;
  children?: React.ReactNode; // Make children optional
  className?: string;
  variant?: "default" | "outline" | "destructive";
}

export default function BaseButton({
  type = "button",
  onClick,
  isLoading = false,
  disabled = false,
  icon,
  children,
  className = "",
  variant = "default",
}: BaseButtonProps) {
  const variants = {
    default: "bg-blue-500 text-white hover:bg-blue-600 dark:bg-blue-600 dark:hover:bg-blue-700",
    outline: "border border-gray-300 text-gray-700 hover:bg-gray-50 dark:border-gray-600 dark:text-gray-300 dark:hover:bg-gray-800",
    destructive: "bg-red-600 text-white hover:bg-red-700 dark:bg-red-600 dark:hover:bg-red-700",
    dark: "bg-gray-800 text-white hover:bg-gray-900 dark:bg-gray-900 dark:hover:bg-gray-800",
    success: "bg-green-500 text-white hover:bg-green-600 dark:bg-green-600 dark:hover:bg-green-700",
  };

  // Check if button only has an icon
  const isIconOnly = icon && !children;

  return (
    <button
      type={type}
      onClick={onClick}
      disabled={isLoading || disabled}
      className={`
        inline-flex items-center justify-center gap-2 cursor-pointer 
        ${isIconOnly ? 'p-2' : 'px-4 py-2'} 
        rounded-md text-sm font-medium 
        transition-colors duration-200
        disabled:opacity-50 disabled:cursor-not-allowed
        ${variants[variant]}
        ${className}
      `}
    >
      {isLoading ? (
        <Loader2 className="w-4 h-4 animate-spin" />
      ) : icon ? (
        <span className="flex items-center justify-center">{icon}</span>
      ) : null}
      {children && <span className="whitespace-nowrap">{children}</span>}
    </button>
  );
}
