import React, { useEffect, useState } from "react";
import { BaseModal } from "@/components/ui/BaseModal";
import {
  useCreateInclusionMutation,
  useUpdateInclusionMutation,
} from "@/features/inclusions/inclusionApi";
import { toast } from "sonner";

interface FormModalProps {
  isOpen: boolean;
  onClose: () => void;
  editData?: any;
  afterSubmit?: () => void;
}

export default function FormModal({
  isOpen,
  onClose,
  editData,
  afterSubmit,
}: FormModalProps) {
  const [formData, setFormData] = useState({
    id: undefined,
    name: "",
  });

  const [createInclusion, { isLoading: isCreating }] = useCreateInclusionMutation();
  const [updateInclusion, { isLoading: isUpdating }] = useUpdateInclusionMutation();

  useEffect(() => {
    if (editData) {
      setFormData({
        ...editData,
      });
    } else {
      setFormData({
        id: undefined,
        name: "",
      });
    }
  }, [editData, isOpen]);

  const handleChange = (
    e: React.ChangeEvent<HTMLInputElement | HTMLTextAreaElement>
  ) => {
    const { name, value, type } = e.target;
    setFormData((prev) => ({
      ...prev,
      [name]: value,
    }));
  };


  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    try {
      if (formData.id) {
        await updateInclusion(formData).unwrap();
        toast.success("Inclusion updated successfully");
      } else {
        await createInclusion(formData).unwrap();
        toast.success("Inclusion created successfully");
      }

      if (afterSubmit) afterSubmit();
      onClose();
    } catch (error) {
      console.error("Failed to save inclusion:", error);
      toast.error("Failed to save inclusion");
    }
  };

  if (!isOpen) return null;

  return (
    <BaseModal
      onClose={onClose}
      title={`${formData.id ? "Edit" : "Add"} Inclusion`}
      onSubmit={handleSubmit}
      btnCaption={formData.id ? "Update" : "Add"}
      btnLoading={isCreating || isUpdating}
    >
      <div className="max-h-[90vh] flex flex-col">
        <div className="flex-1 pr-2">
          <form className="space-y-4" onSubmit={(e) => handleSubmit(e as React.FormEvent)}>
            <div>
              <label className="block text-sm font-medium text-gray-700 dark:text-gray-300 mb-1">
                Name
              </label>
              <input
                type="text"
                name="name"
                value={formData.name}
                onChange={handleChange}
                placeholder="Enter inclusion name"
                className="w-full px-3 py-2 border rounded-md text-sm text-gray-700 dark:text-gray-300 dark:bg-gray-800 border-gray-300 dark:border-gray-700 focus:outline-none focus:ring-2 focus:ring-blue-500"
                required
              />
            </div>
          </form>
        </div>
      </div>
    </BaseModal>
  );
}
